import {artLink} from './constituicao.js'

const artigos =
{
	"1":{
		"caput":'O Senado Federal tem sede no Palácio do Congresso Nacional, em Brasília.',
		"enumeravel": [{
			"tipo":"paragrafo",
			"lista":{
					"1":{
						"caput":'Em caso de guerra, de comoção intestina, de calamidade pública ou de ocorrência que impossibilite o seu funcionamento na sede, o Senado poderá reunir-se, eventualmente, em qualquer outro local, por determinação da Mesa, a requerimento da maioria dos Senadores.'
					},
			}
		}]
	},
	"2":{
		"caput":'O Senado Federal reunir-se-á:',
		"enumeravel": [{
			"tipo":"inciso",
			"lista":{
					"1":{
						"caput":'anualmente, de 2 de fevereiro a 17 de julho e de 1º de agosto a 22 de dezembro, durante as sessões legislativas ordinárias, observado o disposto no art. 57 da Constituição;'
					},
					"2":{
						"caput":'quando convocado extraordinariamente o Congresso Nacional (Const., art. 57, §§ 6º a 8º).'
					},
			}
		}]
	},
	"3":{
		"caput":'A primeira e a terceira sessões legislativas ordinárias de cada legislatura serão precedidas de reuniões preparatórias, que obedecerão às seguintes normas:',
		"enumeravel": [{
			"tipo":"inciso",
			"lista":{
					"1":{
						"caput":'iniciar-se-ão com o quorum mínimo de um sexto da composição do Senado, em horário fixado pela Presidência, observando-se, nas deliberações, o disposto no art. 288;'
					},
					"2":{
						"caput":'a direção dos trabalhos caberá à Mesa anterior, dela excluídos, no início de legislatura, aqueles cujos mandatos com ela houverem terminado, ainda que reeleitos;'
					},
					"3":{
						"caput":'na falta dos membros da Mesa anterior, assumirá a Presidência o mais idoso dentre os presentes, o qual convidará, para os quatro lugares de Secretários, Senadores pertencentes às representações partidárias mais numerosas;'
					},
					"4":{
						"caput":'a primeira reunião preparatória realizar-se-á:',
						"enumeravel": [{
							"tipo":"letra",
							"lista":{
									"1":{
										"caput":'início de legislatura, a partir do dia 1º de fevereiro;'
									},
									"2":{
										"caput":'terceira sessão legislativa ordinária, no dia 1º de fevereiro;'
									},
							}
						}]
					},
					"5":{
						"caput":'no início de legislatura, os Senadores eleitos prestarão o compromisso regimental na primeira reunião preparatória; em reunião seguinte, será realizada a eleição do Presidente e, na terceira, a dos demais membros da Mesa;'
					},
					"6":{
						"caput":'na terceira sessão legislativa ordinária, far-se-á a eleição do Presidente da Mesa na primeira reunião preparatória e a dos demais membros, na reunião seguinte;'
					},
					"7":{
						"caput":'reuniões preparatórias, não será lícito o uso da palavra, salvo para declaração pertinente à matéria que nelas deva ser tratada.'
					},
			}
		}]
	},
	"4":{
		"caput":'A posse, ato público por meio do qual o Senador se investe no mandato, realizar-se-á perante o Senado, durante reunião preparatória, sessão deliberativa ou não deliberativa, precedida da apresentação à Mesa do diploma expedido pela Justiça Eleitoral, o qual será publicado no Diário do Senado Federal.',
		"enumeravel": [{
			"tipo":"paragrafo",
			"lista":{
					"1":{
						"caput":'A apresentação do diploma poderá ser feita pelo diplomado, pessoalmente, por ofício ao Primeiro-Secretário, por intermédio do seu Partido ou de qualquer Senador.'
					},
					"2":{
						"caput":'Presente o diplomado, o Presidente designará três Senadores para recebê-lo, introduzi-lo no plenário e conduzi-lo até a Mesa, onde, estando todos de pé, prestará o seguinte compromisso: “Prometo guardar a Constituição Federal e as leis do País, desempenhar fiel e lealmente o mandato de Senador que o povo me conferiu e sustentar a união, a integridade e a independência do Brasil”.'
					},
					"3":{
						"caput":'Quando forem diversos os Senadores a prestar o compromisso a que se refere o § 2º, somente um o pronunciará e os demais, ao serem chamados, dirão: “Assim o prometo”.'
					},
					"4":{
						"caput":'Durante o recesso, a posse realizar-se-á perante o Presidente, em solenidade pública em seu gabinete, observada a exigência da apresentação do diploma e da prestação do compromisso, devendo o fato ser noticiado no Diário do Senado Federal.'
					},
					"5":{
						"caput":'O Senador deverá tomar posse dentro de noventa dias, contados da instalação da sessão legislativa, ou, se eleito durante esta, contados da diplomação, podendo o prazo ser prorrogado, por motivo justificado, a requerimento do interessado, por mais trinta dias.'
					},
					"6":{
						"caput":'Findo o prazo de noventa dias, se o Senador não tomar posse nem requerer sua prorrogação, considerar-se-á como tendo renunciado ao mandato, convocando-se o primeiro Suplente.'
					},
			}
		}]
	},
	"5":{
		"caput":'O primeiro Suplente, convocado para a substituição de Senador licenciado, terá o prazo de trinta dias improrrogáveis para prestar o compromisso, e, nos casos de vaga ou de afastamento nos termos do art. 39, II, de sessenta dias, que poderá ser prorrogado, por motivo justificado, a requerimento do interessado, por mais trinta dias.',
		"enumeravel": [{
			"tipo":"paragrafo",
			"lista":{
					"1":{
						"caput":'Se, dentro dos prazos estabelecidos neste artigo, o Suplente não tomar posse e nem requerer sua prorrogação, considerar-se-á como tendo renunciado ao mandato, convocando-se o segundo Suplente, que terá, em qualquer hipótese, trinta dias para prestar o compromisso.'
					},
					"2":{
						"caput":'O Suplente, por ocasião da primeira convocação, deverá prestar o compromisso na forma do art. 4º e, nas seguintes, o Presidente comunicará à Casa a sua volta ao exercício do mandato.'
					},
			}
		}]
	},
	"6":{
		"caput":'Nos casos dos arts. 4º, § 5º, e 5º, § 1º, havendo requerimento e findo o prazo sem ter sido votado, considerar-se-á como concedida a prorrogação.'
	},
	"7":{
		"caput":'Por ocasião da posse, o Senador ou Suplente convocado comunicará à Mesa, por escrito, o nome parlamentar com que deverá figurar nas publicações e registros da Casa e a sua filiação partidária, observando o disposto no art. 78, parágrafo único.',
		"enumeravel": [{
			"tipo":"paragrafo",
			"lista":{
					"1":{
						"caput":'Do nome parlamentar não constarão mais de duas palavras, não computadas nesse número as preposições.'
					},
					"2":{
						"caput":'A alteração do nome parlamentar ou da filiação partidária deverá ser comunicada, por escrito, à Mesa, vigorando a partir da publicação no Diário do Senado Federal.'
					},
			}
		}]
	},
	"8":{
		"caput":'O Senador deve apresentar-se no edifício do Senado à hora regimental, para tomar parte nas sessões do Plenário, bem como à hora de reunião da comissão de que seja membro, cabendo-lhe:',
		"enumeravel": [{
			"tipo":"inciso",
			"lista":{
					"1":{
						"caput":'oferecer proposições, discutir, votar e ser votado;'
					},
					"2":{
						"caput":'solicitar, de acordo com o disposto no art. 216, informações às autoridades sobre fatos relativos ao serviço público ou úteis à elaboração legislativa;'
					},
					"3":{
						"caput":'usar da palavra, observadas as disposições deste Regimento.'
					},
			}
		}]
	},
	"9":{
		"caput":'É facultado ao Senador, uma vez empossado:',
		"enumeravel": [{
			"tipo":"inciso",
			"lista":{
					"1":{
						"caput":'examinar quaisquer documentos existentes no Arquivo;'
					},
					"2":{
						"caput":'requisitar da autoridade competente, por intermédio da Mesa ou diretamente, providências para garantia das suas imunidades e informações para sua defesa;'
					},
					"3":{
						"caput":'frequentar a Biblioteca e utilizar os seus livros e publicações, podendo requisitá-los para consulta, fora das dependências do Senado, desde que não se trate de obras raras, assim classificadas pela Comissão Diretora;'
					},
					"4":{
						"caput":'frequentar o edifício do Senado e as respectivas dependências, só ou acompanhado, vedado ao acompanhante o ingresso no plenário, durante as sessões, e nos locais privativos dos Senadores;'
					},
					"5":{
						"caput":'utilizar-se dos diversos serviços do Senado, desde que para fins relacionados com as suas funções;'
					},
					"6":{
						"caput":'receber em sua residência o Diário do Senado Federal, o do Congresso Nacional e o Diário Oficial da União.'
					},
			}
		}]
	},
	"10":{
		"caput":'O Senador ou Suplente, por ocasião da posse, inscreverá, em livro específico, de próprio punho, seu nome, o nome parlamentar, a respectiva rubrica, filiação partidária, idade, estado civil e outras declarações que julgue conveniente fazer.'
	},
	"11":{
		"caput":'Com base nos dados referidos no art. 10, o Primeiro-Secretário expedirá as respectivas carteiras de identidade.'
	},
	"12":{
		"caput":'A remuneração do Senador é devida:',
		"enumeravel": [{
			"tipo":"inciso",
			"lista":{
					"1":{
						"caput":'a partir do início da legislatura, ao diplomado antes da instalação da primeira sessão legislativa ordinária;'
					},
					"2":{
						"caput":'a partir da expedição do diploma, ao diplomado posteriormente à instalação;'
					},
					"3":{
						"caput":'a partir da posse, ao Suplente em exercício.'
					},
			}
		}]
	},
	"13":{
		"caput":'Será considerado ausente o Senador cujo nome não conste da lista de comparecimento, salvo se em licença, ou em representação a serviço da Casa ou, ainda, em missão política ou cultural de interesse parlamentar, previamente aprovada pela Mesa, obedecido o disposto no art. 40.',
		"enumeravel": [{
			"tipo":"paragrafo",
			"lista":{
					"1":{
						"caput":'O painel do plenário será acionado nas sessões deliberativas.'
					},
					"2":{
						"caput":'Considerar-se-á ainda ausente o Senador que, embora conste da lista de presença das sessões deliberativas, deixar de comparecer às votações, salvo se em obstrução declarada por líder partidário ou de bloco parlamentar.'
					},
			}
		}]
	},
	"14":{
		"caput":'O Senador poderá fazer uso da palavra:',
		"enumeravel": [{
			"tipo":"inciso",
			"lista":{
					"1":{
						"caput":'nos cento e vinte minutos que antecedem a Ordem do Dia, por dez minutos, nas sessões deliberativas, e por vinte minutos, nas sessões não deliberativas;'
					},
					"2":{
						"caput":'se líder, uma vez por sessão:',
						"enumeravel": [{
							"tipo":"letra",
							"lista":{
									"1":{
										"caput":'cinco minutos, em qualquer fase da sessão, exceto durante a Ordem do Dia, para comunicação urgente de interesse partidário; ou'
									},
									"2":{
										"caput":'vinte minutos, após a Ordem do Dia, com preferência sobre os oradores inscritos;'
									},
							}
						}]
					},
					"3":{
						"caput":'na discussão de qualquer proposição (art. 273), uma só vez, por dez minutos;'
					},
					"4":{
						"caput":'na discussão da proposição em regime de urgência (art. 336), uma só vez, por dez minutos, limitada a palavra a cinco Senadores a favor e cinco contra;'
					},
					"5":{
						"caput":'na discussão da redação final (art. 321), uma só vez, por cinco minutos, o relator e um Senador de cada partido;'
					},
					"6":{
						"caput":'no encaminhamento de votação (art. 308 e parágrafo único do art. 310), uma só vez, por cinco minutos;'
					},
					"7":{
						"caput":'no encaminhamento de votação de proposição em regime de urgência (art. 336), uma só vez, por cinco minutos, o relator da comissão de mérito e os líderes de partido ou bloco parlamentar ou Senadores por eles designados;'
					},
					"8":{
						"caput":'para explicação pessoal, em qualquer fase da sessão, por cinco minutos, se nominalmente citado na ocasião, para esclarecimento de ato ou fato que lhe tenha sido atribuído em discurso ou aparte, não sendo a palavra dada, com essa finalidade, a mais de dois oradores na mesma sessão;'
					},
					"9":{
						"caput":'para comunicação inadiável, manifestação de aplauso ou semelhante, homenagem de pesar, uma só vez, por cinco minutos;'
					},
					"10":{
						"caput":'em qualquer fase da sessão, por cinco minutos:',
						"enumeravel": [{
							"tipo":"letra",
							"lista":{
									"1":{
										"caput":'ordem, para indagação sobre andamento dos trabalhos, reclamação quanto à observância do Regimento, indicação de falha ou equívoco em relação à matéria da Ordem do Dia, vedado, porém, abordar assunto já resolvido pela Presidência;'
									},
									"2":{
										"caput":'suscitar questão de ordem, nos termos do art. 403;'
									},
									"3":{
										"caput":'contraditar questão de ordem, limitada a palavra a um só Senador;'
									},
							}
						}]
					},
					"11":{
						"caput":'após a Ordem do Dia, pelo prazo de vinte minutos, para as considerações que entender (art. 176);'
					},
					"12":{
						"caput":'para apartear, por dois minutos, obedecidas as seguintes normas:',
						"enumeravel": [{
							"tipo":"letra",
							"lista":{
									"1":{
										"caput":'aparte dependerá de permissão do orador, subordinando-se, em tudo que lhe for aplicável, às disposições referentes aos debates;'
									},
									"2":{
										"caput":'serão permitidos apartes:'
									},
									"3":{
										"caput":'ao Presidente;'
									},
									"4":{
										"caput":'a parecer oral;'
									},
									"5":{
										"caput":'a encaminhamento de votação, salvo nos casos de requerimento de homenagem de pesar ou de voto de aplauso ou semelhante;'
									},
									"6":{
										"caput":'a explicação pessoal;'
									},
									"7":{
										"caput":'a questão de ordem;'
									},
									"8":{
										"caput":'a contradita a questão de ordem;'
									},
									"9":{
										"caput":'a uso da palavra por cinco minutos;'
									},
									"10":{
										"caput":'recusa de permissão para apartear será sempre compreendida em caráter geral, ainda que proferida em relação a um só Senador;'
									},
									"11":{
										"caput":'aparte proferido sem permissão do orador não será publicado;'
									},
									"12":{
										"caput":'apartear, o Senador conservar-se-á sentado e falará ao microfone;'
									},
							}
						}]
					},
					"13":{
						"caput":'para interpelar Ministro de Estado, por cinco minutos, e para a réplica, por dois minutos (art. 398, X).'
					},
					"14":{
						"caput":'por delegação de sua liderança partidária, por cinco minutos, observado o disposto na alínea a do inciso II e do § 3º deste artigo.'
					},
			}
		}]
	},
	"15":{
		"caput":'Os prazos previstos no art. 14 só poderão ser prorrogados, pelo Presidente, por um ou dois minutos, para permitir o encerramento do pronunciamento, após o que o som do orador será cortado, não sendo lícito ao Senador utilizar-se do tempo destinado a outro, em acréscimo ao de que disponha.'
	},
	"16":{
		"caput":'A palavra será dada na ordem em que for pedida, salvo inscrição.'
	},
	"17":{
		"caput":'Haverá, sobre a mesa, no plenário, livro especial no qual se inscreverão os Senadores que quiserem usar da palavra, nas diversas fases da sessão, devendo ser rigorosamente observada a ordem de inscrição.',
		"enumeravel": [{
			"tipo":"paragrafo",
			"lista":{
					"1":{
						"caput":'O Senador só poderá usar da palavra mais de duas vezes por semana se não houver outro orador inscrito que pretenda ocupar a tribuna.'
					},
					"2":{
						"caput":'A inscrição será para cada sessão, podendo ser aceita com antecedência não superior a duas sessões deliberativas ordinárias ou não deliberativas.'
					},
			}
		}]
	},
	"18":{
		"caput":'O Senador, no uso da palavra, poderá ser interrompido:',
		"enumeravel": [{
			"tipo":"inciso",
			"lista":{
					"1":{
						"caput":'pelo Presidente:',
						"enumeravel": [{
							"tipo":"letra",
							"lista":{
									"1":{
										"caput":'leitura e votação de requerimento de urgência, no caso do art. 336, I, e deliberação sobre a matéria correspondente;'
									},
									"2":{
										"caput":'votação não realizada no momento oportuno, por falta de número (arts. 304 e 305);'
									},
									"3":{
										"caput":'comunicação importante;'
									},
									"4":{
										"caput":'recepção de visitante (art. 199);'
									},
									"5":{
										"caput":'votação de requerimento de prorrogação da sessão;'
									},
									"6":{
										"caput":'suspender a sessão, em caso de tumulto no recinto ou ocorrência grave no edifício do Senado;'
									},
									"7":{
										"caput":'adverti-lo quanto à observância do Regimento;'
									},
									"8":{
										"caput":'prestar esclarecimentos que interessem à boa ordem dos trabalhos;'
									},
							}
						}]
					},
					"2":{
						"caput":'por outro Senador:',
						"enumeravel": [{
							"tipo":"letra",
							"lista":{
									"1":{
										"caput":'o seu consentimento, para aparteá-lo;'
									},
									"2":{
										"caput":'de seu consentimento, para formular à Presidência reclamação quanto à observância do Regimento.',
										"enumeravel": [{
											"tipo":"paragrafo",
											"lista":{
													"1":{
														"caput":'O tempo de interrupção previsto neste artigo será descontado em favor do orador, salvo quanto ao disposto no inciso II, a.'
													},
											}
										}]
									},
							}
						}]
					},
			}
		}]
	},
	"19":{
		"caput":'Ao Senador é vedado:',
		"enumeravel": [{
			"tipo":"inciso",
			"lista":{
					"1":{
						"caput":'usar de expressões descorteses ou insultuosas;'
					},
					"2":{
						"caput":'falar sobre resultado de deliberação definitiva do Plenário, salvo em explicação pessoal.'
					},
			}
		}]
	},
	"20":{
		"caput":'Não será lícito ler da tribuna ou incluir em discurso, aparte, declaração de voto ou em qualquer outra manifestação pública, documento de natureza sigilosa.'
	},
	"21":{
		"caput":'O Senador, ao fazer uso da palavra, manter-se-á de pé, salvo licença para se conservar sentado, por motivo de saúde, e dirigir-se-á ao Presidente ou a este e aos Senadores, não lhe sendo lícito permanecer de costas para a Mesa.'
	},
	"22":{
		"caput":'Em caso de infração do art. 19, I, proceder-se-á da seguinte maneira:',
		"enumeravel": [{
			"tipo":"inciso",
			"lista":{
					"1":{
						"caput":'o Presidente advertirá o Senador, usando da expressão “Atenção!”;'
					},
					"2":{
						"caput":'se essa observação não for suficiente, o Presidente dirá “Senador F..., atenção!”;'
					},
					"3":{
						"caput":'não bastando o aviso nominal, o Presidente retirar-lhe-á a palavra;'
					},
					"4":{
						"caput":'insistindo o Senador em desatender às advertências, o Presidente determinará sua saída do recinto, o que deverá ser feito imediatamente;'
					},
					"5":{
						"caput":'em caso de recusa, o Presidente suspenderá a sessão, que não será reaberta até que seja obedecida sua determinação.'
					},
			}
		}]
	},
	"23":{
		"caput":'Constituirá desacato ao Senado:',
		"enumeravel": [{
			"tipo":"inciso",
			"lista":{
					"1":{
						"caput":'reincidir na desobediência à medida disciplinar prevista no art. 22, IV;'
					},
					"2":{
						"caput":'agressão, por atos ou palavras, praticada por Senador contra a Mesa ou contra outro Senador, nas dependências da Casa.'
					},
			}
		}]
	},
	"24":{
		"caput":'Em caso de desacato ao Senado, proceder-se-á de acordo com as seguintes normas:',
		"enumeravel": [{
			"tipo":"inciso",
			"lista":{
					"1":{
						"caput":'o Segundo-Secretário, por determinação da Presidência, lavrará relatório pormenorizado do ocorrido;'
					},
					"2":{
						"caput":'cópias autenticadas do relatório serão encaminhadas aos demais membros da Mesa e aos líderes que, em reunião convocada pelo Presidente, deliberarão:',
						"enumeravel": [{
							"tipo":"letra",
							"lista":{
									"1":{
										"caput":'arquivamento do relatório;'
									},
									"2":{
										"caput":'constituição de comissão para, sobre o fato, se manifestar;'
									},
							}
						}]
					},
					"3":{
						"caput":'na hipótese prevista na alínea b do inciso II, a comissão, de posse do relatório, reunir-se-á, no prazo de duas horas, a partir de sua constituição, a fim de eleger o Presidente, que designará relator para a matéria;'
					},
					"4":{
						"caput":'a comissão poderá ouvir as pessoas envolvidas no caso e as testemunhas que entender;'
					},
					"5":{
						"caput":'a comissão terá o prazo de dois dias úteis para emitir parecer, que será conclusivo, podendo propor uma das seguintes medidas:',
						"enumeravel": [{
							"tipo":"letra",
							"lista":{
									"1":{
										"caput":'pública ao Senador;'
									},
									"2":{
										"caput":'de processo de perda de mandato (Const., art. 55, II);'
									},
							}
						}]
					},
					"6":{
						"caput":'aprovado pela comissão, o parecer será encaminhado à Mesa para o procedimento cabível no caso.'
					},
			}
		}]
	},
	"25":{
		"caput":'Se algum Senador praticar, dentro do edifício do Senado, ato incompatível com o decoro parlamentar ou com a compostura pessoal, a Mesa dele conhecerá e abrirá inquérito,',
		"enumeravel": [{
			"tipo":"None",
			"lista":{
					"1":{
						"caput":'caso ao Plenário, que sobre ele deliberará, no prazo improrrogável de dez dias úteis.'
					},
			}
		}]
	},
	"26":{
		"caput":'Falecendo algum Senador em período de funcionamento do Senado, o Presidente comunicará o fato à Casa e proporá seja a sessão do dia dedicada a reverenciar a memória do extinto, deliberando o Plenário com qualquer número.'
	},
	"27":{
		"caput":'O Senado far-se-á representar, nas cerimônias fúnebres que se realizarem pelo falecimento de qualquer dos seus membros, por uma comissão constituída, no mínimo, de três Senadores, designados pelo Presidente, de ofício ou mediante deliberação do Plenário, sem embargo de outras homenagens aprovadas.',
		"enumeravel": [{
			"tipo":"paragrafo",
			"lista":{
					"1":{
						"caput":'Na hipótese de ser a comissão designada de ofício, o fato será comunicado ao Plenário, pelo Presidente.'
					},
			}
		}]
	},
	"28":{
		"caput":'As vagas, no Senado, verificar-se-ão em virtude de:',
		"enumeravel": [{
			"tipo":"inciso",
			"lista":{
					"1":{
						"caput":'falecimento;'
					},
					"2":{
						"caput":'renúncia;'
					},
					"3":{
						"caput":'perda de mandato.'
					},
			}
		}]
	},
	"29":{
		"caput":'A comunicação de renúncia à senatória ou à suplência deve ser dirigida por escrito à Mesa, com firma reconhecida, e independe da aprovação do Senado, mas somente tornar-se-á efetiva e irretratável depois de lida no Período do Expediente e publicada no Diário do Senado Federal.',
		"enumeravel": [{
			"tipo":"paragrafo",
			"lista":{
					"1":{
						"caput":'É lícito ao Senador, ou ao Suplente em exercício, fazer em plenário, oralmente, a renúncia ao mandato, a qual tornar-se-á efetiva e irretratável depois da sua publicação no Diário do Senado Federal.'
					},
			}
		}]
	},
	"30":{
		"caput":'Considerar-se-á como tendo renunciado (arts. 4º, § 6º, e 5º, § 1º):',
		"enumeravel": [{
			"tipo":"inciso",
			"lista":{
					"1":{
						"caput":'o Senador que não prestar o compromisso no prazo estabelecido neste Regimento;'
					},
					"2":{
						"caput":'o Suplente que, convocado, não se apresentar para entrar em exercício no prazo estabelecido neste Regimento.'
					},
			}
		}]
	},
	"31":{
		"caput":'A ocorrência de vacância, em qualquer hipótese, será comunicada pelo Presidente ao Plenário.',
		"enumeravel": [{
			"tipo":"paragrafo",
			"lista":{
					"1":{
						"caput":'Nos casos do art. 30, até o dia útil que se seguir à publicação da comunicação de vacância, qualquer Senador dela poderá interpor recurso para o Plenário, que deliberará, ouvida a Comissão de Constituição, Justiça e Cidadania.'
					},
			}
		}]
	},
	"32":{
		"caput":'Perde o mandato o Senador (Const., art. 55):',
		"enumeravel": [{
			"tipo":"inciso",
			"lista":{
					"1":{
						"caput":'que infringir qualquer das proibições constantes do art. 54 da Constituição;'
					},
					"2":{
						"caput":'cujo procedimento for declarado incompatível com o decoro parlamentar;'
					},
					"3":{
						"caput":'que deixar de comparecer à terça parte das sessões deliberativas ordinárias do Senado, em cada sessão legislativa anual, salvo licença ou missão autorizada;'
					},
					"4":{
						"caput":'que perder ou tiver suspensos os direitos políticos;'
					},
					"5":{
						"caput":'quando o decretar a Justiça Eleitoral;'
					},
					"6":{
						"caput":'que sofrer condenação criminal em sentença definitiva e irrecorrível.'
					},
			}
		}]
	},
	"33":{
		"caput":'Admitida a representação pelo voto do Plenário, o Presidente designará comissão composta de nove membros para instrução da matéria.',
		"enumeravel": [{
			"tipo":"paragrafo",
			"lista":{
					"1":{
						"caput":'Recebida e processada, será fornecida cópia da representação ao acusado, que terá o prazo de quinze dias úteis, prorrogável por igual período, para apresentar, à comissão, sua defesa escrita.'
					},
					"2":{
						"caput":'Apresentada ou não a defesa, a comissão, após proceder às diligências que entender necessárias, emitirá parecer, concluindo por projeto de resolução, no sentido da perda do mandato ou do arquivamento definitivo do processo.'
					},
					"3":{
						"caput":'Para falar sobre o parecer, será concedida vista do processo ao acusado pelo prazo de dez dias úteis.'
					},
			}
		}]
	},
	"34":{
		"caput":'O acusado poderá assistir, pessoalmente ou por procurador, a todos os atos e diligências, e requerer o que julgar conveniente aos interesses da defesa.'
	},
	"35":{
		"caput":'O projeto de resolução, depois de lido no Período do Expediente, publicado no Diário do Senado Federal em avulso eletrônico, será incluído em Ordem do Dia e submetido à votação.'
	},
	"36":{
		"caput":'As imunidades dos Senadores subsistirão durante o estado de sítio, só podendo ser suspensas mediante voto de dois terços dos membros da Casa, nos casos de atos praticados fora do recinto do Congresso Nacional, que sejam incompatíveis com a execução da medida (Const., art. 53, § 8º).'
	},
	"37":{
		"caput":'Serão observadas, na decretação da suspensão das imunidades, as disposições do capítulo VIII no que forem aplicáveis.'
	},
	"38":{
		"caput":'Considerar-se-á como ausente, para efeito do disposto no art. 55, III, da Constituição, o Senador cujo nome não conste das listas de comparecimento das sessões deliberativas ordinárias.',
		"enumeravel": [{
			"tipo":"paragrafo",
			"lista":{
					"1":{
						"caput":'Para os efeitos deste artigo, aplica-se o disposto no art. 13, não sendo, ainda, considerada a ausência do Senador nos sessenta dias anteriores às eleições gerais.'
					},
			}
		}]
	},
	"39":{
		"caput":'O Senador deverá comunicar ao Presidente sempre que:',
		"enumeravel": [{
			"tipo":"inciso",
			"lista":{
					"1":{
						"caput":'ausentar-se do País;'
					},
					"2":{
						"caput":'assumir cargo de Ministro de Estado, de Governador de Território, de Secretário de Estado, do Distrito Federal, de Território, de Prefeitura de Capital ou de chefe de missão diplomática temporária (Const., art. 56, I).'
					},
			}
		}]
	},
	"40":{
		"caput":'A ausência do Senador, quando incumbido de representação da Casa ou, ainda, no desempenho de missão no País ou no exterior, deverá ser autorizada mediante deliberação do Plenário, se houver ônus para o Senado.',
		"enumeravel": [{
			"tipo":"paragrafo",
			"lista":{
					"1":{
						"caput":'A autorização poderá ser:',
						"enumeravel": [{
							"tipo":"inciso",
							"lista":{
									"1":{
										"caput":'solicitada pelo interessado;'
									},
									"2":{
										"caput":'proposta:',
										"enumeravel": [{
											"tipo":"letra",
											"lista":{
													"1":{
														"caput":'Presidência, quando de sua autoria a indicação;'
													},
													"2":{
														"caput":'Comissão de Relações Exteriores e Defesa Nacional, no caso de missão a realizar-se no estrangeiro;'
													},
													"3":{
														"caput":'comissão que tiver maior pertinência, no caso de missão a realizar-se no País;'
													},
													"4":{
														"caput":'líder do bloco parlamentar ou do partido a que pertença o interessado.',
														"enumeravel": [{
															"tipo":"paragrafo",
															"lista":{
																	"1":{
																		"caput":'Na solicitação ou na proposta deverá ser mencionado o prazo de afastamento do Senador.'
																	},
																	"2":{
																		"caput":'A solicitação ou proposta será lida no Período do Expediente e votada em seguida à Ordem do Dia da mesma sessão.'
																	},
																	"3":{
																		"caput":'No caso do § 1º, I e II, d, será ouvida a Comissão de Relações Exteriores e Defesa Nacional ou a que tiver maior pertinência, sendo o parecer oferecido, imediatamente, por escrito ou oralmente, podendo o relator solicitar prazo não excedente a duas horas.'
																	},
																	"4":{
																		"caput":'Os casos de licença serão decididos pela Mesa com recurso para o Plenário.'
																	},
															}
														}]
													},
											}
										}]
									},
							}
						}]
					},
			}
		}]
	},
	"41":{
		"caput":'Nos casos do art. 40, se não for possível, por falta de número, realizar-se a votação em duas sessões deliberativas ordinárias consecutivas, ou se o Senado estiver em recesso, o pedido será despachado pelo Presidente, retroagindo os efeitos da licença à data do requerimento.'
	},
	"42":{
		"caput":'O Senador afastado do exercício do mandato não poderá ser incumbido de representação da Casa, de comissão, ou de grupo parlamentar.'
	},
	"43":{
		"caput":'Para os efeitos do disposto no art. 55, III, da Constituição, o Senador poderá:',
		"enumeravel": [{
			"tipo":"inciso",
			"lista":{
					"1":{
						"caput":'quando, por motivo de doença, se encontre impossibilitado de comparecer às sessões do Senado, requerer licença, instruída com laudo de inspeção de saúde (Const., art. 56, II);'
					},
					"2":{
						"caput":'solicitar licença para tratar de interesses particulares, desde que o afastamento não ultrapasse cento e vinte dias por sessão legislativa (Const., art. 56, II).'
					},
			}
		}]
	},
	"44":{
		"caput":'Considerar-se-á como licença concedida, para os efeitos do art. 55, III, da Constituição, o não comparecimento às sessões do Senador temporariamente privado da liberdade, em virtude de processo criminal em curso.'
	},
	"44-A":{
		"caput":'Considerar-se-á como licença autorizada, para os fins do disposto no art. 55, III, da Constituição, e no art. 38, parágrafo único, deste Regimento, a ausência às sessões de Senador candidato à Presidência ou Vice-Presidência da República, no período compreendido entre o registro da candidatura no Tribunal Superior Eleitoral e a apuração do respectivo pleito.',
		"enumeravel": [{
			"tipo":"paragrafo",
			"lista":{
					"1":{
						"caput":'O disposto neste artigo aplica-se aos candidatos que concorrerem ao segundo turno.'
					},
					"2":{
						"caput":'Para os fins do disposto neste artigo o Senador deverá encaminhar à Mesa certidão comprobatória do registro de sua candidatura perante a Justiça Eleitoral.'
					},
			}
		}]
	},
	"45":{
		"caput":'Dar-se-á a convocação de Suplente nos casos de vaga, de afastamento do exercício do mandato para investidura nos cargos referidos no art. 39, II, ou de licença por prazo superior a cento e vinte dias (Const., art. 56, § 1º).'
	},
	"46":{
		"caput":'A Mesa se compõe de Presidente, dois Vice-Presidentes e quatro Secretários.',
		"enumeravel": [{
			"tipo":"paragrafo",
			"lista":{
					"1":{
						"caput":'Os Secretários substituir-se-ão conforme a numeração ordinal e, nesta ordem, substituirão o Presidente, na falta dos Vice-Presidentes.'
					},
					"2":{
						"caput":'Os Secretários serão substituídos, em seus impedimentos, por Suplentes em número de quatro.'
					},
					"3":{
						"caput":'O Presidente convidará quaisquer Senadores para substituírem, em sessão, os Secretários, na ausência destes e dos Suplentes.'
					},
					"4":{
						"caput":'Não se achando presentes o Presidente e seus substitutos legais, inclusive os Suplentes, assumirá a Presidência o Senador mais idoso.'
					},
			}
		}]
	},
	"47":{
		"caput":'A assunção a cargo de Ministro de Estado, de Governador de Território e de Secretário de Estado, do Distrito Federal, de Território, de Prefeitura de Capital, ou de chefe de missão diplomática temporária, implica renúncia ao cargo que o Senador exerça na Mesa.'
	},
	"48":{
		"caput":'Ao Presidente compete:',
		"enumeravel": [{
			"tipo":"inciso",
			"lista":{
					"1":{
						"caput":'exercer as atribuições previstas nos arts. 57, § 6º, I e II, 66, § 7º, e 80 da Constituição;'
					},
					"2":{
						"caput":'velar pelo respeito às prerrogativas do Senado e às imunidades dos Senadores;'
					},
					"3":{
						"caput":'convocar e presidir as sessões do Senado e as sessões conjuntas do Congresso Nacional;'
					},
					"4":{
						"caput":'propor a transformação de sessão pública em secreta;'
					},
					"5":{
						"caput":'propor a prorrogação da sessão;'
					},
					"6":{
						"caput":'designar a Ordem do Dia das sessões deliberativas e retirar matéria da pauta para cumprimento de despacho, correção de erro ou omissão no avulso eletrônico e para sanar falhas da instrução;'
					},
					"7":{
						"caput":'fazer ao Plenário, em qualquer momento, comunicação de interesse do Senado e do País;'
					},
					"8":{
						"caput":'fazer observar na sessão a Constituição, as leis e este Regimento;'
					},
					"9":{
						"caput":'assinar as atas das sessões secretas, uma vez aprovadas;'
					},
					"10":{
						"caput":'determinar o destino do expediente lido e distribuir as matérias às comissões;'
					},
					"11":{
						"caput":'impugnar as proposições que lhe pareçam contrárias à Constituição, às leis, ou a este Regimento, ressalvado ao autor recurso para o Plenário, que decidirá após audiência da Comissão de Constituição, Justiça e Cidadania;'
					},
					"12":{
						"caput":'declarar prejudicada proposição que assim deva ser considerada, na conformidade regimental;'
					},
					"13":{
						"caput":'decidir as questões de ordem;'
					},
					"14":{
						"caput":'orientar as discussões e fixar os pontos sobre que devam versar, podendo, quando conveniente, dividir as proposições para fins de votação;'
					},
					"15":{
						"caput":'dar posse aos Senadores;'
					},
					"16":{
						"caput":'convocar Suplente de Senador;'
					},
					"17":{
						"caput":'comunicar ao Tribunal Superior Eleitoral a ocorrência de vaga de Senador, quando não haja Suplente a convocar e faltarem mais de quinze meses para o término do mandato (Const., art. 56, II, § 2º);'
					},
					"18":{
						"caput":'propor ao Plenário a indicação de Senador para desempenhar missão temporária no País ou no exterior;'
					},
					"19":{
						"caput":'propor ao Plenário a constituição de comissão para a representação externa do Senado;'
					},
					"20":{
						"caput":'designar oradores para as sessões especiais do Senado e sessões solenes do Congresso Nacional;'
					},
					"21":{
						"caput":'designar substitutos de membros das comissões e nomear relator em plenário;'
					},
					"22":{
						"caput":'convidar, se necessário, o relator ou o Presidente da comissão a explicar as conclusões de seu parecer;'
					},
					"23":{
						"caput":'desempatar as votações, quando ostensivas;'
					},
					"24":{
						"caput":'proclamar o resultado das votações;'
					},
					"25":{
						"caput":'despachar, de acordo com o disposto no art. 41, requerimento de licença de Senador;'
					},
					"26":{
						"caput":'despachar os requerimentos constantes do parágrafo único do art. 214 e do inciso II do art. 215;'
					},
					"27":{
						"caput":'assinar os autógrafos dos projetos e emendas a serem remetidos à Câmara dos Deputados, e dos projetos destinados à sanção;'
					},
					"28":{
						"caput":'promulgar as resoluções do Senado e os decretos legislativos;'
					},
					"29":{
						"caput":'assinar a correspondência dirigida pelo Senado às seguintes autoridades:',
						"enumeravel": [{
							"tipo":"letra",
							"lista":{
									"1":{
										"caput":'da República;'
									},
									"2":{
										"caput":'da República;'
									},
									"3":{
										"caput":'da Câmara dos Deputados;'
									},
									"4":{
										"caput":'do Supremo Tribunal Federal, dos Tribunais Superiores do País e do Tribunal de Contas da União;'
									},
									"5":{
										"caput":'de Governos estrangeiros e seus representantes no Brasil;'
									},
									"6":{
										"caput":'das Casas de Parlamento estrangeiro;'
									},
									"7":{
										"caput":'dos Estados, do Distrito Federal e dos Territórios Federais;'
									},
									"8":{
										"caput":'das Assembleias Legislativas dos Estados;'
									},
									"9":{
										"caput":'judiciárias, em resposta a pedidos de informações sobre assuntos pertinentes ao Senado, no curso de feitos judiciais;'
									},
							}
						}]
					},
					"30":{
						"caput":'autorizar a divulgação das sessões, nos termos do disposto no art. 186;'
					},
					"31":{
						"caput":'promover a publicação dos debates e de todos os trabalhos e atos do Senado, impedindo a de expressões vedadas por este Regimento, inclusive quando constantes de documento lido pelo orador;'
					},
					"32":{
						"caput":'avocar a representação do Senado quando se trate de atos públicos de especial relevância, e não seja possível designar comissão ou Senador para esse fim;'
					},
					"33":{
						"caput":'resolver, ouvido o Plenário, qualquer caso não previsto neste Regimento;'
					},
					"34":{
						"caput":'presidir as reuniões da Mesa e da Comissão Diretora, podendo discutir e votar;'
					},
					"35":{
						"caput":'exercer a competência fixada no Regulamento Administrativo do Senado Federal.'
					},
			}
		}]
	},
	"49":{
		"caput":'Na distribuição das matérias subordinadas, na forma do art. 91, à apreciação terminativa das comissões, o Presidente do Senado, quando a proposição tiver seu mérito vinculado a mais de uma comissão, poderá:',
		"enumeravel": [{
			"tipo":"inciso",
			"lista":{
					"1":{
						"caput":'definir qual a comissão de maior pertinência que deva sobre ela decidir;'
					},
					"2":{
						"caput":'determinar que o seu estudo seja feito em reunião conjunta das comissões, observado, no que couber, o disposto no art. 113.'
					},
			}
		}]
	},
	"50":{
		"caput":'O Presidente somente se dirigirá ao Plenário da cadeira presidencial, não lhe sendo lícito dialogar com os Senadores nem os apartear, podendo, entretanto, interrompê-los nos casos previstos no art. 18, I.',
		"enumeravel": [{
			"tipo":"paragrafo",
			"lista":{
					"1":{
						"caput":'O Presidente deixará a cadeira presidencial sempre que, como Senador, quiser participar ativamente dos trabalhos da sessão.'
					},
			}
		}]
	},
	"51":{
		"caput":'O Presidente terá apenas voto de desempate nas votações ostensivas, contando-se, porém, a sua presença para efeito de quorum e podendo, em escrutínio secreto, votar como qualquer Senador.'
	},
	"52":{
		"caput":'Ao Primeiro Vice-Presidente compete:',
		"enumeravel": [{
			"tipo":"inciso",
			"lista":{
					"1":{
						"caput":'substituir o Presidente nas suas faltas ou impedimentos;'
					},
					"2":{
						"caput":'exercer as atribuições estabelecidas no art. 66, § 7º, da Constituição, quando não as tenha exercido o Presidente.'
					},
			}
		}]
	},
	"53":{
		"caput":'Ao Segundo Vice-Presidente compete substituir o Primeiro Vice-Presidente nas suas faltas ou impedimentos.'
	},
	"54":{
		"caput":'Ao Primeiro-Secretário compete:',
		"enumeravel": [{
			"tipo":"inciso",
			"lista":{
					"1":{
						"caput":'ler em plenário, na íntegra ou em resumo, a correspondência oficial recebida pelo Senado, os pareceres das comissões, as proposições apresentadas quando os seus autores não as tiverem lido, e quaisquer outros documentos que devam constar do expediente da sessão;'
					},
					"2":{
						"caput":'despachar a matéria do expediente que lhe for distribuída pelo Presidente;'
					},
					"3":{
						"caput":'assinar a correspondência do Senado Federal, salvo nas hipóteses do art. 48, inciso XXIX, e fornecer certidões;'
					},
					"4":{
						"caput":'receber a correspondência dirigida ao Senado e tomar as providências dela decorrentes;'
					},
					"5":{
						"caput":'assinar, depois do Presidente, as atas das sessões secretas;'
					},
					"6":{
						"caput":'rubricar a listagem especial com o resultado da votação realizada através do sistema eletrônico, e determinar sua anexação ao processo da matéria respectiva;'
					},
					"7":{
						"caput":'promover a guarda das proposições em curso;'
					},
					"8":{
						"caput":'determinar a entrega aos Senadores dos avulsos eletrônicos relativos à matéria da Ordem do Dia;'
					},
					"9":{
						"caput":'encaminhar os papéis distribuídos às comissões;'
					},
					"10":{
						"caput":'expedir as carteiras de identidade dos Senadores (art. 11).'
					},
			}
		}]
	},
	"55":{
		"caput":'Ao Segundo-Secretário compete lavrar as atas das sessões secretas, proceder-lhes a leitura e assiná-las depois do Primeiro-Secretário.'
	},
	"56":{
		"caput":'Ao Terceiro e Quarto-Secretários compete:',
		"enumeravel": [{
			"tipo":"inciso",
			"lista":{
					"1":{
						"caput":'fazer a chamada dos Senadores, nos casos determinados neste Regimento;'
					},
					"2":{
						"caput":'contar os votos, em verificação de votação;'
					},
					"3":{
						"caput":'auxiliar o Presidente na apuração das eleições, anotando os nomes dos votados e organizando as listas respectivas.'
					},
			}
		}]
	},
	"57":{
		"caput":'Os Secretários, ao lerem qualquer documento, conservar-se-ão de pé e permanecerão sentados ao procederem à chamada dos Senadores.'
	},
	"58":{
		"caput":'Ao integrarem a Mesa, os Secretários não poderão usar da palavra senão para a chamada dos Senadores ou para a leitura de documentos, ordenada pelo Presidente.'
	},
	"59":{
		"caput":'Os membros da Mesa serão eleitos para mandato de dois anos, vedada a reeleição para o período imediatamente subsequente (Const., art. 57, § 4º).',
		"enumeravel": [{
			"tipo":"paragrafo",
			"lista":{
					"1":{
						"caput":'Na constituição da Mesa é assegurada, tanto quanto possível, a representação proporcional dos partidos e blocos parlamentares que participam do Senado (Const., art. 58, § 1º).'
					},
					"2":{
						"caput":'Para os fins do cálculo de proporcionalidade, as bancadas partidárias são consideradas pelos seus quantitativos à data da diplomação.'
					},
					"3":{
						"caput":'No caso de vaga definitiva, o preenchimento far-se-á, dentro de cinco dias úteis, pela forma estabelecida no art. 60, salvo se faltarem menos de cento e vinte dias para o término do mandato da Mesa.'
					},
					"4":{
						"caput":'Enquanto não eleito o novo Presidente, os trabalhos do Senado serão dirigidos pela Mesa do período anterior.'
					},
			}
		}]
	},
	"60":{
		"caput":'A eleição dos membros da Mesa será feita em escrutínio secreto, exigida maioria de votos, presente a maioria da composição do Senado e assegurada, tanto quanto possível, a participação proporcional das representações partidárias ou dos blocos parlamentares com atuação no Senado.',
		"enumeravel": [{
			"tipo":"paragrafo",
			"lista":{
					"1":{
						"caput":'A eleição far-se-á em quatro escrutínios, na seguinte ordem, para:',
						"enumeravel": [{
							"tipo":"inciso",
							"lista":{
									"1":{
										"caput":'o Presidente;'
									},
									"2":{
										"caput":'os Vice-Presidentes;'
									},
									"3":{
										"caput":'os Secretários;'
									},
									"4":{
										"caput":'os Suplentes de Secretários.'
									},
							}
						}]
					},
					"2":{
						"caput":'A eleição, para os cargos constantes dos incisos II a IV do § 1º, far-se-á com cédulas uninominais, contendo a indicação do cargo a preencher, e colocadas, as referentes a cada escrutínio, na mesma sobrecarta.'
					},
					"3":{
						"caput":'Na apuração, o Presidente fará, preliminarmente, a separação das cédulas referentes ao mesmo cargo, lendo-as, em seguida, uma a uma, e passando-as ao Segundo-Secretário, que anotará o resultado.'
					},
					"4":{
						"caput":'Por proposta de um terço dos Senadores ou de líder que represente este número, a eleição para o preenchimento dos cargos constantes do § 1º, II e III, poderá ser feita em um único escrutínio, obedecido o disposto nos §§ 2º e 3º.'
					},
			}
		}]
	},
	"61":{
		"caput":'As representações partidárias poderão constituir bloco parlamentar.',
		"enumeravel": [{
			"tipo":"paragrafo",
			"lista":{
					"1":{
						"caput":'Somente será admitida a formação de bloco parlamentar que represente, no mínimo, um décimo da composição do Senado.'
					},
			}
		}]
	},
	"62":{
		"caput":'O bloco parlamentar terá líder, a ser indicado dentre os líderes das representações partidárias que o compõem.',
		"enumeravel": [{
			"tipo":"paragrafo",
			"lista":{
					"1":{
						"caput":'Os demais líderes assumirão, preferencialmente, as funções de vice-líderes do bloco parlamentar, na ordem indicada pelo titular da liderança.'
					},
					"2":{
						"caput":'As lideranças dos partidos que se coligarem em bloco parlamentar perdem suas atribuições e prerrogativas regimentais.'
					},
			}
		}]
	},
	"63":{
		"caput":'(Revogado).'
	},
	"64":{
		"caput":'Aplica-se ao líder de bloco parlamentar o disposto no art. 66.'
	},
	"65":{
		"caput":'A Maioria, a Minoria e as representações partidárias terão líderes e vice-líderes.',
		"enumeravel": [{
			"tipo":"paragrafo",
			"lista":{
					"1":{
						"caput":'A Maioria é integrada por bloco parlamentar ou representação partidária que represente a maioria absoluta da Casa.'
					},
					"2":{
						"caput":'Formada a Maioria, a Minoria será aquela integrada pelo maior bloco parlamentar ou representação partidária que se lhe opuser.'
					},
					"3":{
						"caput":'A constituição da Maioria e da Minoria será comunicada à Mesa pelos líderes dos blocos parlamentares ou das representações partidárias que as compõem.'
					},
					"4":{
						"caput":'O líder da maioria e o da minoria serão os líderes dos blocos parlamentares ou das representações partidárias que as compõem, e as funções de vice-liderança serão exercidas pelos demais líderes das representações partidárias que integrem os respectivos blocos parlamentares.'
					},
					"5":{
						"caput":'As vantagens administrativas adicionais estabelecidas para os gabinetes das lideranças somente serão admitidas às representações partidárias que tiverem, no mínimo, um vinte e sete avos da composição do Senado Federal.'
					},
					"6":{
						"caput":'Na hipótese de nenhum bloco parlamentar alcançar maioria absoluta, assume as funções constitucionais e regimentais da maioria o líder do bloco parlamentar ou representação partidária que tiver o maior número de integrantes, e da minoria, o líder do bloco parlamentar ou representação partidária que se lhe seguir em número de integrantes e que se lhe opuser.'
					},
					"7":{
						"caput":'A indicação dos líderes partidários será feita no início da primeira e da terceira sessões legislativas de cada legislatura, e comunicada à Mesa em documento subscrito pela maioria dos membros da respectiva bancada, podendo a mesma maioria substituí-los em qualquer oportunidade.'
					},
					"8":{
						"caput":'Os vice-líderes das representações partidárias serão indicados pelos respectivos líderes, na proporção de um vice-líder para cada grupo de três integrantes de bloco parlamentar ou representação partidária, assegurado pelo menos um vice-líder e não computada a fração inferior a três.'
					},
			}
		}]
	},
	"66":{
		"caput":'É da competência dos líderes das representações partidárias, além de outras atribuições regimentais, indicar os representantes das respectivas agremiações nas comissões.',
		"enumeravel": [{
			"tipo":"paragrafo",
			"lista":{
					"1":{
						"caput":'Ausente ou impedido o líder, as suas atribuições serão exercidas pelo vice-líder.'
					},
			}
		}]
	},
	"66-A":{
		"caput":'O Presidente da República poderá indicar Senador para exercer a função de líder do governo.',
		"enumeravel": [{
			"tipo":"paragrafo",
			"lista":{
					"1":{
						"caput":'O líder do governo poderá indicar vice-líderes dentre os integrantes das representações partidárias que apoiem o governo.'
					},
			}
		}]
	},
	"67":{
		"caput":'O Senado, atendendo a convite, poderá se fazer representar em ato ou solenidade de cunho internacional, nacional ou regional, mediante deliberação do Plenário por proposta do Presidente ou a requerimento de qualquer Senador ou comissão.'
	},
	"68":{
		"caput":'A representação externa far-se-á por comissão ou por um Senador.'
	},
	"69":{
		"caput":'É lícito ao Presidente avocar a representação do Senado quando se trate de ato de excepcional relevo.'
	},
	"70":{
		"caput":'Na impossibilidade de o Plenário deliberar sobre a matéria, será facultado ao Presidente autorizar representação externa para:',
		"enumeravel": [{
			"tipo":"inciso",
			"lista":{
					"1":{
						"caput":'chegada ou partida de personalidade de destaque na vida pública nacional ou internacional;'
					},
					"2":{
						"caput":'solenidade de relevante expressão nacional ou internacional;'
					},
					"3":{
						"caput":'funeral ou cerimônia fúnebre em que, regimentalmente, caiba essa representação.'
					},
			}
		}]
	},
	"71":{
		"caput":'O Senado terá comissões permanentes e temporárias (Const., art. 58).'
	},
	"72":{
		"caput":'As comissões permanentes, além da Comissão Diretora, são as seguintes:',
		"enumeravel": [{
			"tipo":"inciso",
			"lista":{
					"1":{
						"caput":'Comissão de Assuntos Econômicos (CAE);'
					},
					"2":{
						"caput":'Comissão de Assuntos Sociais (CAS);'
					},
					"3":{
						"caput":'Comissão de Constituição, Justiça e Cidadania (CCJ);'
					},
					"4":{
						"caput":'Comissão de Educação, Cultura e Esporte (CE);'
					},
					"5":{
						"caput":'Comissão de Transparência, Governança, Fiscalização e Controle e Defesa do Consumidor (CTFC);'
					},
					"6":{
						"caput":'Comissão de Direitos Humanos e Legislação Participativa (CDH);'
					},
					"7":{
						"caput":'Comissão de Relações Exteriores e Defesa Nacional (CRE);'
					},
					"8":{
						"caput":'Comissão de Serviços de Infraestrutura (CI);'
					},
					"9":{
						"caput":'Comissão de Desenvolvimento Regional e Turismo (CDR);'
					},
					"10":{
						"caput":'Comissão de Agricultura e Reforma Agrária (CRA);'
					},
					"11":{
						"caput":'Comissão de Ciência, Tecnologia, Inovação, Comunicação e Informática (CCT);'
					},
					"12":{
						"caput":'Comissão Senado do Futuro (CSF);'
					},
					"13":{
						"caput":'Comissão de Meio Ambiente (CMA).'
					},
			}
		}]
	},
	"73":{
		"caput":'Ressalvada a Comissão Diretora, cabe às comissões permanentes, no âmbito das respectivas competências, criar subcomissões permanentes ou temporárias, até o máximo de quatro, mediante proposta de qualquer de seus integrantes.',
		"enumeravel": [{
			"tipo":"paragrafo",
			"lista":{
					"1":{
						"caput":'Ao funcionamento das subcomissões aplicar-se-ão, no que couber, as disposições deste Regimento relativas ao funcionamento das comissões permanentes.'
					},
					"2":{
						"caput":'Os relatórios aprovados nas subcomissões serão submetidos à apreciação do Plenário da respectiva comissão, sendo a decisão final, para todos os efeitos, proferida em nome desta.'
					},
			}
		}]
	},
	"74":{
		"caput":'As comissões temporárias serão:',
		"enumeravel": [{
			"tipo":"inciso",
			"lista":{
					"1":{
						"caput":'internas - as previstas no Regimento para finalidade específica;'
					},
					"2":{
						"caput":'externas - destinadas a representar o Senado em congressos, solenidades e outros atos públicos;'
					},
					"3":{
						"caput":'parlamentares de inquérito - criadas nos termos do art. 58, § 3º, da Constituição.'
					},
			}
		}]
	},
	"75":{
		"caput":'As comissões externas serão criadas por deliberação do Plenário, a requerimento de qualquer Senador ou comissão, ou por proposta do Presidente.',
		"enumeravel": [{
			"tipo":"paragrafo",
			"lista":{
					"1":{
						"caput":'O requerimento ou a proposta deverá indicar o objetivo da comissão e o número dos respectivos membros.'
					},
			}
		}]
	},
	"76":{
		"caput":'As comissões temporárias se extinguem:',
		"enumeravel": [{
			"tipo":"inciso",
			"lista":{
					"1":{
						"caput":'pela conclusão da sua tarefa; ou'
					},
					"2":{
						"caput":'ao término do respectivo prazo; e'
					},
					"3":{
						"caput":'ao término da sessão legislativa ordinária.'
					},
			}
		}]
	},
	"77":{
		"caput":'A Comissão Diretora é constituída dos titulares da Mesa, tendo as demais comissões permanentes o seguinte número de membros:',
		"enumeravel": [{
			"tipo":"inciso",
			"lista":{
					"1":{
						"caput":'Comissão de Assuntos Econômicos, 27;'
					},
					"2":{
						"caput":'Comissão de Assuntos Sociais, 21;'
					},
					"3":{
						"caput":'Comissão de Constituição, Justiça e Cidadania, 27;'
					},
					"4":{
						"caput":'Comissão de Educação, Cultura e Esporte, 27;'
					},
					"5":{
						"caput":'Comissão de Transparência, Governança, Fiscalização e Controle e Defesa do Consumidor, 17.'
					},
					"6":{
						"caput":'Comissão de Direitos Humanos e Legislação Participativa, 19;'
					},
					"7":{
						"caput":'Comissão de Relações Exteriores e Defesa Nacional, 19;'
					},
					"8":{
						"caput":'Comissão de Serviços de Infraestrutura, 23;'
					},
					"9":{
						"caput":'Comissão de Desenvolvimento Regional e Turismo, 17;'
					},
					"10":{
						"caput":'Comissão de Agricultura e Reforma Agrária, 17;'
					},
					"11":{
						"caput":'Comissão de Ciência, Tecnologia, Inovação, Comunicação e Informática, 17;'
					},
					"12":{
						"caput":'Comissão Senado do Futuro, 11;'
					},
					"13":{
						"caput":'Comissão de Meio Ambiente, 17.'
					},
			}
		}]
	},
	"78":{
		"caput":'Os membros das comissões serão designados pelo Presidente, por indicação escrita dos respectivos líderes, assegurada, tanto quanto possível, a participação proporcional das representações partidárias ou dos blocos parlamentares com atuação no Senado Federal (Const., art. 58, § 1º).',
		"enumeravel": [{
			"tipo":"paragrafo",
			"lista":{
					"1":{
						"caput":'Para fins de proporcionalidade, as representações partidárias são fixadas pelos seus quantitativos à data da diplomação, salvo nos casos de posterior criação, fusão ou incorporação de partidos.'
					},
			}
		}]
	},
	"79":{
		"caput":'No início de cada legislatura, os líderes, uma vez indicados, reunir-se-ão para fixar a representação numérica dos partidos e dos blocos parlamentares nas comissões permanentes.'
	},
	"80":{
		"caput":'Fixada a representação prevista no art. 79, os líderes entregarão à Mesa, nos dois dias úteis subsequentes, as indicações dos titulares das comissões e, em ordem numérica, as dos respectivos suplentes.',
		"enumeravel": [{
			"tipo":"paragrafo",
			"lista":{
					"1":{
						"caput":'Recebidas as indicações, o Presidente fará a designação das comissões.'
					},
			}
		}]
	},
	"81":{
		"caput":'O lugar na comissão pertence ao partido ou bloco parlamentar, competindo ao líder respectivo pedir, em documento escrito, a substituição, em qualquer circunstância ou oportunidade, de titular ou suplente por ele indicado.',
		"enumeravel": [{
			"tipo":"paragrafo",
			"lista":{
					"1":{
						"caput":'A substituição de membro da comissão que se desligar do partido ao qual pertence o lugar na comissão não alterará a proporcionalidade estabelecida nos termos do parágrafo único do art. 78 e do art. 79.'
					},
					"2":{
						"caput":'A substituição de Senador que exerça a presidência de comissão deverá ser precedida de autorização da maioria da respectiva bancada, salvo na hipótese de seu desligamento do partido que ali representar.'
					},
			}
		}]
	},
	"82":{
		"caput":'A designação dos membros das comissões temporárias será feita:',
		"enumeravel": [{
			"tipo":"inciso",
			"lista":{
					"1":{
						"caput":'para as internas, nas oportunidades estabelecidas neste Regimento;'
					},
					"2":{
						"caput":'para as externas, imediatamente após a aprovação do requerimento que der motivo à sua criação.'
					},
			}
		}]
	},
	"83":{
		"caput":'As comissões permanentes, exceto a Diretora, terão suplentes em número igual ao de titulares.',
		"enumeravel": [{
			"tipo":"paragrafo",
			"lista":{
					"1":{
						"caput":'(Revogado).'
					},
			}
		}]
	},
	"84":{
		"caput":'Compete ao suplente substituir o membro da comissão:',
		"enumeravel": [{
			"tipo":"inciso",
			"lista":{
					"1":{
						"caput":'eventualmente, nos seus impedimentos, para quorum nas reuniões;'
					},
					"2":{
						"caput":'por determinados períodos, nas hipóteses previstas nos arts. 39, 40 e 43.'
					},
			}
		}]
	},
	"85":{
		"caput":'Em caso de impedimento temporário de membro da comissão e não havendo suplente a convocar, o Presidente desta solicitará à Presidência da Mesa a designação de substituto, devendo a escolha recair em Senador do mesmo partido ou bloco parlamentar do substituído, salvo se os demais representantes do partido ou bloco não puderem ou não quiserem aceitar a designação.',
		"enumeravel": [{
			"tipo":"paragrafo",
			"lista":{
					"1":{
						"caput":'Ausentes o Presidente e o Vice-Presidente da comissão, o Presidente do Senado poderá designar, de ofício, substitutos eventuais a fim de possibilitar o funcionamento do órgão.'
					},
					"2":{
						"caput":'Cessará o exercício do substituto desde que o substituído compareça à reunião da respectiva comissão.'
					},
			}
		}]
	},
	"86":{
		"caput":'A renúncia a lugar em comissão far-se-á em comunicação escrita à Mesa.'
	},
	"87":{
		"caput":'Impossibilitado de comparecer a qualquer reunião de comissão a que pertença, o Senador deverá comunicar o fato ao Presidente a tempo de ser tomada a providência regimental para a sua substituição.'
	},
	"88":{
		"caput":'No início da legislatura, nos cinco dias úteis que se seguirem à designação de seus membros, e na terceira sessão legislativa, nos cinco dias úteis que se seguirem à indicação dos líderes, cada comissão reunir-se-á para instalar seus trabalhos e eleger, em escrutínio secreto, o seu Presidente e o Vice-Presidente.',
		"enumeravel": [{
			"tipo":"paragrafo",
			"lista":{
					"1":{
						"caput":'Em caso do não cumprimento do disposto neste artigo, ficarão investidos nos cargos os dois titulares mais idosos, até que se realize a eleição.'
					},
					"2":{
						"caput":'Ocorrendo empate, a eleição será repetida no dia seguinte; verificando-se novo empate, será considerado eleito o mais idoso.'
					},
					"3":{
						"caput":'Na ausência do Presidente e do Vice-Presidente, presidirá a comissão o mais idoso dos titulares.'
					},
					"4":{
						"caput":'Em caso de vaga dos cargos de Presidente ou de Vice-Presidente, far-se-á o preenchimento por meio de eleição realizada nos cinco dias úteis que se seguirem à vacância, salvo se faltarem sessenta dias ou menos para o término dos respectivos mandatos.'
					},
					"5":{
						"caput":'Aceitar função prevista no art. 39, II, importa em renúncia ao cargo de Presidente ou de Vice-Presidente de comissão.'
					},
					"6":{
						"caput":'Ao mandato de Presidente e de Vice-Presidente das comissões permanentes e de suas subcomissões aplica-se o disposto no art. 59.'
					},
			}
		}]
	},
	"89":{
		"caput":'Ao Presidente de comissão compete:',
		"enumeravel": [{
			"tipo":"inciso",
			"lista":{
					"1":{
						"caput":'ordenar e dirigir os trabalhos da comissão;'
					},
					"2":{
						"caput":'dar-lhe conhecimento de toda a matéria recebida;'
					},
					"3":{
						"caput":'designar, na comissão, relatores para as matérias;'
					},
					"4":{
						"caput":'designar, dentre os componentes da comissão, os membros das subcomissões e fixar a sua composição;'
					},
					"5":{
						"caput":'resolver as questões de ordem;'
					},
					"6":{
						"caput":'ser o elemento de comunicação da comissão com a Mesa, com as outras comissões e suas respectivas subcomissões e com os líderes;'
					},
					"7":{
						"caput":'convocar as suas reuniões extraordinárias, de ofício ou a requerimento de qualquer de seus membros, aprovado pela comissão;'
					},
					"8":{
						"caput":'promover a publicação das atas das reuniões no Diário do Senado Federal;'
					},
					"9":{
						"caput":'solicitar, em virtude de deliberação da comissão, os serviços de funcionários técnicos para estudo de determinado trabalho, sem prejuízo das suas atividades nas repartições a que pertençam;'
					},
					"10":{
						"caput":'convidar, para o mesmo fim e na forma do inciso IX, técnicos ou especialistas particulares e representantes de entidades ou associações científicas;'
					},
					"11":{
						"caput":'desempatar as votações quando ostensivas;'
					},
					"12":{
						"caput":'distribuir matérias às subcomissões;'
					},
					"13":{
						"caput":'assinar o expediente da comissão.'
					},
			}
		}]
	},
	"90":{
		"caput":'Às comissões compete:',
		"enumeravel": [{
			"tipo":"inciso",
			"lista":{
					"1":{
						"caput":'discutir e votar projeto de lei nos termos do art. 91 (Const., art. 58, § 2º, I);'
					},
					"2":{
						"caput":'realizar audiências públicas com entidades da sociedade civil (Const., art. 58, § 2º, II);'
					},
					"3":{
						"caput":'convocar Ministros de Estado ou quaisquer titulares de órgãos diretamente subordinados à Presidência da República para prestarem informações sobre assuntos inerentes a suas atribuições e ouvir os Ministros quando no exercício da faculdade prevista no art. 50, § 1º, da Constituição (Const., arts. 50 e 58, § 2º, III);'
					},
					"4":{
						"caput":'receber petições, reclamações, representações ou queixas de qualquer pessoa contra atos ou omissões das autoridades ou entidades públicas (Const., art. 58, § 2º, IV);'
					},
					"5":{
						"caput":'solicitar depoimento de qualquer autoridade ou cidadão (Const., art. 58, § 2º, V);'
					},
					"6":{
						"caput":'apreciar programas de obras, planos nacionais, regionais e setoriais de desenvolvimento e sobre eles emitir parecer (Const., art. 58, § 2º, VI);'
					},
					"7":{
						"caput":'propor a sustação dos atos normativos do Poder Executivo que exorbitem do poder regulamentar (Const., art. 49, V);'
					},
					"8":{
						"caput":'acompanhar junto ao Governo a elaboração da proposta orçamentária, bem como sua execução;'
					},
					"9":{
						"caput":'acompanhar, fiscalizar e controlar as políticas governamentais pertinentes às áreas de sua competência;'
					},
					"10":{
						"caput":'exercer a fiscalização e controle dos atos do Poder Executivo, incluídos os da administração indireta, e quanto às questões relativas à competência privativa do Senado (Const., arts. 49, X, e 52, V a IX);'
					},
					"11":{
						"caput":'estudar qualquer assunto compreendido nas atribuições do Senado, propondo as medidas legislativas cabíveis;'
					},
					"12":{
						"caput":'opinar sobre o mérito das proposições submetidas ao seu exame, emitindo o respectivo parecer;'
					},
					"13":{
						"caput":'realizar diligência.'
					},
			}
		}]
	},
	"91":{
		"caput":'Às comissões, no âmbito de suas atribuições, cabe, dispensada a competência do Plenário, nos termos do art. 58, § 2º, I, da Constituição, discutir e votar:',
		"enumeravel": [{
			"tipo":"inciso",
			"lista":{
					"1":{
						"caput":'projetos de lei ordinária de autoria de Senador, ressalvado projeto de código;'
					},
					"2":{
						"caput":'projetos de resolução que versem sobre a suspensão da execução, no todo ou em parte, de lei declarada inconstitucional por decisão definitiva do Supremo Tribunal Federal (Const., art. 52, X).'
					},
					"3":{
						"caput":'projetos de decreto legislativo de que trata o § 1º do art. 223 da Constituição Federal.'
					},
			}
		}]
	},
	"92":{
		"caput":'Aplicam-se à tramitação dos projetos e demais proposições submetidas à deliberação terminativa das comissões as disposições relativas a turnos, prazos, emendas e demais formalidades e ritos exigidos para as matérias submetidas à apreciação do Plenário do Senado.'
	},
	"93":{
		"caput":'A audiência pública será realizada pela comissão para:',
		"enumeravel": [{
			"tipo":"inciso",
			"lista":{
					"1":{
						"caput":'instruir matéria sob sua apreciação;'
					},
					"2":{
						"caput":'tratar de assunto de interesse público relevante.'
					},
			}
		}]
	},
	"94":{
		"caput":'Os depoimentos serão prestados por escrito e de forma conclusiva.',
		"enumeravel": [{
			"tipo":"paragrafo",
			"lista":{
					"1":{
						"caput":'Na hipótese de haver defensores e opositores, relativamente à matéria objeto de exame, a comissão procederá de forma que possibilite a audiência de todas as partes interessadas.'
					},
					"2":{
						"caput":'Os membros da comissão poderão, terminada a leitura, interpelar o orador exclusivamente sobre a exposição lida, por prazo nunca superior a três minutos.'
					},
					"3":{
						"caput":'O orador terá o mesmo prazo para responder a cada Senador, sendo lhe vedado interpelar os membros da comissão.'
					},
			}
		}]
	},
	"95":{
		"caput":'Da reunião de audiência pública será lavrada ata, arquivando-se, no âmbito da comissão, os pronunciamentos escritos e documentos que os acompanharem.',
		"enumeravel": [{
			"tipo":"paragrafo",
			"lista":{
					"1":{
						"caput":'Será admitido, a qualquer tempo, a requerimento de Senador, o traslado de peças.'
					},
			}
		}]
	},
	"96":{
		"caput":'A comissão receberá petições, reclamações, representações ou queixas de qualquer pessoa contra ato ou omissão de autoridade ou entidade pública sobre assunto de sua competência.',
		"enumeravel": [{
			"tipo":"paragrafo",
			"lista":{
					"1":{
						"caput":'Os expedientes referidos neste artigo deverão ser encaminhados por escrito, com identificação do autor e serão distribuídos a um relator que os apreciará e apresentará relatório com sugestões quanto às providências a serem tomadas pela comissão, pela Mesa ou pelo Ministério Público.'
					},
					"2":{
						"caput":'O relatório será discutido e votado na comissão, devendo concluir por projeto de resolução se contiver providência a ser tomada por outra instância que não a da própria comissão.'
					},
			}
		}]
	},
	"96-A":{
		"caput":'Os dirigentes máximos das agências reguladoras comparecerão ao Senado Federal, em periodicidade anual, para prestar contas sobre o exercício de suas atribuições e o desempenho da agência, bem como para apresentar avaliação das políticas públicas no âmbito de suas competências.',
		"enumeravel": [{
			"tipo":"paragrafo",
			"lista":{
					"1":{
						"caput":'O comparecimento de que trata o caput ocorrerá em reunião conjunta da comissão temática pertinente e das Comissões de Assuntos Econômicos e de Constituição, Justiça e Cidadania.'
					},
			}
		}]
	},
	"96-B":{
		"caput":'No desempenho da competência prevista no inciso IX do art. 90, as comissões permanentes selecionarão, na área de sua competência, políticas públicas desenvolvidas no âmbito do Poder Executivo, para serem avaliadas.',
		"enumeravel": [{
			"tipo":"paragrafo",
			"lista":{
					"1":{
						"caput":'Cada comissão permanente selecionará as políticas públicas até o último dia útil do mês de março de cada ano.'
					},
					"2":{
						"caput":'Para realizar a avaliação de que trata o caput, que se estenderá aos impactos das políticas públicas e às atividades meio de suporte para sua execução, poderão ser solicitadas informações e documentos a órgãos do Poder Executivo e ao Tribunal de Contas da União, bem como a entidades da sociedade civil, nos termos do art. 50 da Constituição Federal.'
					},
					"3":{
						"caput":'Ao final da sessão legislativa, a comissão apresentará relatório com as conclusões da avaliação realizada.'
					},
					"4":{
						"caput":'A Consultoria Legislativa e a Consultoria de Orçamentos, Fiscalização e Controle do Senado Federal elaborarão estudos e relatórios técnicos que subsidiarão os trabalhos da avaliação de que trata o caput.'
					},
					"5":{
						"caput":'O Instituto de Pesquisa DataSenado produzirá análises e relatórios estatísticos para subsidiar a avaliação de que trata o caput.'
					},
			}
		}]
	},
	"97":{
		"caput":'Às comissões permanentes compete estudar e emitir parecer sobre os assuntos submetidos ao seu exame.'
	},
	"98":{
		"caput":'À Comissão Diretora compete:',
		"enumeravel": [{
			"tipo":"inciso",
			"lista":{
					"1":{
						"caput":'exercer a administração interna do Senado nos termos das atribuições fixadas no seu Regulamento Administrativo;'
					},
					"2":{
						"caput":'regulamentar a polícia interna;'
					},
					"3":{
						"caput":'propor ao Senado projeto de resolução dispondo sobre sua organização, funcionamento, polícia, criação, transformação ou extinção de cargos, empregos e funções de seus serviços e a iniciativa de lei para a fixação da respectiva remuneração, observados os parâmetros estabelecidos na lei de diretrizes orçamentárias (Const., art. 52, XIII);'
					},
					"4":{
						"caput":'emitir, obrigatoriamente, parecer sobre as proposições que digam respeito ao serviço e ao pessoal da Secretaria do Senado e as que alterem este Regimento, salvo o disposto no art. 401, § 2º, II;'
					},
					"5":{
						"caput":'elaborar a redação final das proposições de iniciativa do Senado e das emendas e projetos da Câmara dos Deputados aprovados pelo Plenário, escoimando-os dos vícios de linguagem, das impropriedades de expressão, defeitos de técnica legislativa, cláusulas de justificação e palavras desnecessárias.'
					},
					"6":{
						"caput":'apreciar requerimento de tramitação em conjunto de proposição regulando a mesma matéria e o recurso de que trata o art. 48, § 3º, exceto se a proposição constar da Ordem do Dia ou for objeto de parecer aprovado em comissão (art. 258).'
					},
			}
		}]
	},
	"99":{
		"caput":'À Comissão de Assuntos Econômicos compete opinar sobre proposições pertinentes aos seguintes assuntos:',
		"enumeravel": [{
			"tipo":"inciso",
			"lista":{
					"1":{
						"caput":'aspecto econômico e financeiro de qualquer matéria que lhe seja submetida por despacho do Presidente, por deliberação do Plenário, ou por consulta de comissão, e, ainda, quando, em virtude desses aspectos, houver recurso de decisão terminativa de comissão para o Plenário;'
					},
					"2":{
						"caput":'(Revogado);'
					},
					"3":{
						"caput":'problemas econômicos do País, política de crédito, câmbio, seguro e transferência de valores, comércio exterior e interestadual, sistema monetário, bancário e de medidas, títulos e garantia dos metais, sistema de poupança, consórcio e sorteio e propaganda comercial;'
					},
					"4":{
						"caput":'tributos, tarifas, empréstimos compulsórios, finanças públicas, normas gerais sobre direito tributário, financeiro e econômico; orçamento, juntas comerciais, conflitos de competência em matéria tributária entre a União, os Estados, o Distrito Federal e os Municípios, dívida pública e fiscalização das instituições financeiras;'
					},
					"5":{
						"caput":'escolha dos Ministros do Tribunal de Contas da União (Const., arts. 49, XIII, e 52, III, b), e do presidente e diretores do Banco Central (Const., art. 52, III, d);'
					},
					"6":{
						"caput":'matérias a que se referem os arts. 389, 393 e 394;'
					},
					"7":{
						"caput":'outros assuntos correlatos.'
					},
			}
		}]
	},
	"99-A":{
		"caput":'À Comissão de Assuntos Econômicos compete, ainda, avaliar periodicamente a funcionalidade do Sistema Tributário Nacional, em sua estrutura e seus componentes, e o desempenho das administrações tributárias da União, dos Estados, do Distrito Federal e dos Municípios.'
	},
	"100":{
		"caput":'À Comissão de Assuntos Sociais compete opinar sobre proposições que digam respeito a:',
		"enumeravel": [{
			"tipo":"inciso",
			"lista":{
					"1":{
						"caput":'relações de trabalho, organização do sistema nacional de emprego e condição para o exercício de profissões, seguridade social, previdência social, população indígena e assistência social;'
					},
					"2":{
						"caput":'proteção e defesa da saúde, condições e requisitos para remoção de órgãos, tecidos e substâncias humanas para fins de transplante, pesquisa, tratamento e coleta de sangue humano e seus derivados, produção, controle e fiscalização de medicamentos, saneamento, inspeção e fiscalização de alimentos e competência do Sistema Único de Saúde;'
					},
					"3":{
						"caput":'(Revogado);'
					},
					"4":{
						"caput":'outros assuntos correlatos.'
					},
			}
		}]
	},
	"101":{
		"caput":'À Comissão de Constituição, Justiça e Cidadania compete:',
		"enumeravel": [{
			"tipo":"inciso",
			"lista":{
					"1":{
						"caput":'opinar sobre a constitucionalidade, juridicidade e regimentalidade das matérias que lhe forem submetidas por deliberação do Plenário, por despacho da Presidência, por consulta de qualquer comissão, ou quando em virtude desses aspectos houver recurso de decisão terminativa de comissão para o Plenário;'
					},
					"2":{
						"caput":'ressalvadas as atribuições das demais comissões, emitir parecer, quanto ao mérito, sobre as matérias de competência da União, especialmente as seguintes:',
						"enumeravel": [{
							"tipo":"letra",
							"lista":{
									"1":{
										"caput":'de Estado e Territórios, incorporação ou desmembramento de áreas a eles pertencentes;'
									},
									"2":{
										"caput":'de defesa, estado de sítio e intervenção federal (Const., art. 49, IV), requisições civis e anistia;'
									},
									"3":{
										"caput":'pública, corpos de bombeiros militares, polícia, inclusive marítima, aérea de fronteiras, rodoviária e ferroviária;'
									},
									"4":{
										"caput":'civil, comercial, penal, processual, eleitoral, aeronáutico, espacial, marítimo e penitenciário;'
									},
									"5":{
										"caput":'dos símbolos nacionais, nacionalidade, cidadania e naturalização, extradição e expulsão de estrangeiros, emigração e imigração;'
									},
									"6":{
										"caput":'do serviço público civil da União e servidores da administração direta e indireta do Poder Judiciário, do Ministério Público e dos Territórios;'
									},
									"7":{
										"caput":'gerais de licitação e contratação, em todas as modalidades, para as administrações públicas diretas, autárquicas e fundacionais da União, Estados, Distrito Federal e Municípios, obedecido o disposto no art. 37, XXI, da Constituição, e para as empresas públicas e sociedades de economia mista, nos termos do art. 173, § 1º, III, também da Constituição (Const., art. 22, XXVII);'
									},
									"8":{
										"caput":'de mandato de Senador (Const., art. 55), pedido de licença de incorporação de Senador às Forças Armadas (Const., art. 53, § 7º);'
									},
									"9":{
										"caput":'de Ministro do Supremo Tribunal Federal, dos Tribunais Superiores e de Governador de Território, escolha e destituição do Procurador-Geral da República (Const., art. 52, III, a, c e e);'
									},
									"10":{
										"caput":'temporária da sede do Governo Federal;'
									},
									"11":{
										"caput":'públicos, organização administrativa e judiciária do Ministério Público e Defensoria Pública da União e dos Territórios, organização judiciária do Ministério Público e da Defensoria Pública do Distrito Federal;'
									},
									"12":{
										"caput":'dos Estados e bens do domínio da União;'
									},
									"13":{
										"caput":'e inquilinato;'
									},
									"14":{
										"caput":'funcionamento e processo do juizado de pequenas causas, assistência jurídica e defensoria pública, custas dos serviços forenses;'
									},
									"15":{
										"caput":'a que se refere o art. 96, II, da Constituição Federal;'
									},
							}
						}]
					},
					"3":{
						"caput":'propor, por projeto de resolução, a suspensão, no todo ou em parte, de leis declaradas inconstitucionais pelo Supremo Tribunal Federal (Const., art. 52, X);'
					},
					"4":{
						"caput":'opinar, em cumprimento a despacho da Presidência, sobre as emendas apresentadas como de redação, nas condições previstas no parágrafo único do art. 234;'
					},
					"5":{
						"caput":'opinar sobre assunto de natureza jurídica ou constitucional que lhe seja submetido, em consulta, pelo Presidente, de ofício, ou por deliberação do Plenário, ou por outra comissão;'
					},
					"6":{
						"caput":'opinar sobre recursos interpostos às decisões da Presidência;'
					},
					"7":{
						"caput":'(Revogado).'
					},
			}
		}]
	},
	"101-A":{
		"caput":'O Ministro de Estado da Justiça comparecerá anualmente à Comissão de Constituição, Justiça e Cidadania do Senado Federal para prestar informações e esclarecimentos a respeito da atuação de sua Pasta, bem como para apresentar avaliação das políticas públicas no âmbito de suas competências.'
	},
	"102":{
		"caput":'À Comissão de Educação, Cultura e Esporte compete opinar sobre proposições que versem sobre:',
		"enumeravel": [{
			"tipo":"inciso",
			"lista":{
					"1":{
						"caput":'normas gerais sobre educação, cultura, ensino e desportos, instituições educativas e culturais, diretrizes e bases da educação nacional e salário-educação;'
					},
					"2":{
						"caput":'diversão e espetáculos públicos, criações artísticas, datas comemorativas e homenagens cívicas;'
					},
					"3":{
						"caput":'formação e aperfeiçoamento de recursos humanos;'
					},
					"4":{
						"caput":'(Revogado);'
					},
					"5":{
						"caput":'(Revogado);'
					},
					"6":{
						"caput":'outros assuntos correlatos.'
					},
			}
		}]
	},
	"102-A":{
		"caput":'À Comissão de Transparência, Governança, Fiscalização e Controle e Defesa do Consumidor, além da aplicação, no que couber, do disposto no art. 90 e sem prejuízo das atribuições das demais comissões, compete:',
		"enumeravel": [{
			"tipo":"inciso",
			"lista":{
					"1":{
						"caput":'exercer a fiscalização e o controle dos atos do Poder Executivo, incluídos os da administração indireta, podendo, para esse fim:',
						"enumeravel": [{
							"tipo":"letra",
							"lista":{
									"1":{
										"caput":'a eficácia, eficiência e economicidade dos projetos e programas de governo no plano nacional, no regional e no setorial de desenvolvimento, emitindo parecer conclusivo;'
									},
									"2":{
										"caput":'a compatibilidade da execução orçamentária com os planos e programas governamentais e destes com os objetivos aprovados em lei;'
									},
									"3":{
										"caput":'por escrito, informações à administração direta e indireta, bem como requisitar documentos públicos necessários à elucidação do ato objeto de fiscalização;'
									},
									"4":{
										"caput":'as contas dos administradores e demais responsáveis por dinheiros, bens e valores públicos da administração direta e indireta, incluídas as fundações e sociedades instituídas e mantidas pelo poder público federal, notadamente quando houver indícios de perda, extravio ou irregularidade de qualquer natureza de que resulte prejuízo ao Erário;'
									},
									"5":{
										"caput":'a efetivação de perícias, bem como solicitar ao Tribunal de Contas da União que realize inspeções ou auditorias de natureza contábil, financeira, orçamentária, operacional e patrimonial nas unidades administrativas da União e demais entidades referidas na alínea d;'
									},
									"6":{
										"caput":'as contas nacionais das empresas supranacionais de cujo capital social a União participe de forma direta ou indireta, bem assim a aplicação de quaisquer recursos repassados mediante convênio, acordo, ajuste ou outros instrumentos congêneres, a Estado, ao Distrito Federal ou a Município;'
									},
									"7":{
										"caput":'a interação do Senado Federal com os órgãos do Poder Executivo que, pela natureza de suas atividades, possam dispor ou gerar dados de que necessite para o exercício de fiscalização e controle;'
									},
									"8":{
										"caput":'a interação do Senado Federal com os órgãos do Poder Judiciário e do Ministério Público que, pela natureza de suas atividades, possam propiciar ou gerar dados de que necessite para o exercício de fiscalização e controle;'
									},
									"9":{
										"caput":'ao Plenário do Senado as providências cabíveis em relação aos resultados da avaliação, inclusive quanto ao resultado das diligências realizadas pelo Tribunal de Contas da União;'
									},
							}
						}]
					},
					"2":{
						"caput":'opinar sobre matérias pertinentes aos seguintes temas:',
						"enumeravel": [{
							"tipo":"letra",
							"lista":{
									"1":{
										"caput":'à corrupção;'
									},
									"2":{
										"caput":'e modernização das práticas gerenciais na administração pública federal direta e indireta;'
									},
									"3":{
										"caput":'eficaz, efetiva e eficiente de serviços públicos;'
									},
									"4":{
										"caput":'e prestação de contas e de informações à população, com foco na responsabilidade da gestão fiscal e dos gastos públicos, bem como nas necessidades dos cidadãos;'
									},
									"5":{
										"caput":'e incentivo, na administração pública, de novos meios de prestação de informações à sociedade, tais como redes, sítios e portais eletrônicos, e apoio a Estados e Municípios para a implantação desses meios;'
									},
							}
						}]
					},
					"3":{
						"caput":'opinar sobre assuntos pertinentes à defesa do consumidor, especialmente:',
						"enumeravel": [{
							"tipo":"letra",
							"lista":{
									"1":{
										"caput":'elaborar e propor normas e medidas voltadas à melhoria contínua das relações de mercado, em especial as que envolvem fornecedores e consumidores;'
									},
									"2":{
										"caput":'os instrumentos legislativos reguladores, contratuais e penais, referentes aos direitos dos consumidores e dos fornecedores, com ênfase em condições, limites e uso de informações, responsabilidade civil, respeito à privacidade, aos direitos autorais, às patentes e similares;'
									},
									"3":{
										"caput":'as políticas e as ações desenvolvidas pelo Poder Público relativas à defesa dos direitos do consumidor, à defesa da concorrência e à repressão da formação e da atuação ilícita de monopólios;'
									},
									"4":{
										"caput":'denúncias e denunciar práticas referentes a abuso do poder econômico, qualidade e apresentação de produtos, técnicas de propaganda e publicidade nocivas ou enganosas;'
									},
									"5":{
										"caput":'as relações entre custo e preço de produtos, bens e serviços, com vistas a estabelecer normas de repressão à usura, aos lucros excessivos, ao aumento indiscriminado de preços e à cartelização de segmentos do mercado;'
									},
									"6":{
										"caput":'as condições de concorrência com ênfase na defesa dos produtores e dos fornecedores nacionais, considerados os interesses dos consumidores e a soberania nacional;'
									},
									"7":{
										"caput":'e disponibilizar estudos, dados estatísticos e informações, no âmbito de suas competências.',
										"enumeravel": [{
											"tipo":"paragrafo",
											"lista":{
													"1":{
														"caput":'No exercício da competência de fiscalização e controle prevista no inciso I do caput, a Comissão de Transparência, Governança, Fiscalização e Controle e Defesa do Consumidor:',
														"enumeravel": [{
															"tipo":"inciso",
															"lista":{
																	"1":{
																		"caput":'remeterá cópia da documentação pertinente ao Ministério Público, a fim de que este promova a ação cabível, de natureza cível ou penal, se for constatada a existência de irregularidade;'
																	},
																	"2":{
																		"caput":'poderá atuar, mediante solicitação, em colaboração com as comissões permanentes e temporárias, incluídas as comissões parlamentares de inquérito, com vistas ao adequado exercício de suas atividades.'
																	},
															}
														}]
													},
											}
										}]
									},
							}
						}]
					},
			}
		}]
	},
	"102-B":{
		"caput":'A fiscalização e o controle dos atos do Poder Executivo, inclusive os da administração indireta, pela Comissão de Transparência, Governança, Fiscalização e Controle e Defesa do Consumidor obedecerão às seguintes regras:',
		"enumeravel": [{
			"tipo":"inciso",
			"lista":{
					"1":{
						"caput":'a proposta de fiscalização e controle poderá ser apresentada por qualquer membro ou Senador à Comissão, com específica indicação do ato e fundamentação da providência objetivada;'
					},
					"2":{
						"caput":'a proposta será relatada previamente, quanto à oportunidade e conveniência da medida e ao alcance jurídico, administrativo, político, econômico, social ou orçamentário do ato impugnado, definindo-se o plano de execução e a metodologia de avaliação;'
					},
					"3":{
						"caput":'aprovado o relatório prévio pela Comissão, o relator poderá solicitar os recursos e o assessoramento necessários ao bom desempenho da Comissão, incumbindo à Mesa e à Administração da Casa o atendimento preferencial das providências requeridas. Rejeitado o relatório, a matéria será encaminhada ao Arquivo;'
					},
					"4":{
						"caput":'o relatório final da fiscalização e controle, em termos de comprovação da legalidade do ato, avaliação política, administrativa, social e econômica de sua edição, e quanto à eficácia dos'
					},
					"5":{
						"caput":'a gestão orçamentária, financeira e patrimonial, obedecerá, no que concerne à tramitação, as normas do art. 102-C.'
					},
			}
		}]
	},
	"102-C":{
		"caput":'Ao termo dos trabalhos, a Comissão apresentará relatório circunstanciado, com suas conclusões, que será publicado no Diário do Senado Federal e encaminhado.',
		"enumeravel": [{
			"tipo":"inciso",
			"lista":{
					"1":{
						"caput":'à Mesa, para as providências de alçada desta, ou ao Plenário, oferecendo, conforme o caso, projeto de lei, de decreto legislativo, de resolução ou indicação;'
					},
					"2":{
						"caput":'ao Ministério Público ou à Advocacia-Geral da União, com cópia da documentação, para que promova a responsabilidade civil ou criminal por infrações apuradas e adote outras medidas decorrentes de suas funções institucionais;'
					},
					"3":{
						"caput":'ao Poder Executivo, para adotar as providências saneadoras de caráter disciplinar e administrativo decorrentes do disposto no art. 37, §§ 2º a 6º, da Constituição Federal, e demais disposições constitucionais e legais aplicáveis;'
					},
					"4":{
						"caput":'à comissão permanente que tenha maior pertinência com a matéria, a qual incumbirá o atendimento do prescrito no inciso III;'
					},
					"5":{
						"caput":'à Comissão Mista de Planos, Orçamentos Públicos e Fiscalização e ao Tribunal de Contas da União, para as providências previstas no art. 71 da Constituição Federal.'
					},
			}
		}]
	},
	"102-D":{
		"caput":'Aplicam-se à Comissão de Transparência, Governança, Fiscalização e Controle e Defesa do Consumidor as normas regimentais pertinentes às demais comissões permanentes, no que não conflitarem com os termos das disposições constantes dos arts. 102-A a 102-C.',
		"enumeravel": [{
			"tipo":"paragrafo",
			"lista":{
					"1":{
						"caput":'Ocorrendo a hipótese de exercício concorrente de competência fiscalizadora por duas ou mais comissões sobre os mesmos fatos, os trabalhos se desdobrarão em reuniões conjuntas, por iniciativa do Presidente de um dos órgãos ou de um ou mais de seus membros.'
					},
					"2":{
						"caput":'A Comissão de Transparência, Governança, Fiscalização e Controle e Defesa do Consumidor poderá, se houver motivo suficiente, comunicar fatos investigados à comissão correspondente da Câmara dos Deputados, para que esta adote a providência que considerar cabível.'
					},
			}
		}]
	},
	"102-E":{
		"caput":'À Comissão de Direitos Humanos e Legislação Participativa compete opinar sobre:',
		"enumeravel": [{
			"tipo":"inciso",
			"lista":{
					"1":{
						"caput":'sugestões legislativas apresentadas por associações e órgãos de classe, sindicatos e entidades organizadas da sociedade civil, exceto partidos políticos com representação política no Congresso Nacional;'
					},
					"2":{
						"caput":'pareceres técnicos, exposições e propostas oriundas de entidades científicas e culturais e de qualquer das entidades mencionadas no inciso I;'
					},
					"3":{
						"caput":'garantia e promoção dos direitos humanos;'
					},
					"4":{
						"caput":'direitos da mulher;'
					},
					"5":{
						"caput":'proteção à família;'
					},
					"6":{
						"caput":'proteção e integração social das pessoas portadoras de deficiências e de proteção à infância, à juventude e aos idosos;'
					},
					"7":{
						"caput":'fiscalização, acompanhamento, avaliação e controle das políticas governamentais relativas aos direitos humanos, aos direitos da mulher, aos direitos das minorias sociais ou étnicas, aos direitos dos estrangeiros, à proteção e integração das pessoas portadoras de deficiência e à proteção à infância, à juventude e aos idosos.'
					},
			}
		}]
	},
	"102-F":{
		"caput":'À Comissão de Meio Ambiente compete opinar sobre assuntos pertinentes à defesa do meio ambiente, especialmente:',
		"enumeravel": [{
			"tipo":"inciso",
			"lista":{
					"1":{
						"caput":'proteção do meio ambiente, controle da poluição, conservação da natureza e defesa do solo, dos recursos naturais e genéticos, das florestas, da caça, da pesca, da fauna, da flora e dos recursos hídricos;'
					},
					"2":{
						"caput":'política e sistema nacional de meio ambiente;'
					},
					"3":{
						"caput":'preservação, conservação, exploração e manejo de florestas e da biodiversidade;'
					},
					"4":{
						"caput":'conservação e gerenciamento do uso do solo e dos recursos hídricos, no tocante ao meio ambiente e ao desenvolvimento sustentável;'
					},
					"5":{
						"caput":'fiscalização dos alimentos e dos produtos e insumos agrícolas e pecuários, no tocante ao meio ambiente e ao desenvolvimento sustentável;'
					},
					"6":{
						"caput":'direito ambiental;'
					},
					"7":{
						"caput":'agências reguladoras na área de meio ambiente, inclusive a Agência Nacional de Águas (ANA);'
					},
					"8":{
						"caput":'outros assuntos correlatos.'
					},
			}
		}]
	},
	"103":{
		"caput":'À Comissão de Relações Exteriores e Defesa Nacional compete emitir parecer sobre:',
		"enumeravel": [{
			"tipo":"inciso",
			"lista":{
					"1":{
						"caput":'proposições referentes aos atos e relações internacionais (Const., art. 49, I) e ao Ministério das Relações Exteriores;'
					},
					"2":{
						"caput":'comércio exterior;'
					},
					"3":{
						"caput":'indicação de nome para chefe de missão diplomática de caráter permanente junto a governos estrangeiros e das organizações internacionais de que o Brasil faça parte (Const., art. 52, IV);'
					},
					"4":{
						"caput":'(Revogado);'
					},
					"5":{
						"caput":'Forças Armadas de terra, mar e ar, requisições militares, passagem de forças estrangeiras e sua permanência no território nacional, questões de fronteiras e limites do território nacional, espaço aéreo e marítimo, declaração de guerra e celebração de paz (Const., art. 49, II);'
					},
					"6":{
						"caput":'assuntos referentes à Organização das Nações Unidas e entidades internacionais de qualquer natureza;'
					},
					"7":{
						"caput":'autorização para o Presidente ou o Vice-Presidente da República se ausentarem do território nacional (Const., art. 49, III);'
					},
					"8":{
						"caput":'outros assuntos correlatos.'
					},
			}
		}]
	},
	"104":{
		"caput":'À Comissão de Serviços de Infraestrutura compete opinar sobre matérias pertinentes a:',
		"enumeravel": [{
			"tipo":"inciso",
			"lista":{
					"1":{
						"caput":'transportes de terra, mar e ar, obras públicas em geral, minas, recursos geológicos, serviços de telecomunicações, parcerias público-privadas e agências reguladoras pertinentes;'
					},
					"2":{
						"caput":'outros assuntos correlatos.'
					},
			}
		}]
	},
	"104-A":{
		"caput":'À Comissão de Desenvolvimento Regional e Turismo compete opinar sobre matérias pertinentes a:',
		"enumeravel": [{
			"tipo":"inciso",
			"lista":{
					"1":{
						"caput":'proposições que tratem de assuntos referentes às desigualdades regionais e às políticas de desenvolvimento regional, dos Estados e dos Municípios;'
					},
					"2":{
						"caput":'planos regionais de desenvolvimento econômico e social;'
					},
					"3":{
						"caput":'programas, projetos, investimentos e incentivos voltados para o desenvolvimento regional;'
					},
					"4":{
						"caput":'integração regional;'
					},
					"5":{
						"caput":'agências e organismos de desenvolvimento regional;'
					},
					"6":{
						"caput":'proposições que tratem de assuntos referentes ao turismo;'
					},
					"7":{
						"caput":'políticas relativas ao turismo;'
					},
					"8":{
						"caput":'outros assuntos correlatos.'
					},
			}
		}]
	},
	"104-B":{
		"caput":'À Comissão de Agricultura e Reforma Agrária compete opinar sobre proposições pertinentes aos seguintes temas:',
		"enumeravel": [{
			"tipo":"inciso",
			"lista":{
					"1":{
						"caput":'direito agrário;'
					},
					"2":{
						"caput":'planejamento, acompanhamento e execução da política agrícola e fundiária;'
					},
					"3":{
						"caput":'agricultura, pecuária e abastecimento;'
					},
					"4":{
						"caput":'agricultura familiar e segurança alimentar;'
					},
					"5":{
						"caput":'silvicultura, aquicultura e pesca;'
					},
					"6":{
						"caput":'comercialização e fiscalização de produtos e insumos, inspeção e fiscalização de alimentos, vigilância e defesa sanitária animal e vegetal;'
					},
					"7":{
						"caput":'irrigação e drenagem;'
					},
					"8":{
						"caput":'uso e conservação do solo na agricultura;'
					},
					"9":{
						"caput":'utilização e conservação, na agricultura, dos recursos hídricos e genéticos;'
					},
					"10":{
						"caput":'política de investimentos e financiamentos agropecuários, seguro rural e endividamento rural;'
					},
					"11":{
						"caput":'tributação da atividade rural;'
					},
					"12":{
						"caput":'alienação ou concessão de terras públicas com área superior a dois mil e quinhentos hectares, aquisição ou arrendamento de propriedade rural por pessoa física ou jurídica estrangeira, definição da pequena e da média propriedade rural;'
					},
					"13":{
						"caput":'uso ou posse temporária da terra e regularização dominial de terras rurais e de sua ocupação;'
					},
					"14":{
						"caput":'colonização e reforma agrária;'
					},
					"15":{
						"caput":'cooperativismo e associativismo rurais;'
					},
					"16":{
						"caput":'emprego, previdência e renda rurais;'
					},
					"17":{
						"caput":'políticas de apoio às pequenas e médias propriedades rurais;'
					},
					"18":{
						"caput":'política de desenvolvimento tecnológico da agropecuária, mediante estímulos fiscais, financeiros e creditícios à pesquisa e experimentação agrícola, pesquisa, plantio e comercialização de organismos geneticamente modificados;'
					},
					"19":{
						"caput":'extensão rural;'
					},
					"20":{
						"caput":'organização do ensino rural;'
					},
					"21":{
						"caput":'outros assuntos correlatos.'
					},
			}
		}]
	},
	"104-C":{
		"caput":'À Comissão de Ciência, Tecnologia, Inovação, Comunicação e Informática compete opinar sobre proposições pertinentes aos seguintes temas:',
		"enumeravel": [{
			"tipo":"inciso",
			"lista":{
					"1":{
						"caput":'desenvolvimento científico, tecnológico e inovação tecnológica;'
					},
					"2":{
						"caput":'política nacional de ciência, tecnologia, inovação, comunicação e informática;'
					},
					"3":{
						"caput":'organização institucional do setor;'
					},
					"4":{
						"caput":'acordos de cooperação e inovação com outros países e organismos internacionais na área;'
					},
					"5":{
						"caput":'propriedade intelectual;'
					},
					"6":{
						"caput":'criações científicas e tecnológicas, informática, atividades nucleares de qualquer natureza, transporte e utilização de materiais radioativos, apoio e estímulo à pesquisa e criação de tecnologia;'
					},
					"7":{
						"caput":'comunicação, imprensa, radiodifusão, televisão, outorga e renovação de concessão, permissão e autorização para serviços de radiodifusão sonora e de sons e imagens;'
					},
					"8":{
						"caput":'regulamentação, controle e questões éticas referentes a pesquisa e desenvolvimento científico e tecnológico, inovação tecnológica, comunicação e informática;'
					},
					"9":{
						"caput":'outros assuntos correlatos.'
					},
			}
		}]
	},
	"104-D":{
		"caput":'À Comissão Senado do Futuro compete promover discussões sobre grandes temas e o futuro do País, bem como aprimorar a atuação do Senado nessas questões.'
	},
	"104-E":{
		"caput":'(Revogado).'
	},
	"105":{
		"caput":'Às comissões temporárias compete o desempenho das atribuições que lhes forem expressamente deferidas.'
	},
	"106":{
		"caput":'As comissões reunir-se-ão nas dependências do edifício do Senado Federal.'
	},
	"107":{
		"caput":'As reuniões das comissões permanentes realizar-se-ão:',
		"enumeravel": [{
			"tipo":"inciso",
			"lista":{
					"1":{
						"caput":'se ordinárias, semanalmente, durante a sessão legislativa ordinária, nos seguintes dias e horários:',
						"enumeravel": [{
							"tipo":"letra",
							"lista":{
									"1":{
										"caput":'de Assuntos Econômicos: às terças-feiras, dez horas;'
									},
									"2":{
										"caput":'de Serviços de Infraestrutura: às terças-feiras, quatorze horas;'
									},
									"3":{
										"caput":'de Constituição, Justiça e Cidadania: às quartas-feiras, dez horas;'
									},
									"4":{
										"caput":'de Assuntos Sociais: às quintas-feiras, onze horas e trinta minutos;'
									},
									"5":{
										"caput":'de Relações Exteriores e Defesa Nacional: às quintas-feiras, dez horas;'
									},
									"6":{
										"caput":'de Educação, Cultura e Esporte: às terças-feiras, onze horas;'
									},
									"7":{
										"caput":'de Transparência, Governança, Fiscalização e Controle e Defesa do Consumidor: às terças-feiras, onze horas e trinta minutos;'
									},
									"8":{
										"caput":'de Direitos Humanos e Legislação Participativa: às terças-feiras, doze horas;'
									},
									"9":{
										"caput":'de Desenvolvimento Regional e Turismo: às quartas-feiras, quatorze horas;'
									},
									"10":{
										"caput":'de Agricultura e Reforma Agrária: às quintas-feiras, doze horas;'
									},
									"11":{
										"caput":'de Ciência, Tecnologia, Inovação, Comunicação e Informática: às quartas-feiras, dezoito horas;'
									},
									"12":{
										"caput":'de Meio Ambiente: às quartas-feiras, às onze horas e trinta minutos.'
									},
							}
						}]
					},
					"2":{
						"caput":'se extraordinárias, mediante convocação especial para dia, horário e fim indicados, observando-se, no que for aplicável, o disposto neste Regimento sobre a convocação de sessões extraordinárias do Senado;'
					},
					"3":{
						"caput":'as comissões parlamentares de inquérito reunir-se-ão em horário diverso do estabelecido para o funcionamento das Comissões Permanentes.'
					},
			}
		}]
	},
	"108":{
		"caput":'As reuniões das comissões serão iniciadas com a presença de, no mínimo, um quinto de sua composição, salvo o disposto no § 3º do art. 93.',
		"enumeravel": [{
			"tipo":"paragrafo",
			"lista":{
					"1":{
						"caput":'A pauta dos trabalhos das comissões, salvo em caso de urgência, será disponibilizada em meio eletrônico no portal do Senado Federal, com antecedência mínima de 2 (dois) dias úteis.'
					},
					"2":{
						"caput":'É facultada a utilização de sistema biométrico de identificação no registro de presença dos membros da comissão.'
					},
					"3":{
						"caput":'A suspensão de reunião de comissão somente será permitida quando sua continuação ocorrer em data e hora previamente estabelecidas.'
					},
			}
		}]
	},
	"109":{
		"caput":'A comissão deliberará por maioria de votos, presente a maioria de seus membros, sendo as deliberações terminativas tomadas pelo processo nominal.'
	},
	"110":{
		"caput":'As reuniões serão públicas, salvo os casos expressos neste Regimento ou quando o deliberar a comissão.'
	},
	"111":{
		"caput":'Os trabalhos das comissões iniciar-se-ão, salvo deliberação em contrário, pela leitura e discussão da ata da reunião anterior que, se aprovada, será assinada pelo Presidente.'
	},
	"112":{
		"caput":'É facultado a qualquer Senador assistir às reuniões das comissões, discutir o assunto em debate, pelo prazo por elas prefixado, e enviar-lhes, por escrito, informações ou esclarecimentos.',
		"enumeravel": [{
			"tipo":"paragrafo",
			"lista":{
					"1":{
						"caput":'As informações ou esclarecimentos apresentados serão impressos com os pareceres, se o autor o requerer e a comissão o deferir.'
					},
			}
		}]
	},
	"113":{
		"caput":'O estudo de qualquer matéria poderá ser feito em reunião conjunta de duas ou mais comissões, por iniciativa de qualquer delas, aceita pelas demais, sob a direção do Presidente mais idoso, ou ainda, nos termos do art. 49, II.',
		"enumeravel": [{
			"tipo":"paragrafo",
			"lista":{
					"1":{
						"caput":'Nas reuniões conjuntas observar-se-ão as seguintes normas:',
						"enumeravel": [{
							"tipo":"inciso",
							"lista":{
									"1":{
										"caput":'cada comissão deverá estar presente pela maioria absoluta de seus membros;'
									},
									"2":{
										"caput":'o estudo da matéria será em conjunto, mas a votação far-se-á separadamente, na ordem constante do despacho da Mesa;'
									},
									"3":{
										"caput":'cada comissão poderá ter o seu relator se não preferir relator único;'
									},
									"4":{
										"caput":'o parecer das comissões poderá ser em conjunto, desde que consigne a manifestação de cada uma delas, ou em separado, se essa for a orientação preferida, mencionando, em qualquer caso, os votos vencidos, os em separado, os pelas conclusões e os com restrições.'
									},
							}
						}]
					},
			}
		}]
	},
	"114":{
		"caput":'As comissões permanentes e temporárias serão secretariadas por servidores da Secretaria do Senado e terão assessoramento próprio, constituído de até três assessores, designados pelo respectivo Presidente, ouvida a Consultoria Legislativa ou a de Orçamentos, conforme o caso.',
		"enumeravel": [{
			"tipo":"paragrafo",
			"lista":{
					"1":{
						"caput":'Ao secretário da comissão compete:',
						"enumeravel": [{
							"tipo":"inciso",
							"lista":{
									"1":{
										"caput":'redigir as atas;'
									},
									"2":{
										"caput":'organizar a pauta do dia e do protocolo dos trabalhos com o seu andamento;'
									},
									"3":{
										"caput":'manter atualizados os registros necessários ao controle de designação de relatores.'
									},
							}
						}]
					},
			}
		}]
	},
	"115":{
		"caput":'Das reuniões das comissões lavrar-se-ão atas em folhas avulsas rubricadas pelo Presidente.',
		"enumeravel": [{
			"tipo":"paragrafo",
			"lista":{
					"1":{
						"caput":'Quando, pela importância do assunto em estudo, convier o registro taquigráfico dos debates, o Presidente solicitará ao Primeiro Secretário as providências necessárias.'
					},
					"2":{
						"caput":'Das atas constarão:',
						"enumeravel": [{
							"tipo":"inciso",
							"lista":{
									"1":{
										"caput":'o dia, a hora e o local da reunião;'
									},
									"2":{
										"caput":'os nomes dos membros presentes e os dos ausentes com causa justificada ou sem ela;'
									},
									"3":{
										"caput":'a distribuição das matérias por assuntos e relatores;'
									},
									"4":{
										"caput":'as conclusões dos pareceres lidos;'
									},
									"5":{
										"caput":'referências sucintas aos debates;'
									},
									"6":{
										"caput":'os pedidos de vista, adiamento, diligências e outras providências, salvo quando não se considere conveniente a divulgação da matéria.'
									},
							}
						}]
					},
					"3":{
						"caput":'As atas serão publicadas no Diário do Senado Federal, dentro dos dois dias úteis que se seguirem à reunião, podendo, em casos excepcionais, a juízo do Presidente da comissão, ser essa publicação adiada por igual prazo.'
					},
			}
		}]
	},
	"116":{
		"caput":'Serão secretas as reuniões para deliberar sobre:',
		"enumeravel": [{
			"tipo":"inciso",
			"lista":{
					"1":{
						"caput":'declaração de guerra ou celebração de paz (Const., art. 49, II);'
					},
					"2":{
						"caput":'trânsito ou permanência temporária de forças estrangeiras no território nacional (Const., art. 49, II);'
					},
					"3":{
						"caput":'escolha de chefe de missão diplomática de caráter permanente (Const., art. 52, IV);'
					},
			}
		}]
	},
	"117":{
		"caput":'Nas reuniões secretas, além dos membros da comissão, só será admitida a presença de Senadores e das pessoas a serem ouvidas sobre a matéria em debate.',
		"enumeravel": [{
			"tipo":"paragrafo",
			"lista":{
					"1":{
						"caput":'Os Deputados Federais poderão assistir às reuniões secretas que não tratarem de matéria da competência privativa do Senado Federal.'
					},
			}
		}]
	},
	"118":{
		"caput":'O exame das comissões sobre as proposições, excetuadas as emendas e os casos em que este Regimento determine em contrário, obedecerá aos seguintes prazos:',
		"enumeravel": [{
			"tipo":"inciso",
			"lista":{
					"1":{
						"caput":'vinte dias úteis para a Comissão de Constituição, Justiça e Cidadania;'
					},
					"2":{
						"caput":'quinze dias úteis para as demais comissões.'
					},
			}
		}]
	},
	"119":{
		"caput":'Quando a matéria for despachada a mais de uma comissão e a primeira esgotar o prazo sem sobre ela se manifestar, poderá ser dispensado o seu parecer, por deliberação do Plenário, a requerimento de qualquer Senador.',
		"enumeravel": [{
			"tipo":"paragrafo",
			"lista":{
					"1":{
						"caput":'Se uma das comissões considerar indispensável, antes de proferir o seu parecer, o exame da que houver excedido o prazo, proposta neste sentido será submetida à deliberação do Plenário.'
					},
			}
		}]
	},
	"120":{
		"caput":'O relator tem, para apresentar o relatório, a metade do prazo atribuído à comissão.'
	},
	"121":{
		"caput":'O Presidente da comissão, ex officio ou a requerimento de Senador, poderá mandar incluir, na pauta dos trabalhos, matéria que, distribuída, não tenha sido relatada no prazo regimental, devendo dar conhecimento da decisão ao relator.'
	},
	"122":{
		"caput":'Perante as comissões, poderão apresentar emendas:',
		"enumeravel": [{
			"tipo":"inciso",
			"lista":{
					"1":{
						"caput":'qualquer de seus membros, em todos os casos;'
					},
					"2":{
						"caput":'qualquer Senador:',
						"enumeravel": [{
							"tipo":"letra",
							"lista":{
									"1":{
										"caput":'projetos de código;'
									},
									"2":{
										"caput":'projetos de iniciativa do Presidente da República com tramitação urgente (Const., art. 64, § 1º);'
									},
									"3":{
										"caput":'projetos referidos no art. 91.',
										"enumeravel": [{
											"tipo":"paragrafo",
											"lista":{
													"1":{
														"caput":'No caso do inciso II, o prazo para a apresentação de emenda contar-se-á a partir da publicação da matéria no Diário do Senado Federal, sendo de vinte dias úteis para os projetos de Código e de cinco dias úteis para os demais projetos.'
													},
													"2":{
														"caput":'No avulso eletrônico da Ordem do Dia consignar-se-á a existência de projetos em fase de recebimento de emendas, com a indicação da comissão que deverá recebê-las, do prazo e do número de dias transcorridos.'
													},
											}
										}]
									},
							}
						}]
					},
			}
		}]
	},
	"123":{
		"caput":'Considera-se emenda de comissão a proposta por qualquer de seus membros e por ela adotada.'
	},
	"124":{
		"caput":'Terá o seguinte tratamento a emenda apresentada na forma do art. 122:',
		"enumeravel": [{
			"tipo":"inciso",
			"lista":{
					"1":{
						"caput":'no caso do inciso I, será considerada inexistente quando não adotada pela comissão;'
					},
					"2":{
						"caput":'no caso do inciso II, alínea a, será encaminhada à deliberação do Plenário do Senado, com parecer favorável ou contrário;'
					},
					"3":{
						"caput":'no caso do inciso II, alínea b, será final o pronunciamento, salvo recurso interposto por um décimo dos membros do Senado no sentido de ser a emenda submetida ao Plenário, sem discussão;'
					},
					"4":{
						"caput":'no caso do inciso II, alínea c, será final o pronunciamento da comissão, salvo recurso interposto para discussão e votação da proposição principal.'
					},
			}
		}]
	},
	"125":{
		"caput":'Quando a proposição estiver sujeita, na forma deste Regimento, a parecer em Plenário, o relator, ao proferi-lo, poderá oferecer emenda ou subemenda.'
	},
	"126":{
		"caput":'A designação de relator, independente da matéria e de reunião da comissão, obedecerá à proporção das representações partidárias ou dos blocos parlamentares nela existentes, será alternada entre os seus membros e far-se-á em dois dias úteis após o recebimento do projeto, salvo nos casos em que este Regimento fixe outro prazo.',
		"enumeravel": [{
			"tipo":"paragrafo",
			"lista":{
					"1":{
						"caput":'O relator do projeto será o das emendas a este oferecidas em plenário, salvo ausência ou recusa.'
					},
					"2":{
						"caput":'Quando se tratar de emenda oferecida pelo relator, em plenário, o Presidente da comissão designará outro Senador para relatá-la, sendo essa circunstância consignada no parecer.'
					},
			}
		}]
	},
	"127":{
		"caput":'Não poderá funcionar como relator o autor da proposição.'
	},
	"128":{
		"caput":'Vencido o relator, o Presidente da comissão designará um dos membros, em maioria, para suceder-lhe, exceto se o fato ocorrer apenas em relação a parte da proposição ou emenda, quando permanecerá o mesmo relator, consignando-se no parecer, pormenorizadamente, o objeto do dissenso na deliberação da comissão.'
	},
	"129":{
		"caput":'O Presidente poderá, excepcionalmente, funcionar como relator.'
	},
	"130":{
		"caput":'As matérias que, em cada reunião, devam ser objeto de estudo, constarão de pauta previamente organizada, sendo relatadas na ordem em que nela figurarem, salvo preferência concedida para qualquer delas.'
	},
	"131":{
		"caput":'O relatório deverá ser oferecido por escrito.'
	},
	"132":{
		"caput":'Lido o relatório, desde que a maioria se manifeste de acordo com o relator, passará ele a constituir parecer.',
		"enumeravel": [{
			"tipo":"paragrafo",
			"lista":{
					"1":{
						"caput":'O pedido de vista do processo somente poderá ser aceito por uma única vez e pelo prazo máximo e improrrogável de cinco dias, devendo ser formulado na oportunidade em que for conhecido o voto proferido pelo relator, obedecido o disposto no § 4º.'
					},
					"2":{
						"caput":'Estando a matéria em regime de urgência, a vista somente poderá ser concedida:',
						"enumeravel": [{
							"tipo":"inciso",
							"lista":{
									"1":{
										"caput":'por meia hora, no caso do art. 336, I;'
									},
									"2":{
										"caput":'por vinte e quatro horas, nos casos do art. 336, II e III.'
									},
							}
						}]
					},
					"3":{
						"caput":'Quando se tratar de proposição com prazo determinado, a vista, desde que não ultrapasse os últimos dez dias de sua tramitação, poderá ser concedida por vinte e quatro horas.'
					},
					"4":{
						"caput":'Os prazos a que se referem os §§ 1º a 3º correrão em conjunto se a vista for requerida por mais de um Senador.'
					},
					"5":{
						"caput":'Verificando-se a hipótese prevista no art. 128, o parecer vencedor deverá ser apresentado na reunião ordinária imediata, salvo deliberação em contrário.'
					},
					"6":{
						"caput":'Os membros da comissão que não concordarem com o relatório poderão:',
						"enumeravel": [{
							"tipo":"inciso",
							"lista":{
									"1":{
										"caput":'dar voto em separado;'
									},
									"2":{
										"caput":'assiná-lo, uma vez constituído parecer, com restrições, pelas conclusões, ou declarando-se vencidos.'
									},
							}
						}]
					},
					"7":{
						"caput":'Contam-se como favoráveis os votos pelas conclusões ou com restrições.'
					},
					"8":{
						"caput":'(Revogado).'
					},
					"9":{
						"caput":'Em caso de empate na votação, o Presidente a desempatará.'
					},
			}
		}]
	},
	"133":{
		"caput":'Todo parecer deve ser conclusivo em relação à matéria a que se referir, podendo a conclusão ser:',
		"enumeravel": [{
			"tipo":"inciso",
			"lista":{
					"1":{
						"caput":'pela aprovação, total ou parcial;'
					},
					"2":{
						"caput":'pela rejeição;'
					},
					"3":{
						"caput":'pelo arquivamento;'
					},
					"4":{
						"caput":'pelo destaque, para proposição em separado, de parte da proposição principal, quando originária do Senado, ou de emenda;'
					},
					"5":{
						"caput":'pela apresentação de:',
						"enumeravel": [{
							"tipo":"letra",
							"lista":{
									"1":{
										"caput":''
									},
									"2":{
										"caput":''
									},
									"3":{
										"caput":'ou subemenda;'
									},
									"4":{
										"caput":'a seguir em relação à matéria.'
									},
									"5":{
										"caput":'nos termos do art. 227-A, inciso II; (Incluído pela Resolução n.º 14, de 2019)',
										"enumeravel": [{
											"tipo":"paragrafo",
											"lista":{
													"1":{
														"caput":'Considera-se pela rejeição o parecer pelo arquivamento quando se referir a proposição legislativa.'
													},
													"2":{
														"caput":'Nas hipóteses do inciso V, alíneas a, b e c, o parecer é considerado justificação da proposição apresentada.'
													},
													"3":{
														"caput":'Nas hipóteses do inciso V, alíneas "a", "b", "c" e "e", o parecer é considerado justificação da proposição apresentada. (Redação dada pela Resolução n.º 14, de 2019)'
													},
													"4":{
														"caput":'Sendo favorável o parecer apresentado sobre indicação, ofício, memorial ou outro documento contendo sugestão ou solicitação que dependa de proposição legislativa, esta deverá ser formalizada em conclusão.'
													},
													"5":{
														"caput":'Quando se tratar de parecer sobre matéria que deva ser apreciada em sessão secreta (art. 197), proceder-se-á de acordo com o disposto no art. 116, § 1º.'
													},
													"6":{
														"caput":'Quando o parecer se referir a emendas ou subemendas, deverá oferecer conclusão relativamente a cada uma.'
													},
													"7":{
														"caput":'A comissão, ao se manifestar sobre emendas, poderá reunir a matéria da proposição principal e das emendas com parecer favorável num único texto, com os acréscimos e alterações que visem ao seu aperfeiçoamento.'
													},
													"8":{
														"caput":'As emendas com parecer contrário das comissões serão submetidas ao Plenário, desde que a decisão do órgão técnico não alcance unanimidade de votos, devendo esta circunstância constar expressamente do parecer.'
													},
													"9":{
														"caput":'Toda vez que a comissão concluir o seu parecer com sugestão ou proposta que envolva matéria de requerimento ou emenda, formalizará a proposição correspondente.'
													},
											}
										}]
									},
							}
						}]
					},
			}
		}]
	},
	"134":{
		"caput":'O parecer conterá ementa indicativa da matéria a que se referir.'
	},
	"135":{
		"caput":'As comissões poderão, em seus pareceres, propor seja o assunto apreciado pelo Senado em sessão secreta, caso em que o respectivo processo será entregue ao Presidente da Mesa com o devido sigilo.'
	},
	"136":{
		"caput":'Uma vez assinados pelo Presidente e pelo relator e instruídos com a lista de presença dos membros da comissão, os pareceres serão enviados à Mesa, juntamente com as emendas relatadas, declarações de votos e votos em separado.'
	},
	"137":{
		"caput":'Os pareceres serão lidos em plenário, publicados no Diário do Senado Federal e em avulso eletrônico, após manifestação das comissões a que tenha sido despachada a matéria.',
		"enumeravel": [{
			"tipo":"paragrafo",
			"lista":{
					"1":{
						"caput":'As comissões poderão promover, para estudos, a publicação de seus pareceres ao pé da ata da reunião ou em avulsos eletrônicos especiais.'
					},
			}
		}]
	},
	"138":{
		"caput":'Se o parecer concluir por pedido de providências:',
		"enumeravel": [{
			"tipo":"inciso",
			"lista":{
					"1":{
						"caput":'será despachado pelo Presidente da comissão quando solicitar audiência de outra comissão, reunião conjunta com outra comissão ou diligência interna de qualquer natureza;'
					},
					"2":{
						"caput":'será encaminhado à Mesa para despacho da Presidência ou deliberação do Plenário, nos demais casos.'
					},
			}
		}]
	},
	"139":{
		"caput":'No caso do art. 133, IV, a proposta será submetida ao Plenário antes do prosseguimento do estudo da matéria.'
	},
	"140":{
		"caput":'Os pareceres poderão ser proferidos oralmente, em plenário, por relator designado pelo Presidente da Mesa:',
		"enumeravel": [{
			"tipo":"inciso",
			"lista":{
					"1":{
						"caput":'nas matérias em regime de urgência;'
					},
					"2":{
						"caput":'nas matérias incluídas em Ordem do Dia, nos termos do art. 172;'
					},
					"3":{
						"caput":'nas demais matérias em que este Regimento expressamente o permita.'
					},
			}
		}]
	},
	"141":{
		"caput":'Se o parecer oral concluir pela apresentação de requerimento, projeto ou emenda, o texto respectivo deverá ser remetido à Mesa, por escrito, assinado pelo relator.'
	},
	"142":{
		"caput":'Quando as comissões se ocuparem de assuntos de interesse particular, procederem a inquérito, tomarem depoimentos e informações, ou praticarem outras diligências semelhantes, poderão solicitar, das autoridades legislativas, judiciárias ou administrativas, das entidades autárquicas, sociedades de economia mista e empresas concessionárias de serviços públicos, quaisquer documentos ou informações e permitir às pessoas diretamente interessadas a defesa dos seus direitos, por escrito ou oralmente.'
	},
	"143":{
		"caput":'Quando a comissão julgar que a petição, memorial, representação ou outro documento não deva ter andamento, mandá-lo-á arquivar, por proposta de qualquer de seus membros, comunicando o fato à Mesa.',
		"enumeravel": [{
			"tipo":"paragrafo",
			"lista":{
					"1":{
						"caput":'A comunicação será lida no período do Expediente, publicada no Diário do Senado Federal e encaminhada ao arquivo com o documento que lhe deu origem.'
					},
					"2":{
						"caput":'O exame do documento poderá ser reaberto se o Plenário o deliberar, a requerimento de qualquer Senador.'
					},
					"3":{
						"caput":'A comissão não poderá encaminhar à Câmara dos Deputados ou a outro órgão do Poder Público qualquer documento que lhe tenha sido enviado.'
					},
			}
		}]
	},
	"144":{
		"caput":'Quanto ao documento de natureza sigilosa, observar-se-ão, no trabalho das comissões, as seguintes normas:',
		"enumeravel": [{
			"tipo":"inciso",
			"lista":{
					"1":{
						"caput":'não será lícito transcrevê-lo, no todo ou em parte, nos pareceres e expediente de curso ostensivo;'
					},
					"2":{
						"caput":'se houver sido encaminhado ao Senado em virtude de requerimento formulado perante a comissão, o seu Presidente dele dará conhecimento ao requerente, em particular;'
					},
					"3":{
						"caput":'se a matéria interessar à comissão, ser-lhe-á dada a conhecer em reunião secreta;'
					},
					"4":{
						"caput":'se destinado a instruir o estudo de matéria em curso no Senado, será encerrado em sobrecarta, rubricada pelo Presidente da comissão, que acompanhará o processo em toda a sua tramitação;'
					},
					"5":{
						"caput":'quando o parecer contiver matéria de natureza sigilosa, será objeto das cautelas descritas no inciso IV.'
					},
			}
		}]
	},
	"145":{
		"caput":'A criação de comissão parlamentar de inquérito será feita mediante requerimento de um terço dos membros do Senado Federal.',
		"enumeravel": [{
			"tipo":"paragrafo",
			"lista":{
					"1":{
						"caput":'O requerimento de criação da comissão parlamentar de inquérito determinará o fato a ser apurado, o número de membros, o prazo de duração da comissão e o limite das despesas a serem realizadas.'
					},
					"2":{
						"caput":'Recebido o requerimento, o Presidente ordenará que seja numerado e publicado.'
					},
					"3":{
						"caput":'O Senador só poderá integrar duas comissões parlamentares de inquérito, uma como titular, outra como suplente.'
					},
					"4":{
						"caput":'A comissão terá suplentes, em número igual à metade do número dos titulares mais um, escolhidos no ato da designação destes, observadas as normas constantes do art. 78.'
					},
			}
		}]
	},
	"146":{
		"caput":'Não se admitirá comissão parlamentar de inquérito sobre matérias pertinentes:',
		"enumeravel": [{
			"tipo":"inciso",
			"lista":{
					"1":{
						"caput":'à Câmara dos Deputados;'
					},
					"2":{
						"caput":'às atribuições do Poder Judiciário;'
					},
					"3":{
						"caput":'aos Estados.'
					},
			}
		}]
	},
	"147":{
		"caput":'Na hipótese de ausência do relator a qualquer ato do inquérito, poderá o Presidente da comissão designar-lhe substituto para a ocasião, mantida a escolha na mesma representação partidária ou bloco parlamentar.'
	},
	"148":{
		"caput":'No exercício das suas atribuições, a comissão parlamentar de inquérito terá poderes de investigação próprios das autoridades judiciais, facultada a realização de diligências que julgar necessárias, podendo convocar Ministros de Estado, tomar o depoimento de qualquer autoridade, inquirir testemunhas, sob compromisso, ouvir indiciados, requisitar de órgão público informações ou documentos de qualquer natureza, bem como requerer ao Tribunal de Contas da União a realização de inspeções e auditorias que entender necessárias.',
		"enumeravel": [{
			"tipo":"paragrafo",
			"lista":{
					"1":{
						"caput":'No dia previamente designado, se não houver número para deliberar, a comissão parlamentar de inquérito poderá tomar depoimento das testemunhas ou autoridades convocadas, desde que estejam presentes o Presidente e o relator.'
					},
					"2":{
						"caput":'Os indiciados e testemunhas serão intimados de acordo com as prescrições estabelecidas na legislação processual penal, aplicando-se, no que couber, a mesma legislação, na inquirição de testemunhas e autoridades.'
					},
			}
		}]
	},
	"149":{
		"caput":'O Presidente da comissão parlamentar de inquérito, por deliberação desta, poderá incumbir um dos seus membros ou funcionários da Secretaria do Senado da realização de qualquer sindicância ou diligência necessária aos seus trabalhos.'
	},
	"150":{
		"caput":'Ao término de seus trabalhos, a comissão parlamentar de inquérito enviará à Mesa, para conhecimento do Plenário, seu relatório e conclusões.',
		"enumeravel": [{
			"tipo":"paragrafo",
			"lista":{
					"1":{
						"caput":'A comissão poderá concluir seu relatório por projeto de resolução se o Senado for competente para deliberar a respeito.'
					},
					"2":{
						"caput":'Sendo diversos os fatos objeto de inquérito, a comissão dirá, em separado, sobre cada um, podendo fazê-lo antes mesmo de finda a investigação dos demais.'
					},
			}
		}]
	},
	"151":{
		"caput":'A comissão parlamentar de inquérito encaminhará suas conclusões, se for o caso, ao Ministério Público, para que promova a responsabilidade civil ou criminal dos infratores.'
	},
	"152":{
		"caput":'O prazo da comissão parlamentar de inquérito poderá ser prorrogado, automaticamente, a requerimento de um terço dos membros do Senado, comunicado por escrito à Mesa, lido em plenário e publicado no Diário do Senado Federal, observado o disposto no art. 76, § 4º.'
	},
	"153":{
		"caput":'Nos atos processuais, aplicar-se-ão, subsidiariamente, as disposições do Código de Processo Penal.'
	},
	"154":{
		"caput":'As sessões do Senado podem ser:',
		"enumeravel": [{
			"tipo":"inciso",
			"lista":{
					"1":{
						"caput":'deliberativas:',
						"enumeravel": [{
							"tipo":"letra",
							"lista":{
									"1":{
										"caput":''
									},
									"2":{
										"caput":''
									},
							}
						}]
					},
					"2":{
						"caput":'não deliberativas;'
					},
					"3":{
						"caput":'especiais; e'
					},
					"4":{
						"caput":'de debates temáticos.'
					},
			}
		}]
	},
	"155":{
		"caput":'A sessão terá início de segunda a quinta-feira, às quatorze horas, e, às sextas-feiras, às nove horas, pelo relógio do plenário, presentes no recinto pelo menos um vigésimo da composição do Senado, e terá a duração máxima de quatro horas e trinta minutos, salvo prorrogação, ou no caso do disposto nos arts. 178 e 179.',
		"enumeravel": [{
			"tipo":"paragrafo",
			"lista":{
					"1":{
						"caput":'Ao declarar aberta a sessão, o Presidente proferirá as seguintes palavras: “Sob a proteção de Deus, iniciamos nossos trabalhos”.'
					},
					"2":{
						"caput":'Nos casos dos incisos I e IV do § 6º do art. 154, o Presidente declarará que não pode ser realizada a sessão, designando a Ordem do Dia para a seguinte, e despachando, independentemente de leitura, o expediente que irá integrar a ata da reunião a ser publicada no Diário do Senado Federal.'
					},
					"3":{
						"caput":'Havendo na Ordem do Dia matéria relevante que o justifique, a Presidência poderá adiar por até trinta minutos a abertura da sessão.'
					},
					"4":{
						"caput":'Em qualquer fase da sessão, estando em plenário menos de um vigésimo da composição da Casa, o Presidente a suspenderá, fazendo acionar as campainhas durante dez minutos, e se, ao fim desse prazo, permanecer a inexistência de número, a sessão será encerrada.'
					},
					"5":{
						"caput":'Do período do tempo da sessão descontar-se-ão as suspensões ocorridas.'
					},
			}
		}]
	},
	"156":{
		"caput":'A primeira parte da sessão, que terá a duração de cento e vinte minutos, será destinada à leitura do expediente e aos oradores inscritos na forma do disposto no art. 17.',
		"enumeravel": [{
			"tipo":"paragrafo",
			"lista":{
					"1":{
						"caput":'Constituem matéria do Período do Expediente:',
						"enumeravel": [{
							"tipo":"inciso",
							"lista":{
									"1":{
										"caput":'a apresentação de projeto, indicação, parecer ou requerimento não relacionado com as proposições constantes da Ordem do Dia;'
									},
									"2":{
										"caput":'as comunicações enviadas à Mesa pelos Senadores;'
									},
									"3":{
										"caput":'os pedidos de licença dos Senadores;'
									},
									"4":{
										"caput":'os ofícios, moções, mensagens, telegramas, cartas, memoriais e outros documentos recebidos.'
									},
							}
						}]
					},
					"2":{
						"caput":'O expediente será lido pelo Primeiro Secretário, na íntegra ou em resumo, a juízo do Presidente, ressalvado a qualquer Senador o direito de requerer sua leitura integral.'
					},
			}
		}]
	},
	"157":{
		"caput":'Não será lido, nem constituirá objeto de comunicação em sessão pública, documento de caráter sigiloso, observando-se, quanto ao expediente dessa natureza, as seguintes normas:',
		"enumeravel": [{
			"tipo":"inciso",
			"lista":{
					"1":{
						"caput":'se houver sido remetido ao Senado a requerimento de Senador, ainda que em cumprimento à manifestação do Plenário, o Presidente da Mesa dele dará conhecimento, em particular, ao requerente;'
					},
					"2":{
						"caput":'se a solicitação houver sido formulada por comissão, ao Presidente desta será encaminhado em sobrecarta fechada e rubricada pelo Presidente da Mesa;'
					},
					"3":{
						"caput":'se o documento se destinar a instruir o estudo de matéria em curso no Senado, tramitará em sobrecarta fechada, rubricada pelo Presidente da Mesa e pelos presidentes das comissões que dele tomarem conhecimento, feita na capa do processo a devida anotação.'
					},
			}
		}]
	},
	"158":{
		"caput":'O tempo que se seguir à leitura do expediente será destinado aos oradores do Período do Expediente, podendo cada um dos inscritos usar da palavra pelo prazo máximo de dez minutos nas sessões deliberativas e por vinte minutos nas sessões não deliberativas, sendo cabível a intercalação com as comunicações inadiáveis, o uso da palavra pelas lideranças ou as delegações delas.',
		"enumeravel": [{
			"tipo":"paragrafo",
			"lista":{
					"1":{
						"caput":'O Período do Expediente poderá ser prorrogado pelo Presidente, uma só vez, para que o orador conclua o seu discurso caso não tenha esgotado o tempo de que disponha, após o que a Ordem do Dia terá início impreterivelmente.'
					},
					"2":{
						"caput":'Se algum Senador, antes do término do Período do Expediente, solicitar à Mesa inscrição para manifestação de pesar, comemoração, comunicação inadiável ou explicação pessoal, o Presidente lhe assegurará o uso da palavra durante o Período do Expediente, sendo cabível a intercalação com oradores inscritos, o uso da palavra pelas lideranças ou as delegações destas.'
					},
					"3":{
						"caput":'No caso do § 2º, somente poderão usar da palavra três Senadores, por cinco minutos cada um, durante o Período do Expediente.'
					},
					"4":{
						"caput":'As inscrições que não puderem ser atendidas em virtude do levantamento ou da não realização da sessão, ou em virtude do disposto no § 5º, transferir-se-ão para a sessão do dia seguinte e as desta para a subsequente.'
					},
					"5":{
						"caput":'Havendo, na Ordem do Dia, matéria urgente compreendida no art. 336, I, não serão permitidos oradores no Período do Expediente.'
					},
					"6":{
						"caput":'Ressalvado o disposto no § 1º deste artigo, não haverá prorrogação do Período do Expediente.'
					},
			}
		}]
	},
	"159":{
		"caput":'No Período do Expediente, só poderão ser objeto de deliberação requerimentos que não dependam de parecer das comissões, que não digam respeito a proposições constantes da Ordem do Dia ou os que o Regimento não determine sejam submetidos em outra fase da sessão.'
	},
	"160":{
		"caput":'(Revogado).'
	},
	"161":{
		"caput":'Terminados os discursos do Período do Expediente, serão lidos os documentos que ainda existirem sobre a mesa.',
		"enumeravel": [{
			"tipo":"paragrafo",
			"lista":{
					"1":{
						"caput":'Quando houver, entre os documentos a serem lidos, requerimentos a votar, e se mais de um Senador pedir a palavra para encaminhar a votação, esta ficará adiada para o fim da Ordem do Dia.'
					},
			}
		}]
	},
	"162":{
		"caput":'A Ordem do Dia terá início, impreterivelmente, às dezesseis horas, salvo prorrogação nos termos do art. 158, § 6º.'
	},
	"163":{
		"caput":'As matérias serão incluídas em Ordem do Dia, a juízo do Presidente, segundo sua antiguidade e importância, observada a seguinte sequência:',
		"enumeravel": [{
			"tipo":"inciso",
			"lista":{
					"1":{
						"caput":'medida provisória, a partir do 46º (quadragésimo sexto) dia de sua vigência (Const., art. 62, § 6º);'
					},
					"2":{
						"caput":'matéria urgente de iniciativa do Presidente da República, com prazo de tramitação esgotado (Const., art. 64, § 2º);'
					},
					"3":{
						"caput":'matéria em regime de urgência do art. 336, I;'
					},
					"4":{
						"caput":'matéria preferencial constante do art. 172, II, segundo os prazos ali previstos;'
					},
					"5":{
						"caput":'matéria em regime de urgência do art. 336, II;'
					},
					"6":{
						"caput":'matéria em regime de urgência do art. 336, III;'
					},
					"7":{
						"caput":'matéria em tramitação normal.'
					},
			}
		}]
	},
	"164":{
		"caput":'Os projetos regulando a mesma matéria (art. 258) figurarão na Ordem do Dia em série, iniciada pela proposição preferida pela comissão competente, de maneira que a decisão do Plenário sobre esta prejulgue as demais.'
	},
	"165":{
		"caput":'Os pareceres sobre escolha de autoridades (art. 383) serão incluídos, em série, no final da Ordem do Dia.'
	},
	"166":{
		"caput":'Constarão da Ordem do Dia as matérias não apreciadas da pauta da sessão deliberativa ordinária anterior, com precedência sobre outras dos grupos a que pertençam.'
	},
	"167":{
		"caput":'Ao ser designada a Ordem do Dia, qualquer Senador poderá sugerir ao Presidente a inclusão de matérias em condições de nela figurar (art. 171).',
		"enumeravel": [{
			"tipo":"paragrafo",
			"lista":{
					"1":{
						"caput":'Nenhuma matéria poderá ser incluída em Ordem do Dia sem que tenha sido efetivamente publicada no Diário do Senado Federal e em avulso eletrônico, no mínimo, com dez dias de antecedência.'
					},
			}
		}]
	},
	"168":{
		"caput":'Salvo em casos especiais, assim considerados pela Presidência, não constarão, das Ordens do Dia das sessões das segundas e sextas-feiras, matérias em votação.',
		"enumeravel": [{
			"tipo":"paragrafo",
			"lista":{
					"1":{
						"caput":'O princípio estabelecido neste artigo aplica-se ainda às matérias que tenham sua discussão encerrada nas sessões ordinárias das segundas e sextas-feiras.'
					},
			}
		}]
	},
	"169":{
		"caput":'Somente poderão ser incluídas na Ordem do Dia, para deliberação do Plenário, em cada sessão legislativa, as proposições protocoladas junto à Secretaria-Geral da Mesa até a data de 30 de novembro.',
		"enumeravel": [{
			"tipo":"paragrafo",
			"lista":{
					"1":{
						"caput":'Ficam ressalvadas do disposto neste artigo as matérias da competência privativa do Senado Federal relacionadas no art. 52 da Constituição e, em casos excepcionais, até três matérias, por decisão da Presidência e consenso das lideranças.'
					},
			}
		}]
	},
	"170":{
		"caput":'A Ordem do Dia será anunciada ao término da sessão anterior, publicada no Diário do Senado Federal e em avulso eletrônico antes de iniciar-se a sessão respectiva.',
		"enumeravel": [{
			"tipo":"paragrafo",
			"lista":{
					"1":{
						"caput":'Não será designada Ordem do Dia para a primeira sessão de cada sessão legislativa.'
					},
					"2":{
						"caput":'No avulso eletrônico da Ordem do Dia deverá constar:',
						"enumeravel": [{
							"tipo":"inciso",
							"lista":{
									"1":{
										"caput":'os projetos em fase de recebimento de emendas perante a Mesa ou comissão;'
									},
									"2":{
										"caput":'os projetos em fase de apresentação do recurso a que se refere o art. 91, § 4º;'
									},
									"3":{
										"caput":'as proposições que deverão figurar em Ordem do Dia nas três sessões deliberativas ordinárias seguintes.'
									},
							}
						}]
					},
					"3":{
						"caput":'Nos dados referidos no § 2º, haverá indicação expressa dos prazos, número de dias transcorridos e, no caso do inciso I, da comissão que deverá receber as emendas.'
					},
			}
		}]
	},
	"171":{
		"caput":'A matéria dependente de exame das comissões só será incluída em Ordem do Dia depois de emitidos os pareceres, lidos no Período do Expediente, publicados no Diário do Senado Federal e em avulso eletrônico, observado o interstício regimental (arts. 280 e 281).'
	},
	"172":{
		"caput":'A inclusão em Ordem do Dia de proposição em rito normal, sem que esteja instruída com pareceres das comissões a que houver sido distribuída, só é admissível nas seguintes hipóteses:',
		"enumeravel": [{
			"tipo":"inciso",
			"lista":{
					"1":{
						"caput":'por deliberação do Plenário, se a única ou a última comissão a que estiver distribuída não proferir o seu parecer no prazo regimental;'
					},
					"2":{
						"caput":'por ato do Presidente, quando se tratar:',
						"enumeravel": [{
							"tipo":"letra",
							"lista":{
									"1":{
										"caput":''
									},
									"2":{
										"caput":'projeto de lei ânua ou que tenha por fim prorrogar prazo de lei, se faltarem dez dias, ou menos, para o término de sua vigência ou da sessão legislativa, quando o fato deva ocorrer em período de recesso do Congresso, ou nos dez dias que se seguirem à instalação da sessão legislativa subsequente;'
									},
									"3":{
										"caput":'projeto de decreto legislativo referente a tratado, convênio ou acordo internacional, se faltarem dez dias, ou menos, para o término do prazo no qual o Brasil deva manifestar-se sobre o ato em apreço;'
									},
									"4":{
										"caput":'projetos com prazo, se faltarem vinte dias para o seu término.',
										"enumeravel": [{
											"tipo":"paragrafo",
											"lista":{
													"1":{
														"caput":'Nas hipóteses do inciso II, c e d, o projeto emendado voltará à Ordem do Dia na segunda sessão deliberativa ordinária subsequente, salvo se o encerramento da discussão se der no penúltimo dia do prazo ou da sessão legislativa, hipótese em que a matéria terá a mesma tramitação prevista para o caso do art. 336, II.'
													},
											}
										}]
									},
							}
						}]
					},
			}
		}]
	},
	"173":{
		"caput":'Nenhum projeto poderá ficar sobre a mesa por mais de um mês sem figurar em Ordem do Dia, salvo para diligência aprovada pelo Plenário.'
	},
	"174":{
		"caput":'Em casos excepcionais, assim considerados pela Mesa, e nos sessenta dias que precederem as eleições gerais, poderão ser dispensadas, ouvidas as lideranças partidárias, as fases da sessão correspondentes ao Período do Expediente ou à Ordem do Dia.'
	},
	"175":{
		"caput":'A sequência dos trabalhos da Ordem do Dia não poderá ser alterada senão:',
		"enumeravel": [{
			"tipo":"inciso",
			"lista":{
					"1":{
						"caput":'para posse de Senador;'
					},
					"2":{
						"caput":'para leitura de mensagem, ofício ou documento sobre matéria urgente;'
					},
					"3":{
						"caput":'para pedido de urgência nos casos do art. 336, I;'
					},
					"4":{
						"caput":'em virtude de deliberação do Senado, no sentido de adiamento ou inversão da Ordem do Dia;'
					},
					"5":{
						"caput":'pela retirada de qualquer matéria, para cumprimento de despacho, correção de erro ou omissão no avulso eletrônico e para sanar falhas de instrução;'
					},
					"6":{
						"caput":'para constituição de série, em caso de votação secreta;'
					},
					"7":{
						"caput":'nos casos previstos no art. 304.'
					},
			}
		}]
	},
	"176":{
		"caput":'Esgotada a Ordem do Dia, o tempo que restar para o término da sessão será destinado, preferencialmente, ao uso da palavra pelas lideranças e, havendo tempo, pelos oradores inscritos na forma do disposto no art. 17.'
	},
	"177":{
		"caput":'Esgotado o tempo da sessão ou ultimados a Ordem do Dia e os discursos posteriores a esta, o Presidente a encerrará.'
	},
	"178":{
		"caput":'Se o término do tempo da sessão ocorrer quando iniciada uma votação, esta será ultimada independentemente de pedido de prorrogação.'
	},
	"179":{
		"caput":'Estando em apreciação matéria constante do art. 336, I e II, a sessão só poderá ser encerrada quando ultimada a deliberação.'
	},
	"180":{
		"caput":'A prorrogação da sessão poderá ser concedida pelo Plenário, em votação simbólica, antes do término do tempo regimental:',
		"enumeravel": [{
			"tipo":"inciso",
			"lista":{
					"1":{
						"caput":'por proposta do Presidente;'
					},
					"2":{
						"caput":'a requerimento de qualquer Senador.'
					},
			}
		}]
	},
	"181":{
		"caput":'O tempo que restar para o término da prorrogação será destinado à votação de matérias cuja discussão esteja encerrada.'
	},
	"182":{
		"caput":'Em sessões públicas, além dos Senadores, só serão admitidos no plenário os Suplentes de Senadores, os Deputados Federais, os ex-Senadores, entre estes incluídos os Suplentes de Senador que tenham exercido o mandato, os Ministros de Estado, quando comparecerem para os fins previstos neste Regimento, e os funcionários do Senado em objeto de serviço.'
	},
	"183":{
		"caput":'Durante as sessões públicas, não é permitida a presença, na bancada da imprensa, de pessoa a ela estranha.'
	},
	"184":{
		"caput":'É permitido a qualquer pessoa assistir às sessões públicas, do lugar que lhe for reservado, desde que se encontre desarmada e se conserve em silêncio, sem dar qualquer sinal de aplauso ou de reprovação ao que nelas se passar.',
		"enumeravel": [{
			"tipo":"paragrafo",
			"lista":{
					"1":{
						"caput":'A qualquer pessoa é vedado fumar no recinto do plenário.'
					},
			}
		}]
	},
	"185":{
		"caput":'Em sessão secreta, somente os Senadores terão ingresso no plenário e dependências anexas, ressalvados o disposto no parágrafo único do art. 192 e os casos em que o Senado conceda autorização a outras pessoas para a ela assistirem, mediante proposta da Presidência ou de líder.'
	},
	"186":{
		"caput":'A reportagem fotográfica no recinto, a irradiação sonora, a filmagem e a transmissão em televisão das sessões dependem de autorização do Presidente do Senado.'
	},
	"187":{
		"caput":'A sessão deliberativa extraordinária, convocada de ofício pelo Presidente ou por decisão do Senado, terá o mesmo rito e duração da ordinária.',
		"enumeravel": [{
			"tipo":"paragrafo",
			"lista":{
					"1":{
						"caput":'O Período do Expediente de sessão deliberativa extraordinária não excederá a trinta minutos.'
					},
			}
		}]
	},
	"188":{
		"caput":'Em sessão deliberativa extraordinária, só haverá oradores, antes da Ordem do Dia, caso não haja número para as deliberações.'
	},
	"189":{
		"caput":'O Presidente prefixará dia, horário e Ordem do Dia para a sessão deliberativa extraordinária, dando-os a conhecer, previamente, ao Senado, em sessão ou através de qualquer meio de comunicação.',
		"enumeravel": [{
			"tipo":"paragrafo",
			"lista":{
					"1":{
						"caput":'Não é obrigatória a inclusão, na Ordem do Dia de sessão deliberativa extraordinária, de matéria não ultimada na sessão anterior, ainda que em regime de urgência ou em curso de votação.'
					},
			}
		}]
	},
	"190":{
		"caput":'A sessão secreta será convocada pelo Presidente, de ofício ou mediante requerimento.',
		"enumeravel": [{
			"tipo":"paragrafo",
			"lista":{
					"1":{
						"caput":'A finalidade da sessão secreta deverá figurar expressamente no requerimento, mas não será divulgada, assim como o nome do requerente.'
					},
			}
		}]
	},
	"191":{
		"caput":'Recebido o requerimento a que se refere o art. 190, o Senado passará a funcionar secretamente para a sua votação; se aprovado, e desde que não haja data prefixada, a sessão secreta será convocada para o mesmo dia ou para o dia seguinte.'
	},
	"192":{
		"caput":'Na sessão secreta, antes de se iniciarem os trabalhos, o Presidente determinará a saída do plenário, tribunas, galerias e respectivas dependências, de todas as pessoas estranhas, inclusive funcionários da Casa.',
		"enumeravel": [{
			"tipo":"paragrafo",
			"lista":{
					"1":{
						"caput":'O Presidente poderá admitir na sessão, a seu juízo, a presença dos servidores que julgar necessários.'
					},
			}
		}]
	},
	"193":{
		"caput":'No início dos trabalhos de sessão secreta, deliberar-se-á se o assunto que motivou a convocação deverá ser tratado secreta ou publicamente, não podendo esse debate exceder a quinze minutos, sendo permitido a cada orador usar da palavra por três minutos, de uma só vez. No primeiro caso, prosseguirão os trabalhos secretamente; no segundo, serão levantados para que o assunto seja, oportunamente, apreciado em sessão pública.'
	},
	"194":{
		"caput":'Antes de encerrar-se uma sessão secreta, o Plenário resolverá, por simples votação e sem debate, se deverão ser conservados em sigilo ou publicados o resultado, o nome',
		"enumeravel": [{
			"tipo":"None",
			"lista":{
					"1":{
						"caput":'requereram a convocação e, nos casos do art. 135, os pareceres e demais documentos constantes do processo.'
					},
			}
		}]
	},
	"195":{
		"caput":'Ao Senador que houver participado dos debates em sessão secreta é permitido reduzir por escrito o seu discurso, no prazo de vinte e quatro horas, para ser arquivado com a ata.'
	},
	"196":{
		"caput":'A sessão secreta terá a duração de quatro horas e trinta minutos, salvo prorrogação.'
	},
	"197":{
		"caput":'Transformar-se-á em secreta a sessão:',
		"enumeravel": [{
			"tipo":"inciso",
			"lista":{
					"1":{
						"caput":'obrigatoriamente, quando o Senado tiver de se manifestar sobre:',
						"enumeravel": [{
							"tipo":"letra",
							"lista":{
									"1":{
										"caput":'de guerra (Const., art. 49, II);'
									},
									"2":{
										"caput":'sobre a paz (Const., art. 49, II);'
									},
									"3":{
										"caput":''
									},
									"4":{
										"caput":'de chefe de missão diplomática de caráter permanente (Const., art. 52, IV);'
									},
									"5":{
										"caput":'para realização de sessão secreta (art. 191);'
									},
							}
						}]
					},
					"2":{
						"caput":'por deliberação do Plenário, mediante proposta da Presidência ou a requerimento de qualquer Senador.'
					},
			}
		}]
	},
	"198":{
		"caput":'Somente em sessão secreta poderá ser dado a conhecer, ao Plenário, documento de natureza sigilosa.'
	},
	"199":{
		"caput":'O Senado poderá interromper a sessão ou realizar sessão especial para comemoração ou recepção de altas personalidades, a juízo do Presidente ou por deliberação do Plenário, mediante requerimento de 6 (seis) senadores.',
		"enumeravel": [{
			"tipo":"paragrafo",
			"lista":{
					"1":{
						"caput":'Salvo o caso de recepção a Chefe de Estado ou de Governo ou autoridade equivalente, a sessão especial somente poderá ocorrer 2 (duas) vezes por mês, às segundas ou sextas-feiras, e quando não houver Ordem do Dia previamente agendada para esses dias.'
					},
					"2":{
						"caput":'A homenagem à mesma efeméride ou personalidade somente poderá ocorrer 1 (uma) vez a cada 10 (dez) anos.'
					},
					"3":{
						"caput":'A primeira comemoração das homenagens somente poderá ocorrer após 25 (vinte e cinco) anos do fato.'
					},
					"4":{
						"caput":'A sessão especial terá a duração máxima de 2 (duas) horas.'
					},
					"5":{
						"caput":'Em sessão especial, poderão ser admitidos convidados à Mesa e no Plenário.'
					},
					"6":{
						"caput":'O parlamentar estrangeiro só será recebido em plenário se o Parlamento do seu país der tratamento igual aos congressistas brasileiros que o visitem.'
					},
			}
		}]
	},
	"200":{
		"caput":'A sessão especial, que independe de número, será convocada em sessão, através do Diário do Senado Federal, ou por outro meio oficial de comunicação, e nela somente usarão da',
		"enumeravel": [{
			"tipo":"paragrafo",
			"lista":{
					"1":{
						"caput":'senadores previamente designados pelo Presidente ou por líder de partido ou bloco parlamentar.'
					},
					"2":{
						"caput":'Não serão concedidos apartes nas sessões especiais.'
					},
			}
		}]
	},
	"201":{
		"caput":'Será elaborada ata circunstanciada de cada sessão, contendo, entre outros, os incidentes, debates, declarações da Presidência, listas de presença e chamada, texto das matérias lidas ou votadas e os discursos, a qual constará, salvo se secreta, do Diário do Senado Federal, que será publicado diariamente, durante as sessões legislativas ordinárias e extraordinárias, e, eventualmente, nos períodos de recesso, sempre que houver matéria para publicação.',
		"enumeravel": [{
			"tipo":"paragrafo",
			"lista":{
					"1":{
						"caput":'Não havendo sessão, nos casos do art. 154, § 6º, I e IV, será publicada ata da reunião, que conterá os nomes do Presidente, dos Secretários e dos Senadores presentes, e o expediente despachado.'
					},
					"2":{
						"caput":'Quando o discurso, requisitado para revisão, não for restituído à Taquigrafia até as dezoito horas do dia seguinte, deixará de ser incluído na ata da sessão respectiva, onde figurará nota explicativa a respeito, no lugar a ele correspondente.'
					},
					"3":{
						"caput":'Se, ao fim de trinta dias, o discurso não houver sido restituído, a publicação se fará pela cópia arquivada nos serviços taquigráficos, com nota de que não foi revisto pelo orador.'
					},
			}
		}]
	},
	"202":{
		"caput":'Constarão, também, da ata:',
		"enumeravel": [{
			"tipo":"inciso",
			"lista":{
					"1":{
						"caput":'por extenso:',
						"enumeravel": [{
							"tipo":"letra",
							"lista":{
									"1":{
										"caput":'mensagens ou ofícios do Governo ou da Câmara dos Deputados, salvo quando relativos à sanção de projetos, devolução de autógrafos ou agradecimento de comunicação;'
									},
									"2":{
										"caput":'proposições legislativas e declarações de voto;'
									},
							}
						}]
					},
					"2":{
						"caput":'em súmula, todos os demais documentos lidos no Período do Expediente, salvo deliberação do Senado ou determinação da Presidência.'
					},
			}
		}]
	},
	"203":{
		"caput":'É permitido ao Senador enviar à Mesa, para publicação no Diário do Senado Federal e inclusão nos Anais, o discurso que deseje proferir na sessão, dispensada a sua leitura.'
	},
	"204":{
		"caput":'Quando o esclarecimento da Presidência sobre questão regimental ou discurso de algum Senador forem lidos, constará da ata a indicação de o terem sido.'
	},
	"205":{
		"caput":'A ata registrará, em cada momento, a substituição ocorrida em relação à Presidência da sessão.',
		"enumeravel": [{
			"tipo":"paragrafo",
			"lista":{
					"1":{
						"caput":'Quando a substituição na Presidência se der durante discurso, far-se-á o registro no fim deste.'
					},
			}
		}]
	},
	"206":{
		"caput":'Na ata, o nome do Presidente será registrado, entre parênteses, em seguida às palavras: “O Sr. Presidente”.'
	},
	"207":{
		"caput":'Os pedidos de retificação e as questões de ordem sobre a ata serão decididos pela Presidência.'
	},
	"208":{
		"caput":'A ata de sessão secreta será redigida pelo Segundo-Secretário, aprovada com qualquer número, antes de levantada a sessão, assinada pelo Presidente, Primeiro e Segundo',
		"enumeravel": [{
			"tipo":"paragrafo",
			"lista":{
					"1":{
						"caput":'em sobrecarta lacrada, datada e rubricada pelos Secretários, e recolhida ao arquivo.'
					},
					"2":{
						"caput":'O discurso a que se refere o art. 195 será arquivado com a ata e os documentos referentes à sessão, em segunda sobrecarta, igualmente lacrada.'
					},
					"3":{
						"caput":'O desarquivamento dos documentos referidos no § 1º só poderá ser feito mediante requisição da Presidência.'
					},
			}
		}]
	},
	"209":{
		"caput":'Os trabalhos das sessões serão organizados em anais, por ordem cronológica, para distribuição aos Senadores.'
	},
	"210":{
		"caput":'A transcrição de documento no Diário do Senado Federal, para que conste dos Anais, é permitida:',
		"enumeravel": [{
			"tipo":"inciso",
			"lista":{
					"1":{
						"caput":'quando constituir parte integrante de discurso de Senador;'
					},
					"2":{
						"caput":'quando aprovada pelo Presidente do Senado, a requerimento de qualquer Senador.'
					},
			}
		}]
	},
	"211":{
		"caput":'Consistem as proposições em:',
		"enumeravel": [{
			"tipo":"inciso",
			"lista":{
					"1":{
						"caput":'propostas de emenda à Constituição;'
					},
					"2":{
						"caput":'projetos;'
					},
					"3":{
						"caput":'requerimentos;'
					},
					"4":{
						"caput":'indicações;'
					},
					"5":{
						"caput":'pareceres;'
					},
					"6":{
						"caput":'emendas.'
					},
			}
		}]
	},
	"212":{
		"caput":'Poderão ter tramitação iniciada no Senado propostas de emenda à Constituição de iniciativa:',
		"enumeravel": [{
			"tipo":"inciso",
			"lista":{
					"1":{
						"caput":'de um terço, no mínimo, de seus membros (Const., art. 60, I);'
					},
					"2":{
						"caput":'de mais da metade das Assembleias Legislativas das Unidades da Federação, manifestando-se, cada uma delas, pela maioria relativa de seus membros (Const., art. 60, III).'
					},
			}
		}]
	},
	"213":{
		"caput":'Os projetos compreendem:',
		"enumeravel": [{
			"tipo":"inciso",
			"lista":{
					"1":{
						"caput":'projeto de lei, referente a matéria da competência do Congresso Nacional, com sanção do Presidente da República (Const., art. 48);'
					},
					"2":{
						"caput":'projeto de decreto legislativo, referente à matéria da competência exclusiva do Congresso Nacional (Const., art. 49);'
					},
					"3":{
						"caput":'projeto de resolução sobre matéria da competência privativa do Senado (Const., art. 52).'
					},
			}
		}]
	},
	"213-A":{
		"caput":'É facultado a qualquer Senador ou comissão oferecer projeto de lei de consolidação, atendidos os princípios de que tratam os arts. 13, 14 e 15 da Lei Complementar nº 95, de 26 de fevereiro de 1998, vedada a alteração no mérito das normas que serviram de base para a consolidação.'
	},
	"213-B":{
		"caput":'O projeto recebido será lido, numerado, publicado e distribuído à comissão que guardar maior pertinência quanto à matéria, que se pronunciará sobre o atendimento ao princípio de preservação do conteúdo original das normas consolidadas.',
		"enumeravel": [{
			"tipo":"paragrafo",
			"lista":{
					"1":{
						"caput":'Qualquer Senador ou comissão poderá, no prazo de trinta dias após a publicação do projeto de lei de consolidação, oferecer à comissão encarregada de seu exame:',
						"enumeravel": [{
							"tipo":"inciso",
							"lista":{
									"1":{
										"caput":'sugestões de redação, vedadas alterações que envolvam o mérito da matéria original;'
									},
									"2":{
										"caput":'sugestões de incorporação de normas que não foram objeto de consolidação;'
									},
									"3":{
										"caput":'sugestões de retirada de normas que foram objeto de consolidação.'
									},
							}
						}]
					},
					"2":{
						"caput":'As sugestões que envolverem alteração no mérito da proposição que serviu de base à formulação do projeto de lei de consolidação serão dadas como rejeitadas.'
					},
					"3":{
						"caput":'As disposições referentes à tramitação dos projetos de lei aplicam-se à tramitação e à aprovação do projeto de lei de consolidação, nos termos do que preceitua o Regimento Interno do Senado Federal, ressalvados os procedimentos exclusivos aplicáveis à subespécie, constantes deste Regimento.'
					},
			}
		}]
	},
	"213-C":{
		"caput":'Aprovado o projeto de lei de consolidação na comissão, será ele encaminhado ao Plenário.',
		"enumeravel": [{
			"tipo":"paragrafo",
			"lista":{
					"1":{
						"caput":'Poderão ser oferecidas, em plenário, emendas destinadas à correção de redação que afronte o mérito da matéria, que serão submetidas à deliberação da Comissão de Constituição, Justiça e Cidadania.'
					},
					"2":{
						"caput":'As emendas de correção de erro de redação julgadas improcedentes pela Comissão de Constituição, Justiça e Cidadania serão dadas como rejeitadas.'
					},
			}
		}]
	},
	"213-D":{
		"caput":'Após a entrada em vigor da lei de consolidação, deverão fazer-lhe expressa remissão todos os projetos vinculados à matéria.'
	},
	"213-E":{
		"caput":'Aplicam-se os mesmos procedimentos previstos nos arts. 213-B, 213-C e 213-D aos projetos de lei de consolidação originários da Câmara dos Deputados em revisão no Senado Federal e às emendas da Câmara dos Deputados a projeto de lei de consolidação originário do Senado.'
	},
	"214":{
		"caput":'O requerimento poderá ser oral ou escrito.',
		"enumeravel": [{
			"tipo":"paragrafo",
			"lista":{
					"1":{
						"caput":'É oral e despachado pelo Presidente o requerimento:',
						"enumeravel": [{
							"tipo":"inciso",
							"lista":{
									"1":{
										"caput":'de leitura de qualquer matéria sujeita ao conhecimento do Plenário;'
									},
									"2":{
										"caput":'de retificação da ata;'
									},
									"3":{
										"caput":'de inclusão em Ordem do Dia de matéria em condições regimentais de nela figurar;'
									},
									"4":{
										"caput":'de permissão para falar sentado.'
									},
							}
						}]
					},
			}
		}]
	},
	"215":{
		"caput":'São escritos os requerimentos não referidos no art. 214 e dependem de votação por maioria simples, presente a maioria da composição do Senado, salvo os abaixo especificados:',
		"enumeravel": [{
			"tipo":"inciso",
			"lista":{
					"1":{
						"caput":'dependentes de decisão da Mesa:',
						"enumeravel": [{
							"tipo":"letra",
							"lista":{
									"1":{
										"caput":'informação a Ministro de Estado ou a qualquer titular de órgão diretamente subordinado à Presidência da República (Const., art. 50, § 2º);'
									},
									"2":{
										"caput":'licença (arts. 13 e 43);'
									},
									"3":{
										"caput":'tramitação em conjunto de proposição regulando a mesma matéria, exceto se a proposição constar da Ordem do Dia ou for objeto de parecer aprovado em comissão.'
									},
							}
						}]
					},
					"2":{
						"caput":'dependentes de despacho do Presidente:',
						"enumeravel": [{
							"tipo":"letra",
							"lista":{
									"1":{
										"caput":'publicação de informações oficiais no Diário do Senado Federal;'
									},
									"2":{
										"caput":'esclarecimentos sobre atos da administração interna do Senado;'
									},
									"3":{
										"caput":'retirada de indicação ou requerimento;'
									},
									"4":{
										"caput":'reconstituição de proposição;'
									},
									"5":{
										"caput":'retirada de proposição, desde que não tenha recebido parecer de comissão e não conste de Ordem do Dia (art. 256, § 2º);'
									},
									"6":{
										"caput":'publicação de documentos no Diário do Senado Federal para transcrição nos Anais (art. 210, II).'
									},
									"7":{
										"caput":'conversão de proposição em indicação, nos termos do art. 227-A, inciso I; (Incluído pela Resolução n.º 14, de 2019)'
									},
							}
						}]
					},
					"3":{
						"caput":'dependentes de votação com a presença, no mínimo, de um décimo da composição do Senado:',
						"enumeravel": [{
							"tipo":"letra",
							"lista":{
									"1":{
										"caput":''
									},
									"2":{
										"caput":'prorrogação do tempo da sessão;'
									},
									"3":{
										"caput":'homenagem de pesar, inclusive levantamento da sessão.'
									},
							}
						}]
					},
					"4":{
						"caput":'(Revogado).'
					},
			}
		}]
	},
	"216":{
		"caput":'Os requerimentos de informações estão sujeitos às seguintes normas:',
		"enumeravel": [{
			"tipo":"inciso",
			"lista":{
					"1":{
						"caput":'serão admissíveis para esclarecimento de qualquer assunto submetido à apreciação do Senado ou atinente a sua competência fiscalizadora;'
					},
					"2":{
						"caput":'não poderão conter pedido de providência, consulta, sugestão, conselho ou interrogação sobre propósito da autoridade a quem sem dirija;'
					},
					"3":{
						"caput":'lidos no Período do Expediente, serão despachados à Mesa para decisão;'
					},
					"4":{
						"caput":'se deferidos, serão solicitadas, à autoridade competente, as informações requeridas, ficando interrompida a tramitação da matéria que se pretende esclarecer. Se indeferidos, irão ao Arquivo, feita comunicação ao Plenário;'
					},
					"5":{
						"caput":'as informações recebidas, quando se destinarem à elucidação de matéria pertinente a proposição em curso no Senado, serão incorporadas ao respectivo processo.'
					},
			}
		}]
	},
	"217":{
		"caput":'O requerimento de remessa de documentos equipara-se ao de pedido de informações.'
	},
	"218":{
		"caput":'O requerimento de inserção em ata de voto de pesar só é admissível por motivo de luto nacional decretado pelo Poder Executivo, ou por falecimento de:',
		"enumeravel": [{
			"tipo":"inciso",
			"lista":{
					"1":{
						"caput":'pessoa que tenha exercido o cargo de Presidente ou Vice-Presidente da República;'
					},
					"2":{
						"caput":'ex-membro do Congresso Nacional;'
					},
					"3":{
						"caput":'pessoa que exerça ou tenha exercido o cargo de:',
						"enumeravel": [{
							"tipo":"letra",
							"lista":{
									"1":{
										"caput":'do Supremo Tribunal Federal;'
									},
									"2":{
										"caput":'de Tribunal Superior da União;'
									},
									"3":{
										"caput":'do Tribunal de Contas da União;'
									},
									"4":{
										"caput":'de Estado;'
									},
									"5":{
										"caput":'Presidente de Assembleia Legislativa ou de Tribunal de Justiça estadual;'
									},
									"6":{
										"caput":'de Território ou do Distrito Federal;'
									},
							}
						}]
					},
					"4":{
						"caput":'Chefe de Estado ou de governo estrangeiro;'
					},
					"5":{
						"caput":'Chefe de Missão Diplomática de país estrangeiro acreditada junto ao Governo Brasileiro;'
					},
					"6":{
						"caput":'Chefe de Missão Diplomática do Brasil junto a governo estrangeiro, falecido no posto;'
					},
					"7":{
						"caput":'personalidade de relevo na vida político-administrativa internacional.'
					},
			}
		}]
	},
	"219":{
		"caput":'Ao serem prestadas homenagens de pesar, poderá ser observado um minuto de silêncio, em memória do extinto, após usarem da palavra todos os oradores.'
	},
	"220":{
		"caput":'O requerimento de levantamento da sessão, por motivo de pesar, só é permitido em caso de falecimento do Presidente da República, do Vice-Presidente da República ou de membro do Congresso Nacional.'
	},
	"221":{
		"caput":'Além das homenagens previstas nos arts. 218 a 220, o Plenário poderá autorizar:',
		"enumeravel": [{
			"tipo":"inciso",
			"lista":{
					"1":{
						"caput":'a apresentação de condolências à família do falecido, ao Estado do seu nascimento ou ao em que tenha exercido a sua atividade, ao partido político e a altas entidades culturais a que haja pertencido;'
					},
					"2":{
						"caput":'a representação nos funerais e cerimônias levadas a efeito em homenagem à memória do extinto.'
					},
			}
		}]
	},
	"222":{
		"caput":'O Senador poderá apresentar requerimento de voto de aplauso, congratulações, louvor, solidariedade ou censura, que será, após lido no Período do Expediente, encaminhado em nome do autor.',
		"enumeravel": [{
			"tipo":"paragrafo",
			"lista":{
					"1":{
						"caput":'Se disser respeito a ato público ou a acontecimento de alta significação nacional ou internacional, o voto de aplauso, congratulações, louvor, solidariedade ou censura poderá, mediante requerimento subscrito por um terço da composição da Casa, ser encaminhado em nome do Senado Federal, após sua aprovação pelo Plenário.'
					},
					"2":{
						"caput":'Aplica-se o disposto no § 1º se o requerimento for de iniciativa de comissão permanente, observado o disposto no art. 245.'
					},
					"3":{
						"caput":'Os requerimentos referidos nos §§ 1º e 2º não apreciados durante a sessão legislativa em que foram apresentados serão arquivados definitivamente.'
					},
			}
		}]
	},
	"223":{
		"caput":'(Revogado).'
	},
	"224-X":{
		"caput":'Indicação corresponde a sugestão de Senador ou comissão para que o assunto, nela focalizado, seja objeto de providência ou estudo pelo órgão competente da Casa, com a finalidade do seu esclarecimento ou formulação de proposição legislativa.'
	},
	"224":{
		"caput":'Indicação é a proposição por meio da qual o Senador ou a comissão: (Redação dada pela Resolução n.º 14, de 2019)',
		"enumeravel": [{
			"tipo":"inciso",
			"lista":{
					"1":{
						"caput":'sugere a outro Poder a adoção de providência, a realização de ato administrativo ou de gestão ou o envio de projeto sobre matéria de sua iniciativa exclusiva; (Incluído pela Resolução n.º 14, de 2019)'
					},
					"2":{
						"caput":'sugere que o assunto focalizado seja objeto de providência ou estudo pelo órgão ou pela comissão competente da Casa, com a finalidade de seu esclarecimento ou de formulação de proposição legislativa. (Incluído pela Resolução n.º 14, de 2019)'
					},
			}
		}]
	},
	"225":{
		"caput":'A indicação não poderá conter:',
		"enumeravel": [{
			"tipo":"inciso",
			"lista":{
					"1":{
						"caput":'consulta a qualquer comissão sobre:',
						"enumeravel": [{
							"tipo":"letra",
							"lista":{
									"1":{
										"caput":'ou aplicação de lei;'
									},
									"2":{
										"caput":'de outro Poder;'
									},
									"3":{
										"caput":'de outro Poder ou de seus órgãos e autoridades; (Redação dada pela Resolução n.º 14, de 2019)'
									},
							}
						}]
					},
					"2":{
						"caput":'sugestão ou conselho a qualquer Poder.'
					},
					"3":{
						"caput":'conselho a qualquer Poder. (Redação dada pela Resolução n.º 14, de 2019)'
					},
			}
		}]
	},
	"226-X":{
		"caput":'Lida no Período do Expediente, a indicação será encaminhada à comissão competente.'
	},
	"226":{
		"caput":'Lida no Período do Expediente e publicada no Diário do Senado Federal, a indicação será: (Redação dada pela Resolução n.º 14, de 2019)',
		"enumeravel": [{
			"tipo":"inciso",
			"lista":{
					"1":{
						"caput":'no caso do art. 224, inciso I, encaminhada pelo Presidente à autoridade de outro Poder; (Incluído pela Resolução n.º 14, de 2019)'
					},
					"2":{
						"caput":'no caso do art. 224, inciso II, encaminhada pelo Presidente: (Incluído pela Resolução n.º 14, de 2019)',
						"enumeravel": [{
							"tipo":"letra",
							"lista":{
									"1":{
										"caput":'órgão competente da Casa; (Incluída pela Resolução n.º 14, de 2019)'
									},
									"2":{
										"caput":'comissão ou às comissões competentes. (Incluída pela Resolução n.º 14, de 2019)'
									},
							}
						}]
					},
			}
		}]
	},
	"227-X":{
		"caput":'A indicação não será discutida nem votada pelo Senado. A deliberação tomará por base a conclusão do parecer da comissão.',
		"enumeravel": [{
			"tipo":"paragrafo",
			"lista":{
					"1":{
						"caput":'Se a indicação for encaminhada a mais de uma comissão e os pareceres forem discordantes nas suas conclusões, será votado, preferencialmente, o da que tiver mais pertinência regimental para se manifestar sobre a matéria. Em caso de competência concorrente, votar-se-á, preferencialmente, o último, salvo se o Plenário decidir o contrário, a requerimento de qualquer Senador ou comissão. (Suprimido pela Resolução n.º 14, de 2019)'
					},
			}
		}]
	},
	"227":{
		"caput":'A indicação não será discutida nem votada pelo Senado. (Redação dada pela Resolução n.º 14, de 2019)',
		"enumeravel": [{
			"tipo":"paragrafo",
			"lista":{
					"1":{
						"caput":'No caso do art. 226, inciso II, alínea "a", se o órgão competente da Casa sugerir a apresentação de proposição legislativa, a indicação será encaminhada ao seu autor para, se for o caso, apresentar a proposição, a qual seguirá os trâmites regimentais das proposições congêneres. (Incluído pela Resolução n.º 14, de 2019)'
					},
					"2":{
						"caput":'No caso do art. 226, inciso II, alínea "b": (Incluído pela Resolução n.º 14, de 2019)',
						"enumeravel": [{
							"tipo":"inciso",
							"lista":{
									"1":{
										"caput":'se o parecer da comissão competente concluir pela apresentação de proposição legislativa, esta seguirá os trâmites regimentais das proposições congêneres; (Incluído pela Resolução n.º 14, de 2019)'
									},
									"2":{
										"caput":'se a indicação for encaminhada a mais de uma comissão e os pareceres forem discordantes nas suas conclusões: (Incluído pela Resolução n.º 14, de 2019)',
										"enumeravel": [{
											"tipo":"letra",
											"lista":{
													"1":{
														"caput":'votado, preferencialmente, o da comissão que tiver mais pertinência regimental para se manifestar sobre a matéria; (Incluída pela Resolução n.º 14, de 2019)'
													},
													"2":{
														"caput":'caso de competência concorrente, será votado, preferencialmente, o último, salvo se o Plenário decidir o contrário, a requerimento de qualquer Senador ou comissão. (Incluída pela Resolução n.º 14, de 2019)'
													},
											}
										}]
									},
							}
						}]
					},
			}
		}]
	},
	"227-A":{
		"caput":'A proposição na qual for verificado vício insanável de iniciativa poderá ser convertida em indicação: (Incluído pela Resolução n.º 14, de 2019)',
		"enumeravel": [{
			"tipo":"inciso",
			"lista":{
					"1":{
						"caput":'por requerimento de seu autor; (Incluído pela Resolução n.º 14, de 2019)'
					},
					"2":{
						"caput":'por conclusão do parecer da comissão incumbida de analisar sua constitucionalidade. (Incluído pela Resolução n.º 14, de 2019)'
					},
			}
		}]
	},
	"228":{
		"caput":'Constitui proposição o parecer que deva ser discutido e votado pelo Plenário, quando não concluir pela apresentação de projeto, requerimento ou emenda.',
		"enumeravel": [{
			"tipo":"paragrafo",
			"lista":{
					"1":{
						"caput":'Para discussão e votação, o parecer será incluído em Ordem do Dia.'
					},
			}
		}]
	},
	"229-X":{
		"caput":'Se houver mais de um parecer, de conclusões discordantes, sobre a mesma matéria, a ser submetida ao Plenário, proceder-se-á de acordo com a norma estabelecida no art. 227, parágrafo único.'
	},
	"229":{
		"caput":'Se houver mais de um parecer, de conclusões discordantes, sobre a mesma matéria, a ser submetida ao Plenário, proceder-se-á de acordo com o disposto no art. 227, § 2º, inciso II. (Redação dada pela Resolução n.º 14, de 2019)'
	},
	"230":{
		"caput":'Não se admitirá emenda:',
		"enumeravel": [{
			"tipo":"inciso",
			"lista":{
					"1":{
						"caput":'sem relação com a matéria da disposição que se pretenda emendar;'
					},
					"2":{
						"caput":'em sentido contrário à proposição quando se trate de proposta de emenda à Constituição, projeto de lei ou de resolução;'
					},
					"3":{
						"caput":'que diga respeito a mais de um dispositivo, a não ser que se trate de modificações correlatas, de sorte que a aprovação, relativamente a um dispositivo, envolva a necessidade de se alterarem outros;'
					},
					"4":{
						"caput":'que importe aumento da despesa prevista (Const., art. 63):',
						"enumeravel": [{
							"tipo":"letra",
							"lista":{
									"1":{
										"caput":'projetos de iniciativa do Presidente da República, ressalvado o disposto no art. 166, §§ 3º e 4º, da Constituição (Const., art. 63, I);'
									},
									"2":{
										"caput":'projetos sobre organização dos serviços administrativos do Senado Federal, dos Tribunais Federais e do Ministério Público (Const., art. 63, II).'
									},
							}
						}]
					},
			}
		}]
	},
	"231":{
		"caput":'Às comissões é admitido oferecer subemendas, as quais não poderão conter matéria estranha à das respectivas emendas.'
	},
	"232":{
		"caput":'A emenda não adotada pela comissão (art. 124, I) poderá ser renovada em plenário, salvo sendo unânime o parecer pela rejeição.'
	},
	"233":{
		"caput":'Nenhuma emenda será aceita sem que o autor a tenha justificado por escrito ou oralmente.',
		"enumeravel": [{
			"tipo":"paragrafo",
			"lista":{
					"1":{
						"caput":'A justificação oral de emenda em plenário deverá ser feita no prazo que seu autor dispuser para falar no Período do Expediente da sessão.'
					},
			}
		}]
	},
	"234":{
		"caput":'A emenda que altere apenas a redação da proposição será submetida às mesmas formalidades regimentais de que dependerem as pertinentes ao mérito.',
		"enumeravel": [{
			"tipo":"paragrafo",
			"lista":{
					"1":{
						"caput":'Quando houver dúvidas sobre se a emenda apresentada como de redação atinge a substância da proposição, ouvir-se-á a Comissão de Constituição, Justiça e Cidadania.'
					},
			}
		}]
	},
	"235":{
		"caput":'A apresentação de proposição será feita:',
		"enumeravel": [{
			"tipo":"inciso",
			"lista":{
					"1":{
						"caput":'perante comissão, quando se tratar de emenda apresentada de acordo com o disposto no art. 122;'
					},
					"2":{
						"caput":'perante a Mesa, no prazo de cinco dias úteis, quando se tratar de emenda a:',
						"enumeravel": [{
							"tipo":"letra",
							"lista":{
									"1":{
										"caput":'de alteração ou reforma do Regimento Interno;'
									},
									"2":{
										"caput":'de decreto legislativo referente a prestação de contas do Presidente da República;'
									},
									"3":{
										"caput":'apreciados pelas comissões com poder terminativo, quando houver interposição de recurso;'
									},
									"4":{
										"caput":'em turno único, que obtiver parecer favorável, quanto ao mérito, das comissões;'
									},
									"5":{
										"caput":'em turno único, que obtiver parecer contrário, quanto ao mérito, das comissões, desde que admitido recurso para sua tramitação;'
									},
									"6":{
										"caput":'de autoria de comissão;'
									},
							}
						}]
					},
					"3":{
						"caput":'em plenário, nos seguintes casos:',
						"enumeravel": [{
							"tipo":"letra",
							"lista":{
									"1":{
										"caput":'Período do Expediente:'
									},
									"2":{
										"caput":'emenda a matéria a ser votada nessa fase da sessão;'
									},
									"3":{
										"caput":'indicação;'
									},
									"4":{
										"caput":'projeto;'
									},
									"5":{
										"caput":'requerimento que, regimentalmente, não deva ser apresentado em outra fase da sessão;'
									},
									"6":{
										"caput":'Ordem do Dia:'
									},
									"7":{
										"caput":'requerimento que diga respeito à ordenação das matérias da Ordem do Dia ou a proposição dela constante;'
									},
									"8":{
										"caput":'emenda a projeto em turno suplementar, ao anunciar-se sua discussão;'
									},
									"9":{
										"caput":'a Ordem do Dia - requerimento de:'
									},
									"10":{
										"caput":'inclusão, em Ordem do Dia, de matéria em condições de nela figurar;'
									},
									"11":{
										"caput":'dispensa de publicação de redação final para imediata deliberação do Plenário;'
									},
									"12":{
										"caput":'fase da sessão em que a matéria respectiva foi anunciada - requerimento de:'
									},
									"13":{
										"caput":'adiamento de discussão ou votação;'
									},
									"14":{
										"caput":'encerramento de discussão;'
									},
									"15":{
										"caput":'dispensa de discussão;'
									},
									"16":{
										"caput":'votação por determinado processo;'
									},
									"17":{
										"caput":'votação em globo ou parcelada;'
									},
									"18":{
										"caput":'destaque de dispositivo ou emenda para aprovação, rejeição, votação em separado ou constituição de proposição autônoma;'
									},
									"19":{
										"caput":'retirada de proposição constante da Ordem do Dia;'
									},
									"20":{
										"caput":'qualquer fase da sessão - requerimento de:'
									},
									"21":{
										"caput":'leitura de qualquer matéria sujeita ao conhecimento do Senado;'
									},
									"22":{
										"caput":'permissão para falar sentado;'
									},
									"23":{
										"caput":'do término da sessão, requerimento de prorrogação desta.'
									},
							}
						}]
					},
			}
		}]
	},
	"236":{
		"caput":'As proposições devem ser escritas em termos concisos e claros e divididas, sempre que possível, em artigos, parágrafos, incisos e alíneas.'
	},
	"237":{
		"caput":'Os projetos, pareceres e indicações devem ser encimados por ementa.'
	},
	"238":{
		"caput":'As proposições, salvo os requerimentos, devem ser acompanhadas de justificação oral ou escrita, observado o disposto no parágrafo único do art. 233.',
		"enumeravel": [{
			"tipo":"paragrafo",
			"lista":{
					"1":{
						"caput":'Havendo várias emendas do mesmo autor, dependentes de justificação oral, é lícito justificá-las em conjunto.'
					},
			}
		}]
	},
	"239":{
		"caput":'Qualquer proposição autônoma será sempre acompanhada de transcrição, na íntegra ou em resumo, das disposições de lei invocadas em seu texto.'
	},
	"240":{
		"caput":'As matérias constantes de projeto de lei rejeitado somente poderão ser objeto de novo projeto, na mesma sessão legislativa, mediante proposta da maioria absoluta dos membros do Senado (Const., art. 67).'
	},
	"241":{
		"caput":'As proposições que devam ser objeto de imediata deliberação do Plenário serão lidas integralmente, sendo as demais anunciadas em súmula.'
	},
	"242":{
		"caput":'O projeto ou requerimento de autoria individual de Senador, salvo requerimento de licença e de autorização para o desempenho de missão, só será lido quando presente seu autor.'
	},
	"243":{
		"caput":'Considera-se autor da proposição o seu primeiro signatário quando a Constituição ou este Regimento não exija, para a sua apresentação, número determinado de subscritores, não se considerando, neste último caso, assinaturas de apoiamento.'
	},
	"244":{
		"caput":'Ao signatário de proposição só é lícito dela retirar sua assinatura antes da publicação.',
		"enumeravel": [{
			"tipo":"paragrafo",
			"lista":{
					"1":{
						"caput":'Nos casos de proposição dependente de número mínimo de subscritores, se, com a retirada de assinatura, esse limite não for alcançado, o Presidente a devolverá ao primeiro signatário, dando conhecimento do fato ao Plenário.'
					},
			}
		}]
	},
	"245":{
		"caput":'Considera-se de comissão a proposição que, com esse caráter, for por ela apresentada.',
		"enumeravel": [{
			"tipo":"paragrafo",
			"lista":{
					"1":{
						"caput":'A proposição de comissão deve ser assinada pelo seu Presidente e instruída com a lista dos presentes à reunião em que ocorreu sua apresentação, totalizando pelo menos a maioria de seus membros.'
					},
			}
		}]
	},
	"246":{
		"caput":'As proposições serão numeradas de acordo com as seguintes normas:',
		"enumeravel": [{
			"tipo":"inciso",
			"lista":{
					"1":{
						"caput":'terão numeração anual, em séries específicas:',
						"enumeravel": [{
							"tipo":"letra",
							"lista":{
									"1":{
										"caput":'propostas de emenda à Constituição;'
									},
									"2":{
										"caput":'projetos de lei da Câmara;'
									},
									"3":{
										"caput":'projetos de lei do Senado;'
									},
									"4":{
										"caput":'projetos de decreto legislativo, com especificação da Casa de origem;'
									},
									"5":{
										"caput":'projetos de resolução;'
									},
									"6":{
										"caput":'requerimentos;'
									},
									"7":{
										"caput":'indicações;'
									},
									"8":{
										"caput":'pareceres.'
									},
							}
						}]
					},
					"2":{
						"caput":'as emendas serão numeradas, em cada turno, pela ordem dos artigos da proposição emendada, guardada a sequência determinada pela sua natureza, a saber: supressivas, substitutivas, modificativas e aditivas;'
					},
					"3":{
						"caput":'as subemendas de comissão figurarão ao fim da série das emendas de sua iniciativa, subordinadas ao título “subemendas”, com a indicação das emendas a que correspondam. Quando'
					},
					"4":{
						"caput":'emenda forem apresentadas várias subemendas, estas terão numeração ordinal em relação à emenda respectiva;'
					},
					"5":{
						"caput":'as emendas da Câmara serão anexadas ao processo do projeto primitivo e tramitarão com o número deste.'
					},
			}
		}]
	},
	"247":{
		"caput":'A proposição apresentada em plenário só será submetida a apoia-mento por solicitação de qualquer Senador.'
	},
	"248":{
		"caput":'A votação de apoiamento não será encaminhada, salvo se algum Senador pedir a palavra para combatê-lo, caso em que o encaminhamento ficará adstrito a um Senador de cada partido ou bloco parlamentar.',
		"enumeravel": [{
			"tipo":"paragrafo",
			"lista":{
					"1":{
						"caput":'O quorum para aprovação do apoiamento é de um décimo da composição do Senado.'
					},
			}
		}]
	},
	"249":{
		"caput":'Toda proposição apresentada ao Senado será publicada no Diário do Senado Federal, na íntegra, acompanhada, quando for o caso, da justificação e da legislação citada.'
	},
	"250":{
		"caput":'Será publicado em avulso eletrônico, para distribuição aos Senadores e comissões, o texto de toda proposição apresentada ao Senado.',
		"enumeravel": [{
			"tipo":"paragrafo",
			"lista":{
					"1":{
						"caput":'Ao fim da fase de instrução da matéria, serão publicados em avulsos eletrônicos os pareceres proferidos, neles se incluindo:',
						"enumeravel": [{
							"tipo":"inciso",
							"lista":{
									"1":{
										"caput":'o texto das emendas, caso não tenham sido publicadas em avulso eletrônico especial;'
									},
									"2":{
										"caput":'os votos em separado;'
									},
									"3":{
										"caput":'as informações prestadas sobre a matéria pelos órgãos consultados;'
									},
									"4":{
										"caput":'os relatórios e demais documentos referidos no art. 261, § 1º.'
									},
							}
						}]
					},
			}
		}]
	},
	"251":{
		"caput":'Cada proposição, salvo emenda, terá curso próprio.'
	},
	"252":{
		"caput":'Lida perante o Plenário, a proposição será objeto:',
		"enumeravel": [{
			"tipo":"inciso",
			"lista":{
					"1":{
						"caput":'de decisão da Mesa, no caso do art. 215, I;'
					},
					"2":{
						"caput":'de decisão do Presidente, nos casos do art. 214, parágrafo único, e art. 215, II;'
					},
					"3":{
						"caput":'de deliberação de comissão, na forma do art. 91;'
					},
					"4":{
						"caput":'de deliberação do Plenário, nos demais casos.'
					},
			}
		}]
	},
	"253":{
		"caput":'Antes da deliberação do Plenário, haverá manifestação das comissões competentes para estudo da matéria.',
		"enumeravel": [{
			"tipo":"paragrafo",
			"lista":{
					"1":{
						"caput":'Quando se tratar de requerimento, só serão submetidos à apreciação das comissões os seguintes:',
						"enumeravel": [{
							"tipo":"inciso",
							"lista":{
									"1":{
										"caput":'(Revogado);'
									},
									"2":{
										"caput":'de sobrestamento do estudo de proposição (art. 335, parágrafo único).'
									},
							}
						}]
					},
			}
		}]
	},
	"254":{
		"caput":'Quando os projetos receberem pareceres contrários, quanto ao mérito, serão tidos como rejeitados e arquivados definitivamente, salvo recurso de um décimo dos membros do Senado no sentido de sua tramitação.',
		"enumeravel": [{
			"tipo":"paragrafo",
			"lista":{
					"1":{
						"caput":'A comunicação do arquivamento será feita pelo Presidente, em plenário, podendo o recurso ser apresentado no prazo de dois dias úteis contado da comunicação.'
					},
			}
		}]
	},
	"255":{
		"caput":'A deliberação do Senado será:',
		"enumeravel": [{
			"tipo":"inciso",
			"lista":{
					"1":{
						"caput":'na mesma sessão, após a matéria constante da Ordem do Dia, nos requerimentos que solicitem:',
						"enumeravel": [{
							"tipo":"letra",
							"lista":{
									"1":{
										"caput":'no caso do art. 336, II;'
									},
									"2":{
										"caput":'de sessão deliberativa extraordinária, especial ou secreta;'
									},
									"3":{
										"caput":''
									},
							}
						}]
					},
					"2":{
						"caput":'mediante inclusão em Ordem do Dia, quando se tratar de:',
						"enumeravel": [{
							"tipo":"letra",
							"lista":{
									"1":{
										"caput":''
									},
									"2":{
										"caput":''
									},
									"3":{
										"caput":'de:'
									},
									"4":{
										"caput":'urgência do art. 336, III;'
									},
									"5":{
										"caput":'(Revogado);'
									},
									"6":{
										"caput":'inclusão em Ordem do Dia de matéria que não tenha recebido parecer no prazo regimental (art. 172, I);'
									},
									"7":{
										"caput":'audiência de comissão que não tenha oferecido parecer no prazo regimental (art. 119, parágrafo único);'
									},
									"8":{
										"caput":'dispensa de parecer da comissão que haja esgotado o prazo a ela destinado (art. 119, caput);'
									},
									"9":{
										"caput":'constituição de comissão temporária;'
									},
									"10":{
										"caput":'voto de aplauso, congratulações, louvor, solidariedade ou censura (arts. 222);'
									},
									"11":{
										"caput":'tramitação em conjunto, de projetos regulando a mesma matéria, quando houver parecer aprovado em comissão (art. 258, parágrafo único, in fine);'
									},
									"12":{
										"caput":'comparecimento, ao plenário, de Ministro de Estado e titular de órgão diretamente subordinado ao Presidente da República (Const., art. 50);',
										"enumeravel": [{
											"tipo":"numero",
											"lista":{
													"1":{
														"caput":'retirada de proposição com parecer de comissão;'
													},
													"2":{
														"caput":'sobrestamento do estudo de proposição;'
													},
													"3":{
														"caput":'remessa a determinada comissão de matéria despachada a outra.',
														"enumeravel": [{
															"tipo":"inciso",
															"lista":{
																	"1":{
																		"caput":'imediata, nos requerimentos não constantes dos incisos I e II.'
																	},
															}
														}]
													},
													"4":{
														"caput":'Ao ser anunciado o requerimento constante do inciso II, c, 3, será dada a palavra ao Presidente da Comissão em que se ache o projeto para se manifestar sobre a providência requerida.'
													},
											}
										}]
									},
							}
						}]
					},
			}
		}]
	},
	"256":{
		"caput":'A retirada de proposições em curso no Senado é permitida:',
		"enumeravel": [{
			"tipo":"inciso",
			"lista":{
					"1":{
						"caput":'a de autoria de um ou mais Senadores, mediante requerimento do único signatário ou do primeiro deles;'
					},
					"2":{
						"caput":'a de autoria de comissão, mediante requerimento de seu Presidente ou do Relator da matéria, com a declaração expressa de que assim procede devidamente autorizado.'
					},
			}
		}]
	},
	"257":{
		"caput":'Quando, na Comissão de Constituição, Justiça e Cidadania, o relator se pronunciar pela inconstitucionalidade ou injuridicidade da proposição, é permitida sua retirada, antes de proferido o parecer definitivo, mediante requerimento ao Presidente da Comissão, que, o deferindo, encaminhará a matéria à Mesa, através de ofício, a fim de ser arquivada.'
	},
	"258":{
		"caput":'Havendo em curso no Senado duas ou mais proposições regulando a mesma matéria, é lícito promover sua tramitação em conjunto a partir de requerimento de comissão ou de Senador, mediante deliberação da Mesa, salvo as que já foram objeto de parecer aprovado em comissão ou que constem da Ordem do Dia.',
		"enumeravel": [{
			"tipo":"paragrafo",
			"lista":{
					"1":{
						"caput":'Os requerimentos de tramitação conjunta de matérias que já constem da Ordem do Dia ou que tenham parecer aprovado em comissão serão submetidos à deliberação do Plenário.'
					},
			}
		}]
	},
	"259":{
		"caput":'Aprovado o requerimento de tramitação conjunta, os projetos serão remetidos à Comissão de Constituição, Justiça e Cidadania, se sobre algum deles for necessária a apreciação dos aspectos constitucional e jurídico, ou à comissão a que tenham sido distribuídos, para apreciação do mérito.'
	},
	"260":{
		"caput":'Na tramitação em conjunto, serão obedecidas as seguintes normas:',
		"enumeravel": [{
			"tipo":"inciso",
			"lista":{
					"1":{
						"caput":'ao processo do projeto que deva ter precedência serão apensos, sem incorporações, os dos demais;'
					},
					"2":{
						"caput":'terá precedência:',
						"enumeravel": [{
							"tipo":"letra",
							"lista":{
									"1":{
										"caput":'projeto da Câmara sobre o do Senado;'
									},
									"2":{
										"caput":'mais antigo sobre o mais recente, quando originários da mesma Casa;'
									},
							}
						}]
					},
					"3":{
						"caput":'em qualquer caso, a proposição será incluída, em série, com as demais, na Ordem do Dia, obedecido, no processamento dos pareceres, o disposto no art. 268.'
					},
			}
		}]
	},
	"261":{
		"caput":'O processo referente a cada proposição, salvo emenda, será organizado de acordo com as seguintes normas:',
		"enumeravel": [{
			"tipo":"inciso",
			"lista":{
					"1":{
						"caput":'será autuada a proposição principal, consignando-se na respectiva capa, no ato da organização do processo:',
						"enumeravel": [{
							"tipo":"letra",
							"lista":{
									"1":{
										"caput":'natureza da proposição;'
									},
									"2":{
										"caput":'Casa de origem;'
									},
									"3":{
										"caput":'número;'
									},
									"4":{
										"caput":'ano de apresentação;'
									},
									"5":{
										"caput":'ementa completa;'
									},
									"6":{
										"caput":'autor, quando do Senado;'
									},
							}
						}]
					},
					"2":{
						"caput":'em seguida à capa figurarão folhas avulsas, de impresso especial, conforme modelo aprovado pela Comissão Diretora, em duas vias, para original e cópia, constituindo estas últimas os boletins de ação legislativa que irão fornecer informações ao Centro de Processamento de Dados, para registro das matérias em tramitação; e ainda:',
						"enumeravel": [{
							"tipo":"letra",
							"lista":{
									"1":{
										"caput":'projetos da Câmara:'
									},
									"2":{
										"caput":'o ofício de encaminhamento;'
									},
									"3":{
										"caput":'o autógrafo recebido e os documentos que o tiverem acompanhado;'
									},
									"4":{
										"caput":'o resumo da tramitação na Casa de origem;'
									},
									"5":{
										"caput":'um exemplar de cada avulso eletrônico;'
									},
									"6":{
										"caput":'as demais vias dos avulsos eletrônicos e de outros documentos, em sobrecarta anexada ao processo;'
									},
									"7":{
										"caput":'projetos do Senado:'
									},
									"8":{
										"caput":'o texto, a justificação e a legislação citada, quando houver;'
									},
									"9":{
										"caput":'o recorte do Diário do Senado Federal, com a justificação oral, quando houver;'
									},
									"10":{
										"caput":'os documentos que o acompanhem;'
									},
									"11":{
										"caput":'as duplicatas do projeto e dos demais documentos, em sobrecarta anexada ao processo;'
									},
							}
						}]
					},
					"3":{
						"caput":'as peças do processo serão numeradas e rubricadas no Serviço de Protocolo Legislativo antes de seu encaminhamento à Secretaria-Geral da Mesa, para leitura da matéria em plenário;'
					},
					"4":{
						"caput":'serão ainda registradas, no impresso especial, pelo funcionário do órgão por onde passar o processo, todas as ações legislativas e administrativas que ocorrerem durante sua tramitação:',
						"enumeravel": [{
							"tipo":"letra",
							"lista":{
									"1":{
										"caput":'ocorrências da tramitação em cada comissão, o encaminhamento à seguinte e, finalmente, à Mesa;'
									},
									"2":{
										"caput":'inclusão em Ordem do Dia;'
									},
									"3":{
										"caput":'tramitação em plenário;'
									},
									"4":{
										"caput":'manifestação do Senado sobre a matéria;'
									},
									"5":{
										"caput":'remessa à sanção, à promulgação ou à Câmara;'
									},
									"6":{
										"caput":'transformação em lei, decreto legislativo ou resolução, com o número e data respectivos;'
									},
									"7":{
										"caput":'houver veto, todas as ocorrências a ele relacionadas;'
									},
									"8":{
										"caput":'despacho do arquivamento;'
									},
									"9":{
										"caput":'desarquivamentos e novos incidentes;'
									},
							}
						}]
					},
					"5":{
						"caput":'o Serviço de Protocolo Legislativo, ao receber o processo, em qualquer oportunidade, atualizará a numeração das páginas que deverão ser rubricadas pelo funcionário responsável.'
					},
			}
		}]
	},
	"262":{
		"caput":'Relativamente aos documentos de natureza sigilosa, observar-se-ão as normas constantes dos arts. 144 e 157, II e III, e, terminado o curso da matéria, serão recolhidos ao arquivo especial dos documentos com esse caráter, em sobrecarta fechada, rubricada pelo Presidente da Mesa, feita na capa do processo a devida anotação.'
	},
	"263":{
		"caput":'As representações dirigidas à Mesa, contendo observações, sugestões ou solicitações sobre proposições em curso no Senado, serão lidas no Período do Expediente, publicadas, em súmula ou na íntegra, no Diário do Senado Federal, reunidas em processo especial e encaminhadas às respectivas comissões para conhecimento dos relatores e consulta dos demais membros, acompanhando a proposição em todas as suas fases.',
		"enumeravel": [{
			"tipo":"paragrafo",
			"lista":{
					"1":{
						"caput":'É facultado aos Senadores encaminhar ao órgão competente as representações que receberem, para anexação ao processo.'
					},
			}
		}]
	},
	"264":{
		"caput":'Ao ser arquivada a proposição, ser-lhe-á anexada uma coleção dos avulsos eletrônicos publicados para sua instrução no Senado e na Câmara, quando for o caso.'
	},
	"265":{
		"caput":'A decisão do Plenário, apoiando, aprovando, rejeitando proposição ou destacando emenda para constituir projeto em separado, será anotada, com a data respectiva, no texto votado, e assinada pela Presidência.'
	},
	"266":{
		"caput":'O processo da proposição ficará sobre a mesa durante sua tramitação em plenário.'
	},
	"267":{
		"caput":'Ocorrendo extravio de qualquer proposição, a Presidência determinará providências objetivando sua reconstituição, de ofício ou mediante requerimento de qualquer Senador ou comissão, independentemente de deliberação do Plenário.',
		"enumeravel": [{
			"tipo":"paragrafo",
			"lista":{
					"1":{
						"caput":'Quando se tratar de projeto da Câmara, a Mesa solicitará, da Casa de origem, a remessa de cópias autenticadas dos respectivos autógrafos e documentos que o tenham acompanhado.'
					},
					"2":{
						"caput":'Os pareceres já proferidos no Senado serão anexados ao novo processo em cópias autenticadas pelos Presidentes das respectivas comissões.'
					},
					"3":{
						"caput":'A reconstituição do processo deverá ser feita pelo órgão onde este se encontrava por ocasião de seu extravio.'
					},
			}
		}]
	},
	"268":{
		"caput":'Quando a comissão, no mesmo parecer, se referir a várias proposições autônomas, o original dele instruirá o processo da proposição preferencial, sendo aos demais anexadas cópias autenticadas pelo respectivo Presidente.'
	},
	"269":{
		"caput":'A Presidência fará publicar:',
		"enumeravel": [{
			"tipo":"inciso",
			"lista":{
					"1":{
						"caput":'no princípio de cada sessão legislativa, a sinopse de todas as proposições em curso ou resolvidas pelo Senado na sessão anterior;'
					},
					"2":{
						"caput":'mensalmente, a resenha das matérias rejeitadas e as enviadas, no mês anterior, à sanção, à promulgação e à Câmara.'
					},
			}
		}]
	},
	"270":{
		"caput":'As proposições em curso no Senado são subordinadas, em sua apreciação, a um único turno de discussão e votação, salvo proposta de emenda à Constituição.',
		"enumeravel": [{
			"tipo":"paragrafo",
			"lista":{
					"1":{
						"caput":'Havendo substitutivo integral, aprovado pelo Plenário no turno único, o projeto será submetido a turno suplementar.'
					},
			}
		}]
	},
	"271":{
		"caput":'Cada turno é constituído de discussão e votação.'
	},
	"272":{
		"caput":'A discussão da proposição principal e das emendas será em conjunto.'
	},
	"273":{
		"caput":'Anunciada a matéria, será dada a palavra aos oradores para a discussão.'
	},
	"274":{
		"caput":'A discussão não será interrompida, salvo para:',
		"enumeravel": [{
			"tipo":"inciso",
			"lista":{
					"1":{
						"caput":'formulação de questão de ordem;'
					},
					"2":{
						"caput":'adiamento para os fins previstos no art. 279;'
					},
					"3":{
						"caput":'tratar de proposição compreendida no art. 336, I;'
					},
					"4":{
						"caput":'os casos previstos no art. 305;'
					},
					"5":{
						"caput":'comunicação importante ao Senado;'
					},
					"6":{
						"caput":'recepção de visitante;'
					},
					"7":{
						"caput":'votação de requerimento de prorrogação da sessão;'
					},
					"8":{
						"caput":'ser suspensa a sessão (art. 18, I, f).'
					},
			}
		}]
	},
	"275":{
		"caput":'Encerra-se a discussão:',
		"enumeravel": [{
			"tipo":"inciso",
			"lista":{
					"1":{
						"caput":'pela ausência de oradores;'
					},
					"2":{
						"caput":'por deliberação do Plenário, a requerimento de qualquer Senador, quando já houverem falado, pelo menos, três Senadores a favor e três contra.'
					},
			}
		}]
	},
	"276":{
		"caput":'As proposições com pareceres favoráveis poderão ter a discussão dispensada por deliberação do Plenário, mediante requerimento de líder.',
		"enumeravel": [{
			"tipo":"paragrafo",
			"lista":{
					"1":{
						"caput":'A dispensa da discussão deverá ser requerida ao ser anunciada a matéria.'
					},
			}
		}]
	},
	"277":{
		"caput":'Lidos os pareceres das comissões sobre as proposições, em turno único, e publicados em avulsos eletrônicos, abrir-se-á o prazo de cinco dias úteis para apresentação de emendas, findo o qual a matéria, se emendada, voltará às comissões para exame.',
		"enumeravel": [{
			"tipo":"paragrafo",
			"lista":{
					"1":{
						"caput":'Não sendo emendada, a proposição estará em condições de figurar em Ordem do Dia, obedecido o interstício regimental.'
					},
			}
		}]
	},
	"278":{
		"caput":'Lidos os pareceres sobre as emendas, publicados no Diário do Senado Federal e em avulso eletrônico, estará a matéria em condições de figurar em Ordem do Dia, obedecido o interstício regimental.'
	},
	"279":{
		"caput":'A discussão, salvo nos projetos em regime de urgência e o disposto no art. 349, poderá ser adiada, mediante deliberação do Plenário, a requerimento de qualquer Senador ou comissão, para os seguintes fins:',
		"enumeravel": [{
			"tipo":"inciso",
			"lista":{
					"1":{
						"caput":'audiência de comissão que sobre ela não se tenha manifestado;'
					},
					"2":{
						"caput":'reexame por uma ou mais comissões por motivo justificado;'
					},
					"3":{
						"caput":'ser realizada em dia determinado;'
					},
					"4":{
						"caput":'preenchimento de formalidade essencial;'
					},
					"5":{
						"caput":'diligência considerada imprescindível ao seu esclarecimento.'
					},
			}
		}]
	},
	"280":{
		"caput":'É de três dias úteis o interstício entre a publicação de avulsos eletrônicos dos pareceres das comissões e o início da discussão ou votação correspondente.'
	},
	"281":{
		"caput":'A dispensa de interstício e prévia publicação de avulso eletrônico, para inclusão de matéria em Ordem do Dia, poderá ser concedida por deliberação do Plenário, a requerimento de qualquer Senador, desde que a proposição esteja há mais de cinco dias em tramitação no Senado.'
	},
	"282":{
		"caput":'Sempre que for aprovado substitutivo integral a projeto de lei, de decreto legislativo ou de resolução em turno único, será ele submetido a turno suplementar.',
		"enumeravel": [{
			"tipo":"paragrafo",
			"lista":{
					"1":{
						"caput":'Nos projetos sujeitos a prazo de tramitação, o turno suplementar realizar-se-á dois dias úteis após a aprovação do substitutivo, se faltarem oito dias, ou menos, para o término do referido prazo.'
					},
					"2":{
						"caput":'Poderão ser oferecidas emendas no turno suplementar, por ocasião da discussão da matéria, vedada a apresentação de novo substitutivo integral.'
					},
			}
		}]
	},
	"283":{
		"caput":'Se forem oferecidas emendas, na discussão suplementar, a matéria irá às comissões competentes, que não poderão concluir seu parecer por novo substitutivo.',
		"enumeravel": [{
			"tipo":"paragrafo",
			"lista":{
					"1":{
						"caput":'Nos projetos sujeitos a prazo de tramitação, a matéria será incluída em Ordem do Dia na sessão deliberativa ordinária seguinte se faltarem cinco dias, ou menos, para o término do referido prazo, podendo o parecer ser proferido em plenário.'
					},
			}
		}]
	},
	"284":{
		"caput":'Não sendo oferecidas emendas na discussão suplementar, o substitutivo será dado como definitivamente adotado sem votação.'
	},
	"285":{
		"caput":'A emenda da Câmara a projeto do Senado não é suscetível de modificação por meio de subemenda.'
	},
	"286":{
		"caput":'A discussão e a votação das emendas da Câmara a projeto do Senado far-se-ão em globo, exceto:',
		"enumeravel": [{
			"tipo":"inciso",
			"lista":{
					"1":{
						"caput":'se qualquer comissão manifestar-se favoravelmente a umas e contrariamente a outras, caso em que a votação se fará em grupos, segundo os pareceres;'
					},
					"2":{
						"caput":'se for aprovado destaque para a votação de qualquer emenda.'
					},
			}
		}]
	},
	"287":{
		"caput":'O substitutivo da Câmara a projeto do Senado será considerado série de emendas e votado, separadamente, por artigos, parágrafos, incisos, alíneas e itens, em correspondência aos do projeto emendado, salvo aprovação de requerimento para votação em globo ou por grupos de dispositivos, obedecido o disposto no parágrafo único do art. 286.'
	},
	"288":{
		"caput":'As deliberações do Senado serão tomadas por maioria de votos, presente a maioria absoluta dos seus membros (Const., art. 47), salvo nos seguintes casos, em que serão:',
		"enumeravel": [{
			"tipo":"inciso",
			"lista":{
					"1":{
						"caput":'por voto favorável de dois terços da composição da Casa:',
						"enumeravel": [{
							"tipo":"letra",
							"lista":{
									"1":{
										"caput":'condenatória nos casos previstos no art. 52, I e II, da Constituição;'
									},
									"2":{
										"caput":'de alíquotas máximas nas operações internas, para resolver conflito específico que envolva interesse de Estados e do Distrito Federal (Const., art. 155, § 2º, V, b);'
									},
									"3":{
										"caput":'de imunidade de Senadores, durante o estado de sítio (Const., art. 53, § 8º);'
									},
							}
						}]
					},
					"2":{
						"caput":'por voto favorável de três quintos da composição da Casa, proposta de emenda à Constituição (Const., art. 60, § 2º);'
					},
					"3":{
						"caput":'por voto favorável da maioria absoluta da composição da Casa:',
						"enumeravel": [{
							"tipo":"letra",
							"lista":{
									"1":{
										"caput":'de lei complementar (Const., art. 69);'
									},
									"2":{
										"caput":'de ofício, do Procurador-Geral da República (Const., art. 52, XI);'
									},
									"3":{
										"caput":'de mandato de Senador, nos casos previstos no art. 55, § 2º, da Constituição;'
									},
									"4":{
										"caput":'de nome indicado para Ministro do Supremo Tribunal Federal (Const., art. 101, parágrafo único) para Procurador-Geral da República (Const., art. 128, § 1º); para Ministro do Superior Tribunal de Justiça (Const., art. 104, parágrafo único) e para Ministro do Tribunal Superior do Trabalho (Const., art. 111-A);'
									},
									"5":{
										"caput":'de ato do Presidente da República que decretar o estado de defesa (Const., art. 136, § 4º);'
									},
									"6":{
										"caput":'para o Presidente da República decretar o estado de sítio (Const., art. 137, parágrafo único);'
									},
									"7":{
										"caput":'de alíquotas aplicáveis às operações e prestações interestaduais e de exportação (Const., art. 155, § 2º, IV);'
									},
									"8":{
										"caput":'de alíquotas mínimas nas operações internas (Const., art. 155, § 2º, V, a);'
									},
									"9":{
										"caput":'de operações de crédito que excedam o montante das despesas de capital, mediante créditos suplementares ou especiais específicos (Const. art. 167, III);'
									},
									"10":{
										"caput":'de nome indicado para Defensor Público-Geral Federal;'
									},
									"11":{
										"caput":''
									},
									"12":{
										"caput":'de nome indicado para o Conselho Nacional de Justiça (Const., art. 103-B, caput e § 2º);'
									},
									"13":{
										"caput":'de nome indicado para o Conselho Nacional do Ministério Público (Const., art. 130-A, caput);'
									},
							}
						}]
					},
					"4":{
						"caput":'por voto favorável de dois quintos da composição da Casa, aprovação da não renovação da concessão ou permissão para o serviço de radiodifusão sonora e de sons e imagens (Const., art. 223, § 2º);'
					},
					"5":{
						"caput":'por maioria de votos, presentes um décimo dos Senadores, nos requerimentos compreendidos no art. 215, III.'
					},
			}
		}]
	},
	"289":{
		"caput":'A votação poderá ser ostensiva ou secreta.'
	},
	"290":{
		"caput":'Será ostensiva a votação das proposições em geral.'
	},
	"291":{
		"caput":'Será secreta a votação:',
		"enumeravel": [{
			"tipo":"inciso",
			"lista":{
					"1":{
						"caput":'quando o Senado tiver que deliberar sobre:',
						"enumeravel": [{
							"tipo":"letra",
							"lista":{
									"1":{
										"caput":'de ofício, do Procurador-Geral da República (Const., art. 52, XI);'
									},
									"2":{
										"caput":'de mandato de Senador, nos casos previstos no art. 55, § 2º, da Constituição;'
									},
									"3":{
										"caput":'de Senador e autorização da formação de culpa, no caso de flagrante de crime inafiançável (Const., art. 53, § 2º);'
									},
									"4":{
										"caput":'das imunidades de Senador durante o estado de sítio (Const., art. 53, § 8º);'
									},
									"5":{
										"caput":'de autoridades (Const., art. 52, III);'
									},
							}
						}]
					},
					"2":{
						"caput":'nas eleições;'
					},
					"3":{
						"caput":'por determinação do Plenário.'
					},
			}
		}]
	},
	"292":{
		"caput":'Na votação, serão adotados os seguintes processos:',
		"enumeravel": [{
			"tipo":"inciso",
			"lista":{
					"1":{
						"caput":'ostensiva:',
						"enumeravel": [{
							"tipo":"letra",
							"lista":{
									"1":{
										"caput":''
									},
									"2":{
										"caput":''
									},
							}
						}]
					},
					"2":{
						"caput":'secreta:',
						"enumeravel": [{
							"tipo":"letra",
							"lista":{
									"1":{
										"caput":''
									},
									"2":{
										"caput":'meio de cédulas;'
									},
									"3":{
										"caput":'meio de esfera.'
									},
							}
						}]
					},
			}
		}]
	},
	"293":{
		"caput":'No processo simbólico observar-se-ão as seguintes normas:',
		"enumeravel": [{
			"tipo":"inciso",
			"lista":{
					"1":{
						"caput":'os Senadores que aprovarem a matéria deverão permanecer sentados, levantando-se os que votarem pela rejeição;'
					},
					"2":{
						"caput":'o voto dos líderes representará o de seus liderados presentes, permitida a declaração de voto em documento escrito a ser encaminhado à Mesa para publicação;'
					},
					"3":{
						"caput":'se for requerida verificação da votação, será ela repetida pelo processo nominal;'
					},
					"4":{
						"caput":'o requerimento de verificação de votação só será admissível se apoiado por três Senadores;'
					},
					"5":{
						"caput":'procedida a verificação de votação e constatada a existência de número, não será permitida nova verificação antes do decurso de uma hora;'
					},
					"6":{
						"caput":'não será admitido requerimento de verificação se a Presidência já houver anunciado a matéria seguinte;'
					},
					"7":{
						"caput":'antes de anunciado o resultado, será lícito tomar o voto do Senador que penetrar no recinto após a votação;'
					},
					"8":{
						"caput":'verificada a falta de quorum, o Presidente suspenderá a sessão, fazendo acionar as campainhas durante dez minutos, após o que esta será reaberta, procedendo-se a nova votação;'
					},
					"9":{
						"caput":'confirmada a falta de número, ficará adiada a votação, que será reiniciada ao voltar a matéria à deliberação do Plenário;'
					},
					"10":{
						"caput":'se, ao processar-se a verificação, os requerentes não estiverem presentes ou deixarem de votar, considerar-se-á como tendo dela desistido.'
					},
			}
		}]
	},
	"294":{
		"caput":'O processo nominal, que se utilizará nos casos em que seja exigido quorum especial de votação ou por deliberação do Plenário, a requerimento de qualquer Senador, ou ainda, quando houver pedido de verificação, far-se-á pelo registro eletrônico dos votos, obedecidas as seguintes normas:',
		"enumeravel": [{
			"tipo":"inciso",
			"lista":{
					"1":{
						"caput":'os nomes dos Senadores constarão de apregoadores instalados, lateralmente, no plenário, onde serão registrados individualmente:',
						"enumeravel": [{
							"tipo":"letra",
							"lista":{
									"1":{
										"caput":'sinal verde, os votos favoráveis;'
									},
									"2":{
										"caput":'sinal amarelo, as abstenções;'
									},
									"3":{
										"caput":'sinal vermelho, os votos contrários;'
									},
							}
						}]
					},
					"2":{
						"caput":'cada Senador terá lugar fixo, numerado, que ocupará ao ser anunciada a votação, devendo acionar dispositivo próprio de uso individual, localizado na respectiva bancada;'
					},
					"3":{
						"caput":'os líderes votarão em primeiro lugar;'
					},
					"4":{
						"caput":'conhecido o voto das lideranças, votarão os demais Senadores;'
					},
					"5":{
						"caput":'verificado, pelo registro no painel de controle localizado na mesa, que houve empate na votação, o Presidente comunicará o fato ao Plenário e a desempatará, transferindo, em seguida, o resultado aos apregoadores;'
					},
					"6":{
						"caput":'concluída a votação, o Presidente desligará o quadro, liberando o sistema para o processamento de nova votação;'
					},
					"7":{
						"caput":'o resultado da votação será encaminhado à Mesa em listagem especial, onde estarão registrados:',
						"enumeravel": [{
							"tipo":"letra",
							"lista":{
									"1":{
										"caput":'matéria objeto da deliberação;'
									},
									"2":{
										"caput":'data em que se procedeu a votação;'
									},
									"3":{
										"caput":'voto individual de cada Senador;'
									},
									"4":{
										"caput":'resultado da votação;'
									},
									"5":{
										"caput":'total dos votantes;'
									},
							}
						}]
					},
					"8":{
						"caput":'o Primeiro-Secretário rubricará a listagem especial, determinando sua anexação ao processo da matéria respectiva.'
					},
			}
		}]
	},
	"295":{
		"caput":'A votação secreta realizar-se-á pelo sistema eletrônico, salvo nas eleições.',
		"enumeravel": [{
			"tipo":"paragrafo",
			"lista":{
					"1":{
						"caput":'Anunciada a votação, o Presidente convidará os Senadores a acionarem o dispositivo próprio, dando, em seguida, início à fase de apuração.'
					},
					"2":{
						"caput":'Verificada a falta de quorum, proceder-se-á na forma do art. 293, VIII, ficando adiada a votação se ocorrer, novamente, falta de número.'
					},
			}
		}]
	},
	"296":{
		"caput":'A votação por meio de cédulas far-se-á nas eleições.'
	},
	"297":{
		"caput":'A votação por meio de esferas realizar-se-á quando o equipamento de votação eletrônico não estiver em condições de funcionar, obedecidas as seguintes normas:',
		"enumeravel": [{
			"tipo":"inciso",
			"lista":{
					"1":{
						"caput":'utilizar-se-ão esferas brancas, representando votos favoráveis, e pretas, representando votos contrários;'
					},
					"2":{
						"caput":'a esfera que for utilizada para exprimir voto será lançada em uma urna e a que não for usada, em outra que servirá para conferir o resultado da votação.'
					},
			}
		}]
	},
	"298":{
		"caput":'Terminada a apuração, o Presidente proclamará o resultado da votação, especificando os votos favoráveis, contrários, em branco, nulos e as abstenções.'
	},
	"299":{
		"caput":'A votação realizar-se-á imediatamente após a discussão, se este Regimento não dispuser noutro sentido.'
	},
	"300":{
		"caput":'Na votação, serão obedecidas as seguintes normas:',
		"enumeravel": [{
			"tipo":"inciso",
			"lista":{
					"1":{
						"caput":'votar-se-á em primeiro lugar o projeto, ressalvados os destaques dele requeridos e as emendas;'
					},
					"2":{
						"caput":'a votação do projeto, salvo deliberação do Plenário, será em globo, podendo a Presidência dividir a proposição, quando conveniente;'
					},
					"3":{
						"caput":'a votação das emendas que tenham pareceres concordantes de todas as comissões será feita em grupos, segundo o sentido dos pareceres, ressalvados os destaques; as demais e as destacadas serão votadas uma a uma, classificadas segundo a ordem estabelecida no art. 246, II;'
					},
					"4":{
						"caput":'no grupo das emendas de parecer favorável incluem-se as de comissão, quando sobre elas não haja manifestação em contrário de outra;'
					},
					"5":{
						"caput":'serão incluídas no grupo das emendas de parecer contrário aquelas sobre as quais se tenham manifestado pela rejeição as comissões competentes para o exame do mérito, embora consideradas constitucionais;'
					},
					"6":{
						"caput":'as emendas com subemendas serão votadas uma a uma, salvo deliberação do Plenário, mediante proposta de qualquer Senador ou comissão; aprovado o grupo, serão consideradas aprovadas as emendas com modificações constantes das respectivas subemendas;'
					},
					"7":{
						"caput":'a emenda com subemenda, quando votada separadamente, sê-lo-á antes e com ressalva desta, exceto nos seguintes casos, em que a subemenda terá precedência:',
						"enumeravel": [{
							"tipo":"letra",
							"lista":{
									"1":{
										"caput":'for supressiva;'
									},
									"2":{
										"caput":'for substitutiva de todo o texto da emenda;'
									},
									"3":{
										"caput":'for substitutiva de artigo da emenda e a votação desta se fizer por artigo;'
									},
							}
						}]
					},
					"8":{
						"caput":'o Plenário poderá conceder, a requerimento de qualquer Senador, que a votação das emendas se faça destacadamente, ou uma a uma;'
					},
					"9":{
						"caput":'serão votadas, destacadamente, as emendas com parecer no sentido de constituírem projeto em separado;'
					},
					"10":{
						"caput":'quando, ao mesmo dispositivo, forem apresentadas várias emendas da mesma natureza, terão preferência:',
						"enumeravel": [{
							"tipo":"letra",
							"lista":{
									"1":{
										"caput":'de comissões sobre as de Plenário;'
									},
									"2":{
										"caput":'as de comissões, a da que tiver maior competência para se manifestar sobre a matéria;'
									},
							}
						}]
					},
					"11":{
						"caput":'o dispositivo, destacado do projeto para votação em separado, precederá, na votação, as emendas e independerá de parecer;'
					},
					"12":{
						"caput":'se a votação do projeto se fizer separadamente em relação a cada artigo, o texto deste será votado antes das emendas a ele correspondentes, salvo se forem supressivas ou substitutivas;'
					},
					"13":{
						"caput":'terá preferência para votação o substitutivo que tiver pareceres favoráveis de todas as comissões, salvo se o Plenário deliberar noutro sentido;'
					},
					"14":{
						"caput":'havendo mais de um substitutivo, a precedência será regulada pela ordem inversa de sua apresentação, ressalvado o disposto no inciso X, em relação aos das comissões;'
					},
					"15":{
						"caput":'o substitutivo integral, salvo deliberação em contrário, será votado em globo;'
					},
					"16":{
						"caput":'aprovado o substitutivo integral, ficam prejudicados o projeto e as emendas a ele oferecidas;'
					},
					"17":{
						"caput":'anunciada a votação de dispositivo ou emenda destacada, se o autor do requerimento de destaque não pedir a palavra para encaminhá-la, considerarse-á como tendo o Plenário concordado com o parecer da comissão, tomando a matéria destacada a sorte das demais constantes do grupo a que pertencer;'
					},
					"18":{
						"caput":'não será submetida a votos emenda declarada inconstitucional ou injurídica pela Comissão de Constituição, Justiça e Cidadania, salvo se, não sendo unânime o parecer, o requererem líderes que representem, no mínimo, a maioria da composição do Senado.'
					},
			}
		}]
	},
	"301":{
		"caput":'A rejeição do projeto prejudica as emendas a ele oferecidas.'
	},
	"302":{
		"caput":'A rejeição do art. 1º do projeto, votado artigo por artigo, prejudica os demais quando eles forem uma consequência daquele.'
	},
	"303":{
		"caput":'A votação não se interrompe senão por falta de quorum e, observado o disposto nos arts. 178 e 179, pelo término da sessão.'
	},
	"304":{
		"caput":'Ocorrendo falta de número para as deliberações, passar-se-á à matéria em discussão.',
		"enumeravel": [{
			"tipo":"paragrafo",
			"lista":{
					"1":{
						"caput":'Esgotada a matéria em discussão e persistindo a falta de número, a Presidência poderá, no caso de figurar na Ordem do Dia matéria que pela sua relevância o justifique, suspender a sessão por prazo não superior a uma hora, ou conceder a palavra a Senador que dela queira fazer uso.'
					},
			}
		}]
	},
	"305":{
		"caput":'Sobrevindo, posteriormente, a existência de número, voltar-se-á à matéria em votação, interrompendo-se o orador que estiver na tribuna, salvo se estiver discutindo proposição em regime de urgência e a matéria a votar estiver em tramitação normal.'
	},
	"306":{
		"caput":'Nenhum Senador presente à sessão poderá escusar-se de votar, salvo quando se tratar de assunto em que tenha interesse pessoal, devendo declarar o impedimento antes da votação e sendo a sua presença computada para efeito de quorum.'
	},
	"307":{
		"caput":'Em caso de votação secreta, havendo empate, proceder-se-á a nova votação; persistindo o empate, a votação será renovada na sessão seguinte ou nas subsequentes, até que se dê o desempate.'
	},
	"308":{
		"caput":'Anunciada a votação de qualquer matéria, é lícito ao Senador usar da palavra por cinco minutos para encaminhá-la.'
	},
	"309":{
		"caput":'O encaminhamento é medida preparatória da votação; a votação só se considera iniciada após o término do encaminhamento.'
	},
	"310":{
		"caput":'Não terão encaminhamento de votação as eleições e os seguintes requerimentos:',
		"enumeravel": [{
			"tipo":"inciso",
			"lista":{
					"1":{
						"caput":'de permissão para falar sentado;'
					},
					"2":{
						"caput":'de prorrogação do tempo da sessão;'
					},
					"3":{
						"caput":'de prorrogação de prazo para apresentação de parecer;'
					},
					"4":{
						"caput":'de dispensa de interstício e prévia publicação de avulso eletrônico para inclusão de determinada matéria em Ordem do Dia;'
					},
					"5":{
						"caput":'de dispensa de publicação de redação final para sua imediata apreciação;'
					},
					"6":{
						"caput":'de Senador, solicitando de órgão estranho ao Senado a remessa de documentos;'
					},
					"7":{
						"caput":'de comissão ou Senador, solicitando informações oficiais;'
					},
					"8":{
						"caput":'de comissão ou Senador, solicitando a publicação, no Diário do Senado Federal, de informações oficiais;'
					},
					"9":{
						"caput":'de licença de Senador;'
					},
					"10":{
						"caput":'de remessa a determinada comissão de matéria despachada a outra;'
					},
					"11":{
						"caput":'de destaque de disposição ou emenda.'
					},
			}
		}]
	},
	"311":{
		"caput":'Conceder-se-á preferência, mediante deliberação do Plenário:',
		"enumeravel": [{
			"tipo":"inciso",
			"lista":{
					"1":{
						"caput":'de proposição sobre outra ou sobre as demais da Ordem do Dia;'
					},
					"2":{
						"caput":'de emenda ou grupo de emendas sobre as demais oferecidas à mesma proposição ou sobre outras referentes ao mesmo assunto;'
					},
					"3":{
						"caput":'de projeto sobre o substitutivo (art. 300, XIII);'
					},
					"4":{
						"caput":'de substitutivo sobre o projeto (art. 300, XIII).'
					},
			}
		}]
	},
	"312":{
		"caput":'O destaque de partes de qualquer proposição, bem como de emenda do grupo a que pertencer, pode ser concedido, mediante deliberação do Plenário, a requerimento de qualquer Senador, para:',
		"enumeravel": [{
			"tipo":"inciso",
			"lista":{
					"1":{
						"caput":'constituir projeto autônomo, salvo quando a disposição a destacar seja de projeto da Câmara;'
					},
					"2":{
						"caput":'votação em separado;'
					},
					"3":{
						"caput":'aprovação ou rejeição.'
					},
			}
		}]
	},
	"313":{
		"caput":'Permite-se destacar para votação, como emenda autônoma:',
		"enumeravel": [{
			"tipo":"inciso",
			"lista":{
					"1":{
						"caput":'parte de substitutivo, quando a votação se faça preferencialmente sobre o projeto;'
					},
					"2":{
						"caput":'parte de emenda;'
					},
					"3":{
						"caput":'subemenda;'
					},
					"4":{
						"caput":'parte de projeto, quando a votação se fizer preferencialmente sobre o substitutivo.'
					},
			}
		}]
	},
	"314":{
		"caput":'Em relação aos destaques, obedecer-se-ão as seguintes normas:',
		"enumeravel": [{
			"tipo":"inciso",
			"lista":{
					"1":{
						"caput":'o requerimento deve ser formulado:',
						"enumeravel": [{
							"tipo":"letra",
							"lista":{
									"1":{
										"caput":'ser anunciada a proposição, se o destaque atingir algumas de suas partes;'
									},
									"2":{
										"caput":'ser anunciado o grupo das emendas, quando o destaque se referir a qualquer delas;'
									},
									"3":{
										"caput":'ser anunciada a emenda, se o destaque tiver por fim separar algumas de suas partes;'
									},
							}
						}]
					},
					"2":{
						"caput":'não será permitido destaque de expressão cuja retirada inverta o sentido da proposição ou a modifique substancialmente;'
					},
					"3":{
						"caput":'concedido o destaque para votação em separado, submeter-se-á a votos, primeiramente, a matéria principal e, em seguida, a destacada;'
					},
					"4":{
						"caput":'a votação de requerimento de destaque só envolve decisão sobre a parte a destacar se a finalidade do destaque for expressamente mencionada;'
					},
					"5":{
						"caput":'havendo retirada do requerimento de destaque, a matéria destacada voltará ao grupo a que pertencer;'
					},
					"6":{
						"caput":'não se admitirá requerimento de destaque:',
						"enumeravel": [{
							"tipo":"letra",
							"lista":{
									"1":{
										"caput":'aprovação ou rejeição:'
									},
									"2":{
										"caput":'de dispositivo a que houver sido apresentada emenda;'
									},
									"3":{
										"caput":'de emendas que, regimentalmente, devam ser votadas separadamente;'
									},
									"4":{
										"caput":'emendas para constituição de grupos diferentes daqueles a que, regimentalmente, pertençam;'
									},
							}
						}]
					},
					"7":{
						"caput":'destacada uma emenda, sê-lo-ão, automaticamente, as que com ela tenham relação;'
					},
					"8":{
						"caput":'o destaque para projeto em separado de dispositivo ou emenda pode, também, ser proposto por comissão, em seu parecer;'
					},
					"9":{
						"caput":'a votação do requerimento de destaque para projeto em separado precederá a deliberação sobre a matéria principal;'
					},
					"10":{
						"caput":'o destaque para projeto em separado só pode ser submetido a votos se a matéria a destacar for suscetível de constituir proposição de curso autônomo;'
					},
					"11":{
						"caput":'concedido o destaque para projeto em separado, o autor do requerimento terá o prazo de dois dias úteis para oferecer o texto com que deverá tramitar o novo projeto;'
					},
					"12":{
						"caput":'o projeto resultante de destaque terá a tramitação de proposição inicial.'
					},
			}
		}]
	},
	"315":{
		"caput":'O adiamento da votação obedecerá aos mesmos princípios estabelecidos para o adiamento da discussão (art. 279).',
		"enumeravel": [{
			"tipo":"paragrafo",
			"lista":{
					"1":{
						"caput":'O requerimento deverá ser apresentado e votado como preliminar ao ser anunciada a matéria.'
					},
					"2":{
						"caput":'Não havendo número para a votação, o requerimento ficará sobrestado.'
					},
			}
		}]
	},
	"316":{
		"caput":'Proclamado o resultado da votação, é lícito ao Senador encaminhar à Mesa, para publicação, declaração de voto.',
		"enumeravel": [{
			"tipo":"paragrafo",
			"lista":{
					"1":{
						"caput":'Não haverá declaração de voto se a deliberação for secreta, não se completar por falta de número ou não for suscetível de encaminhamento.'
					},
			}
		}]
	},
	"317":{
		"caput":'Terminada a votação, com a aprovação de substitutivo, o projeto irá à comissão competente a fim de elaborar a redação para o turno suplementar.'
	},
	"318":{
		"caput":'É privativo da comissão específica para o estudo da matéria elaborar a redação para o turno suplementar e a redação final nos casos de:',
		"enumeravel": [{
			"tipo":"inciso",
			"lista":{
					"1":{
						"caput":'reforma do Regimento Interno;'
					},
					"2":{
						"caput":'proposta de emenda à Constituição;'
					},
					"3":{
						"caput":'projeto de código ou sua reforma.'
					},
			}
		}]
	},
	"319":{
		"caput":'Nos projetos da Câmara emendados pelo Senado, a redação final limitar-se-á às emendas destacadamente, não as incorporando ao texto da proposição.',
		"enumeravel": [{
			"tipo":"paragrafo",
			"lista":{
					"1":{
						"caput":'A redação final dos projetos de lei da Câmara destinados à sanção será dispensada, salvo se houver vício de linguagem, defeito ou erro manifesto a corrigir.'
					},
			}
		}]
	},
	"320":{
		"caput":'Lida no Período do Expediente, a redação final ficará sobre a mesa para oportuna inclusão em Ordem do Dia, após publicação, no Diário do Senado Federal e em avulso eletrônico, e interstício regimental.',
		"enumeravel": [{
			"tipo":"paragrafo",
			"lista":{
					"1":{
						"caput":'Quando, no decorrer da sessão em que for aprovada a matéria, chegar à mesa a redação final respectiva, poderá o Plenário, por proposta do Presidente, permitir se proceda à sua leitura após o final da Ordem do Dia.'
					},
			}
		}]
	},
	"321":{
		"caput":'A discussão e a votação da redação final poderão ser feitas imediatamente após a leitura, desde que assim o delibere o Senado.'
	},
	"322":{
		"caput":'Quando a redação final for de emendas do Senado a projeto da Câmara, não se admitirão emendas a dispositivo não emendado, salvo as de redação e as que decorram de emendas aprovadas.'
	},
	"323":{
		"caput":'As emendas de redação dependem de parecer da comissão que houver elaborado a redação final, sem prejuízo do disposto no art. 234, parágrafo único.'
	},
	"324":{
		"caput":'Figurando a redação final na Ordem do Dia, se sua discussão for encerrada sem emendas ou retificações, será considerada definitivamente aprovada, sem votação, a não ser que algum Senador requeira seja submetida a votos.'
	},
	"325":{
		"caput":'Verificada a existência de erro em texto aprovado e com redação definitiva, proceder-se-á da seguinte maneira:',
		"enumeravel": [{
			"tipo":"inciso",
			"lista":{
					"1":{
						"caput":'tratando-se de contradição, incoerência, prejudicialidade ou equívoco que importe em alteração do sentido do projeto, ainda não remetido à sanção ou à Câmara, o Presidente encaminhará a matéria à comissão competente para que proponha o modo de corrigir o erro, sendo a proposta examinada pela Comissão de Constituição, Justiça e Cidadania antes de submetida ao Plenário;'
					},
					"2":{
						"caput":'nas hipóteses do inciso I, quando a matéria tenha sido encaminhada à sanção ou à Câmara, o Presidente, após manifestação do Plenário, comunicará o fato ao Presidente da República ou à Câmara, remetendo novos autógrafos, se for o caso, ou solicitando a retificação do texto, mediante republicação da lei;'
					},
					"3":{
						"caput":'tratando-se de inexatidão material, devida a lapso manifesto ou erro gráfico, cuja correção não importe em alteração do sentido da matéria, o Presidente adotará as medidas especificadas no inciso II, mediante ofício à Presidência da República ou à Câmara, dando ciência do fato, posteriormente, ao Plenário.'
					},
			}
		}]
	},
	"326":{
		"caput":'Quando, em autógrafo recebido da Câmara, for verificada a existência de inexatidão material, lapso ou erro manifesto, não estando ainda a proposição aprovada pelo Senado, será sustada a sua apreciação para consulta à Casa de origem, cujos esclarecimentos serão dados a conhecer ao Senado, antes da votação, voltando a matéria às comissões para novo exame se do vício houver resultado alteração de sentido do texto.',
		"enumeravel": [{
			"tipo":"paragrafo",
			"lista":{
					"1":{
						"caput":'Quando a comunicação for feita pela Câmara, proceder-se-á da seguinte maneira:',
						"enumeravel": [{
							"tipo":"inciso",
							"lista":{
									"1":{
										"caput":'lida no Período do Expediente, será a comunicação encaminhada à comissão em que estiver a matéria;'
									},
									"2":{
										"caput":'se a matéria já houver sido examinada por outra comissão, a Presidência providenciará a fim de que a ela volte, para novo exame, antes do parecer do órgão em cujo poder se encontre;'
									},
									"3":{
										"caput":'ao ser a matéria submetida ao Plenário, o Presidente o advertirá do ocorrido;'
									},
									"4":{
										"caput":'se a matéria já houver sido votada pelo Senado, a Presidência providenciará para que seja objeto de nova discussão, promovendo, quando necessário, a substituição dos autógrafos remetidos à Presidência da República ou à Câmara.'
									},
							}
						}]
					},
			}
		}]
	},
	"327":{
		"caput":'Quando, após a aprovação definitiva de projeto de lei originário do Senado, for nele verificada a existência de matéria que deva ser objeto de projeto de decreto legislativo ou de resolução, a Presidência providenciará, ouvida a Comissão de Constituição, Justiça e Cidadania, o desdobramento da proposição.',
		"enumeravel": [{
			"tipo":"paragrafo",
			"lista":{
					"1":{
						"caput":'Seguir-se-á igual orientação quando se trate de projeto de decreto legislativo ou de resolução que contenha matéria de lei.'
					},
			}
		}]
	},
	"328":{
		"caput":'A proposição, aprovada em definitivo pelo Senado, será encaminhada, em autógrafos, à sanção, à promulgação ou à Câmara, conforme o caso.'
	},
	"329":{
		"caput":'Os autógrafos reproduzirão a redação final, aprovada pelo Plenário, ou o texto da Câmara, não emendado.'
	},
	"330":{
		"caput":'O autógrafo procedente da Câmara ficará arquivado no Senado.'
	},
	"331":{
		"caput":'Quando a proposição originária da Câmara for emendada, será remetida à Casa de origem, juntamente com os autógrafos referidos no art. 329, cópia autenticada do autógrafo procedente daquela Casa, salvo se houver segunda via, caso em que será devolvida.'
	},
	"332":{
		"caput":'Ao final da legislatura serão arquivadas todas as proposições em tramitação no Senado, exceto:',
		"enumeravel": [{
			"tipo":"inciso",
			"lista":{
					"1":{
						"caput":'as originárias da Câmara ou por ela revisadas;'
					},
					"2":{
						"caput":'as de autoria de Senadores que permaneçam no exercício de mandato ou que tenham sido reeleitos;'
					},
					"3":{
						"caput":'as apresentadas por Senadores no último ano de mandato;'
					},
					"4":{
						"caput":'as com parecer favorável das comissões;'
					},
					"5":{
						"caput":'as que tratem de matéria de competência exclusiva do Congresso Nacional (Const., art. 49);'
					},
					"6":{
						"caput":'as que tratem de matéria de competência privativa do Senado Federal (Const., art. 52);'
					},
					"7":{
						"caput":'pedido de sustação de processo contra Senador em andamento no Supremo Tribunal Federal (Const., art. 53, §§ 3º e 4º, EC nº 35/2001).'
					},
			}
		}]
	},
	"333":{
		"caput":'(Revogado).'
	},
	"334":{
		"caput":'O Presidente, de ofício ou mediante consulta de qualquer Senador, declarará prejudicada matéria dependente de deliberação do Senado:',
		"enumeravel": [{
			"tipo":"inciso",
			"lista":{
					"1":{
						"caput":'por haver perdido a oportunidade;'
					},
					"2":{
						"caput":'em virtude de seu prejulgamento pelo Plenário em outra deliberação.'
					},
			}
		}]
	},
	"335":{
		"caput":'O estudo de qualquer proposição poderá ser sobrestado, temporariamente, a requerimento de comissão ou de Senador, para aguardar:',
		"enumeravel": [{
			"tipo":"inciso",
			"lista":{
					"1":{
						"caput":'a decisão do Senado ou o estudo de comissão sobre outra proposição com ela conexa;'
					},
					"2":{
						"caput":'o resultado de diligência;'
					},
					"3":{
						"caput":'o recebimento de outra proposição sobre a mesma matéria.'
					},
			}
		}]
	},
	"336":{
		"caput":'A urgência poderá ser requerida:',
		"enumeravel": [{
			"tipo":"inciso",
			"lista":{
					"1":{
						"caput":'quando se trate de matéria que envolva perigo para a segurança nacional ou de providência para atender a calamidade pública;'
					},
					"2":{
						"caput":'quando se pretenda a apreciação da matéria na segunda sessão deliberativa ordinária subsequente à aprovação do requerimento;'
					},
					"3":{
						"caput":'quando se pretenda incluir em Ordem do Dia matéria pendente de parecer.'
					},
			}
		}]
	},
	"337":{
		"caput":'A urgência dispensa, durante toda a tramitação da matéria, interstícios, prazos e formalidades regimentais, salvo pareceres, quorum para deliberação e distribuição de cópias da proposição principal.'
	},
	"338":{
		"caput":'A urgência pode ser proposta:',
		"enumeravel": [{
			"tipo":"inciso",
			"lista":{
					"1":{
						"caput":'no caso do art. 336, I, pela Mesa, pela maioria dos membros do Senado ou líderes que representem esse número;'
					},
					"2":{
						"caput":'no caso do art. 336, II, por dois terços da composição do Senado ou líderes que representem esse número;'
					},
					"3":{
						"caput":'no caso do art. 336, III, por um quarto da composição do Senado ou líderes que representem esse número;'
					},
					"4":{
						"caput":'por comissão, nos casos do art. 336, II e III;'
					},
					"5":{
						"caput":'pela Comissão de Assuntos Econômicos, quando se tratar de pedido de autorização para realizar operações de crédito previstas nos arts. 28 e 33 da Resolução nº 43, de 2001.'
					},
			}
		}]
	},
	"339":{
		"caput":'O requerimento de urgência será lido:',
		"enumeravel": [{
			"tipo":"inciso",
			"lista":{
					"1":{
						"caput":'no caso do art. 336, I, imediatamente, em qualquer fase da sessão, ainda que com interrupção de discurso, discussão ou votação;'
					},
					"2":{
						"caput":'nos demais casos, no Período do Expediente.'
					},
			}
		}]
	},
	"340":{
		"caput":'O requerimento de urgência será submetido à deliberação do Plenário:',
		"enumeravel": [{
			"tipo":"inciso",
			"lista":{
					"1":{
						"caput":'imediatamente, no caso do art. 336, I;'
					},
					"2":{
						"caput":'após a Ordem do Dia, no caso do art. 336, II;'
					},
					"3":{
						"caput":'na sessão deliberativa seguinte, incluído em Ordem do Dia, no caso do art. 336, III.'
					},
			}
		}]
	},
	"341":{
		"caput":'Não serão submetidos à deliberação do Plenário requerimentos de urgência:',
		"enumeravel": [{
			"tipo":"inciso",
			"lista":{
					"1":{
						"caput":'nos casos do art. 336, II e III, antes da publicação do avulso eletrônico da proposição respectiva;'
					},
					"2":{
						"caput":'em número superior a dois, na mesma sessão, não computados os casos do art. 336, I.'
					},
			}
		}]
	},
	"342":{
		"caput":'No caso do art. 336, II, o requerimento de urgência será considerado prejudicado se não houver número para a votação.'
	},
	"343":{
		"caput":'No encaminhamento da votação de requerimento de urgência, poderão usar da palavra, pelo prazo de cinco minutos, um dos signatários e um representante de cada partido ou de bloco parlamentar e, quando se tratar de requerimento de autoria de comissão, o seu Presidente e o relator da matéria para a qual foi a urgência requerida.'
	},
	"344":{
		"caput":'A retirada de requerimento de urgência, obedecido, no que couber, o disposto no art. 256, é admissível mediante solicitação escrita:',
		"enumeravel": [{
			"tipo":"inciso",
			"lista":{
					"1":{
						"caput":'do primeiro signatário, quando não se trate de requerimento de líderes;'
					},
					"2":{
						"caput":'do Presidente da comissão, quando de autoria desta;'
					},
					"3":{
						"caput":'das lideranças que o houverem subscrito.'
					},
			}
		}]
	},
	"345":{
		"caput":'A matéria para a qual o Senado conceda urgência será submetida ao Plenário:',
		"enumeravel": [{
			"tipo":"inciso",
			"lista":{
					"1":{
						"caput":'imediatamente após a concessão da urgência, no caso do art. 336, I;'
					},
					"2":{
						"caput":'na segunda sessão deliberativa ordinária que se seguir à concessão da urgência, incluída a matéria na Ordem do Dia, no caso do art. 336, II;'
					},
					"3":{
						"caput":'na quarta sessão deliberativa ordinária que se seguir à concessão da urgência, na hipótese do art. 336, III.'
					},
			}
		}]
	},
	"346":{
		"caput":'Os pareceres sobre as proposições em regime de urgência devem ser apresentados:',
		"enumeravel": [{
			"tipo":"inciso",
			"lista":{
					"1":{
						"caput":'imediatamente, nas hipóteses do art. 336, I, podendo o relator solicitar prazo não excedente a duas horas;'
					},
					"2":{
						"caput":'quando a matéria for anunciada na Ordem do Dia, no caso do art. 336, II;'
					},
					"3":{
						"caput":'no prazo compreendido entre a concessão da urgência e o dia anterior ao da sessão em cuja Ordem do Dia deva a matéria figurar, quando se tratar de caso previsto no art. 336, III.'
					},
			}
		}]
	},
	"347":{
		"caput":'Na discussão e no encaminhamento de votação das proposições em regime de urgência no caso do art. 336, I, só poderão usar da palavra, e por metade do prazo previsto para as matérias em tramitação normal, o autor da proposição e os relatores, além de um orador de cada partido.'
	},
	"348":{
		"caput":'Encerrada a discussão de matéria em regime de urgência, com a apresentação de emendas, proceder-se-á da seguinte forma:',
		"enumeravel": [{
			"tipo":"inciso",
			"lista":{
					"1":{
						"caput":'no caso do art. 336, I, os pareceres serão proferidos imediatamente, por relator designado pelo Presidente, que poderá pedir o prazo previsto no art. 346, I;'
					},
					"2":{
						"caput":'no caso do art. 336, II, os pareceres poderão ser proferidos imediatamente, ou, se a complexidade da matéria o indicar, no prazo de vinte e quatro horas, saindo, nesta hipótese, a matéria da Ordem do Dia, para nela figurar na sessão deliberativa ordinária subsequente;'
					},
					"3":{
						"caput":'no caso do art. 336, III, o projeto sairá da Ordem do Dia, para nela ser novamente incluído na quarta sessão deliberativa ordinária subsequente, devendo ser proferidos os pareceres sobre as emendas até o dia anterior ao da sessão em que a matéria será apreciada.'
					},
			}
		}]
	},
	"349":{
		"caput":'A realização de diligência, nos projetos em regime de urgência, só é permitida no caso do art. 336, III, e pelo prazo máximo de quatro sessões.',
		"enumeravel": [{
			"tipo":"paragrafo",
			"lista":{
					"1":{
						"caput":'O requerimento pode ser apresentado até ser anunciada a votação.'
					},
			}
		}]
	},
	"350":{
		"caput":'O turno suplementar de matéria em regime de urgência será realizado imediatamente após a aprovação, em turno único, do substitutivo, podendo ser concedido o prazo de vinte e quatro horas para a elaboração da redação para o turno suplementar.'
	},
	"351":{
		"caput":'A redação final de matéria em regime de urgência não depende de publicação e será submetida à deliberação do Senado:',
		"enumeravel": [{
			"tipo":"inciso",
			"lista":{
					"1":{
						"caput":'no caso do art. 336, I, imediatamente após a apresentação, ainda que com interrupção de discussão ou votação;'
					},
					"2":{
						"caput":'nos demais casos, a juízo da Presidência, em qualquer fase da sessão.'
					},
			}
		}]
	},
	"352":{
		"caput":'Extingue-se a urgência:',
		"enumeravel": [{
			"tipo":"inciso",
			"lista":{
					"1":{
						"caput":'pelo término da sessão legislativa;'
					},
					"2":{
						"caput":'nos casos do art. 336, II e III, até ser iniciada a votação da matéria, mediante deliberação do Plenário.'
					},
			}
		}]
	},
	"353":{
		"caput":'São consideradas em regime de urgência, independentemente de requerimento:',
		"enumeravel": [{
			"tipo":"inciso",
			"lista":{
					"1":{
						"caput":'com a tramitação prevista para o caso do art. 336, I, matéria que tenha por fim:',
						"enumeravel": [{
							"tipo":"letra",
							"lista":{
									"1":{
										"caput":'o Presidente da República a declarar guerra, a celebrar a paz, a permitir que forças estrangeiras transitem pelo território nacional ou nele permaneçam temporariamente (Const., art. 49, II);'
									},
									"2":{
										"caput":'o estado de defesa e a intervenção federal; autorizar o estado de sítio ou suspender qualquer dessas medidas (Const., art. 49, IV);'
									},
							}
						}]
					},
					"2":{
						"caput":'com a tramitação prevista para o caso do art. 336, II, a matéria que objetive autorização para o Presidente e o Vice-Presidente da República se ausentarem do País (Const., art. 49, III).'
					},
			}
		}]
	},
	"354":{
		"caput":'A proposta de emenda à Constituição apresentada ao Senado será discutida e votada em dois turnos, considerando-se aprovada se obtiver, em ambos, três quintos dos votos dos membros da Casa (Const., art. 60, § 2º);',
		"enumeravel": [{
			"tipo":"paragrafo",
			"lista":{
					"1":{
						"caput":'Não será objeto de deliberação a proposta de emenda tendente a abolir (Const., art. 60, § 4º):',
						"enumeravel": [{
							"tipo":"inciso",
							"lista":{
									"1":{
										"caput":'a forma federativa de Estado;'
									},
									"2":{
										"caput":'o voto direto, secreto, universal e periódico;'
									},
									"3":{
										"caput":'a separação dos Poderes;'
									},
									"4":{
										"caput":'os direitos e garantias individuais.'
									},
							}
						}]
					},
					"2":{
						"caput":'A Constituição não poderá ser emendada na vigência de intervenção federal, de estado de defesa ou de estado de sítio (Const., art. 60, § 1º).'
					},
			}
		}]
	},
	"355":{
		"caput":'A proposta será lida no Período do Expediente e publicada no Diário do Senado Federal e em avulso eletrônico, para distribuição aos Senadores.'
	},
	"356":{
		"caput":'A proposta será despachada à Comissão de Constituição, Justiça e Cidadania, que terá prazo de até trinta dias, contado da data do despacho da Presidência, para emitir parecer.',
		"enumeravel": [{
			"tipo":"paragrafo",
			"lista":{
					"1":{
						"caput":'O parecer da Comissão de Constituição, Justiça e Cidadania que concluir pela apresentação de emenda deverá conter assinaturas de Senadores que, complementando as dos membros da Comissão, compreendam, no mínimo, um terço dos membros do Senado.'
					},
			}
		}]
	},
	"357":{
		"caput":'Cinco dias após a publicação do parecer no Diário do Senado Federal e em avulso eletrônico, a matéria poderá ser incluída em Ordem do Dia.'
	},
	"358":{
		"caput":'Decorrido o prazo de que trata o art. 356 sem que a Comissão de Constituição, Justiça e Cidadania haja proferido parecer, a proposta de emenda à Constituição será incluída em Ordem do Dia, para discussão, em primeiro turno, durante cinco sessões deliberativas ordinárias consecutivas.',
		"enumeravel": [{
			"tipo":"paragrafo",
			"lista":{
					"1":{
						"caput":'O parecer será proferido oralmente, em plenário, por relator designado pelo Presidente.'
					},
					"2":{
						"caput":'Durante a discussão poderão ser oferecidas emendas assinadas por, no mínimo, um terço dos membros do Senado, desde que guardem relação direta e imediata com a matéria tratada na proposta.'
					},
			}
		}]
	},
	"359":{
		"caput":'Para exame e parecer das emendas, é assegurado à Comissão de Constituição, Justiça e Cidadania o mesmo prazo estabelecido no art. 356.'
	},
	"360":{
		"caput":'Lido o parecer no Período do Expediente, publicado no Diário do Senado Federal e em avulso eletrônico com a proposta e as emendas, a matéria poderá ser incluída em Ordem do Dia.'
	},
	"361":{
		"caput":'Esgotado o prazo da Comissão de Constituição, Justiça e Cidadania, proceder-se-á na forma do disposto no caput do art. 358 e em seu § 1º.',
		"enumeravel": [{
			"tipo":"paragrafo",
			"lista":{
					"1":{
						"caput":'Na sessão deliberativa ordinária que se seguir à emissão do parecer, a proposta será incluída em Ordem do Dia para votação em primeiro turno.'
					},
					"2":{
						"caput":'Somente serão admitidos requerimentos que objetivem a votação em separado de partes da proposta ou de emendas.'
					},
					"3":{
						"caput":'A deliberação sobre a proposta, as emendas e as disposições destacadas para votação em separado será feita pelo processo nominal.'
					},
			}
		}]
	},
	"362":{
		"caput":'O interstício entre o primeiro e o segundo turno será de, no mínimo, cinco dias úteis.'
	},
	"363":{
		"caput":'Incluída a proposta em Ordem do Dia, para o segundo turno, será aberto o prazo de três sessões deliberativas ordinárias para discussão, quando poderão ser oferecidas emendas que não envolvam o mérito.'
	},
	"364":{
		"caput":'Encerrada a discussão, em segundo turno, com apresentação de emendas, a matéria voltará à Comissão, para parecer em cinco dias improrrogáveis, após o que será incluída em Ordem do Dia, em fase de votação.'
	},
	"365":{
		"caput":'Aprovada, sem emendas, a proposta será remetida à Câmara dos Deputados; emendada, será encaminhada à Comissão de Constituição, Justiça e Cidadania, que terá o prazo de três dias para oferecer a redação final.'
	},
	"366":{
		"caput":'A redação final, apresentada à Mesa, será votada, com qualquer número, independentemente de publicação.'
	},
	"367":{
		"caput":'Considera-se proposta nova o substitutivo da Câmara a proposta de iniciativa do Senado.'
	},
	"368":{
		"caput":'Na revisão do Senado à proposta da Câmara aplicar-se-ão as normas estabelecidas neste Título.'
	},
	"369":{
		"caput":'Quando a aprovação da proposta for ultimada no Senado, será o fato comunicado à Câmara dos Deputados e convocada sessão para promulgação da emenda (Const., art. 60, § 3º).'
	},
	"370":{
		"caput":'(Revogado).'
	},
	"371":{
		"caput":'É vedada a apresentação de proposta que objetive alterar dispositivos sem correlação direta entre si.'
	},
	"372":{
		"caput":'Aplicam-se à tramitação da proposta, no que couber, as normas estabelecidas neste Regimento para as demais proposições.'
	},
	"373":{
		"caput":'A matéria constante de proposta de emenda à Constituição rejeitada ou havida por prejudicada não pode ser objeto de nova proposta na mesma sessão legislativa (Const., art. 60, § 5º).'
	},
	"374":{
		"caput":'Na sessão em que for lido o projeto de código, a Presidência designará uma comissão temporária para seu estudo, composta de onze membros, e fixará o calendário de sua tramitação, obedecidos os seguintes prazos e normas:',
		"enumeravel": [{
			"tipo":"inciso",
			"lista":{
					"1":{
						"caput":'a comissão se reunirá até o dia útil seguinte à sua constituição, para eleger o Presidente e o Vice-Presidente, sendo, em seguida, designados um relator geral e tantos relatores parciais quantos necessários;'
					},
					"2":{
						"caput":'ao projeto serão anexadas as proposições em curso ou as sobrestadas, que envolvam matéria com ele relacionada;'
					},
					"3":{
						"caput":'perante a comissão, poderão ser oferecidas emendas, no prazo de vinte dias úteis, a contar da publicação do projeto no Diário do Senado Federal;'
					},
					"4":{
						"caput":'encerrado o prazo para a apresentação de emendas, os relatores parciais encaminharão, dentro de dez dias úteis, ao relator geral, as conclusões de seus trabalhos;'
					},
					"5":{
						"caput":'o relator geral terá o prazo de cinco dias úteis para apresentar, à comissão, o parecer que será publicado em avulso eletrônico, juntamente com o estudo dos relatores parciais e as emendas;'
					},
					"6":{
						"caput":'a comissão terá cinco dias úteis para concluir o seu estudo e encaminhar à Mesa o parecer final sobre o projeto e as emendas;'
					},
					"7":{
						"caput":'na comissão, a discussão da matéria obedecerá à divisão adotada para a designação dos relatores parciais, podendo cada membro usar da palavra uma vez, por dez minutos, o relator parcial, duas vezes, por igual prazo, e o relator geral, duas vezes, pelo prazo de quinze minutos;'
					},
					"8":{
						"caput":'as emendas e subemendas serão votadas, sem encaminhamento, em grupos, segundo o sentido dos pareceres, ressalvados os destaques requeridos pelo autor, com apoiamento de, pelo menos, cinco membros da comissão ou por líder;'
					},
					"9":{
						"caput":'publicado o parecer da comissão e publicado o avulso eletrônico, será o projeto incluído, com exclusividade, em Ordem do Dia, obedecido o interstício regimental;'
					},
					"10":{
						"caput":'a discussão, em plenário, far-se-á sobre o projeto e as emendas, em um único turno, podendo o relator geral usar da palavra sempre que for necessário, ou delegá-la ao relator parcial;'
					},
					"11":{
						"caput":'a discussão poderá ser encerrada mediante autorização do Plenário, a requerimento de líder, depois de debatida a matéria em três sessões deliberativas consecutivas;'
					},
					"12":{
						"caput":'encerrada a discussão, passar-se-á à votação, sendo que os destaques só poderão ser requeridos por líder, pelo relator geral ou por vinte Senadores;'
					},
					"13":{
						"caput":'aprovado com ou sem emendas, o projeto voltará à comissão para a redação final, que deverá ser apresentada no prazo de cinco dias úteis;'
					},
					"14":{
						"caput":'publicada em avulso eletrônico, a redação final será incluída em Ordem do Dia, obedecido o interstício regimental;'
					},
					"15":{
						"caput":'não se fará tramitação simultânea de projetos de código;'
					},
					"16":{
						"caput":'os prazos previstos neste artigo poderão ser aumentados até o quádruplo, por deliberação do Plenário, a requerimento da comissão.'
					},
			}
		}]
	},
	"375":{
		"caput":'Nos projetos de lei de iniciativa do Presidente da República, quando sujeitos à tramitação urgente (Const., art. 64, § 1º) e nos casos de apreciação de atos de outorga ou renovação de concessão, permissão ou autorização para serviço de radiodifusão sonora e de sons e imagens (Const., art. 223, § 1º), proceder-se-á da seguinte forma:',
		"enumeravel": [{
			"tipo":"inciso",
			"lista":{
					"1":{
						"caput":'o projeto será lido no Período do Expediente e distribuído às comissões competentes, somente podendo receber emendas na primeira comissão constante do despacho, pelo prazo de cinco dias;'
					},
					"2":{
						"caput":'o projeto será apreciado, simultaneamente, pelas comissões, sendo feitas tantas autuações quantas forem necessárias;'
					},
					"3":{
						"caput":'as comissões deverão apresentar os pareceres até o vigésimo quinto dia contado do recebimento do projeto no Senado;'
					},
					"4":{
						"caput":'publicado o parecer em avulso eletrônico, decorrido o interstício regimental, o projeto será incluído em Ordem do Dia;'
					},
					"5":{
						"caput":'não sendo emitidos os pareceres no prazo fixado no inciso III, aplicar-se á o disposto no art. 172, II, d;'
					},
					"6":{
						"caput":'o adiamento de discussão ou de votação não poderá ser aceito por prazo superior a vinte e quatro horas;'
					},
					"7":{
						"caput":'a redação final das emendas deverá ser apresentada em plenário no prazo máximo de quarenta e oito horas após a votação da matéria;'
					},
					"8":{
						"caput":'esgotado o prazo de quarenta e cinco dias contado do recebimento do projeto sem que se tenha concluída a votação, deverá ele ser incluído em Ordem do Dia, sobrestando-se a deliberação sobre as demais matérias, até que se ultime a sua votação (Const., art. 64, § 2º).'
					},
			}
		}]
	},
	"376":{
		"caput":'O projeto de decreto legislativo referente a atos internacionais terá a seguinte tramitação:',
		"enumeravel": [{
			"tipo":"inciso",
			"lista":{
					"1":{
						"caput":'só terá iniciado o seu curso se estiver acompanhado de cópia autenticada do texto, em português, do ato internacional respectivo, bem como da mensagem de encaminhamento e da exposição de motivos;'
					},
					"2":{
						"caput":'lido no Período do Expediente, será o projeto publicado e distribuído em avulso eletrônico, acompanhado dos textos referidos no inciso I e despachado à Comissão de Relações Exteriores e Defesa Nacional;'
					},
					"3":{
						"caput":'perante a Comissão, nos cinco dias úteis subsequentes à publicação de avulso eletrônico, poderão ser oferecidas emendas; a Comissão terá, para opinar sobre o projeto, e emendas, o prazo de quinze dias úteis, prorrogável por igual período;'
					},
					"4":{
						"caput":'publicados o parecer e as emendas e o avulso eletrônico, decorrido o interstício regimental, a matéria será incluída em Ordem do Dia;'
					},
					"5":{
						"caput":'não sendo emitido o parecer, conforme estabelece o inciso III, aplicar-se-á o disposto no art. 172, II, c.'
					},
			}
		}]
	},
	"377":{
		"caput":'Compete privativamente ao Senado Federal (Const., art. 52, I e II):',
		"enumeravel": [{
			"tipo":"inciso",
			"lista":{
					"1":{
						"caput":'processar e julgar o Presidente e o Vice-Presidente da República, nos crimes de responsabilidade, bem como os Ministros de Estado e os Comandantes da Marinha, do Exército e da Aeronáutica nos crimes da mesma natureza conexos com aqueles;'
					},
					"2":{
						"caput":'processar e julgar os Ministros do Supremo Tribunal Federal, os membros do Conselho Nacional de Justiça e do Conselho Nacional do Ministério Público, o Procurador-Geral da República e o Advogado-Geral da União, nos crimes de responsabilidade.'
					},
			}
		}]
	},
	"378":{
		"caput":'Em qualquer hipótese, a sentença condenatória só poderá ser proferida pelo voto de dois terços dos membros do Senado, e a condenação limitar-se-á à perda do cargo, com inabilitação, por oito anos, para o exercício de função pública, sem prejuízo das sanções judiciais cabíveis (Const., art. 52, parágrafo único).'
	},
	"379":{
		"caput":'Em todos os trâmites do processo e julgamento serão observadas as normas prescritas na lei reguladora da espécie.'
	},
	"380":{
		"caput":'Para julgamento dos crimes de responsabilidade das autoridades indicadas no art. 377, obedecer-se-ão as seguintes normas:',
		"enumeravel": [{
			"tipo":"inciso",
			"lista":{
					"1":{
						"caput":'recebida pela Mesa do Senado a autorização da Câmara para instauração do processo, nos casos previstos no art. 377, I, ou a denúncia do crime, nos demais casos, será o documento lido no Período do Expediente da sessão seguinte;'
					},
					"2":{
						"caput":'na mesma sessão em que se fizer a leitura, será eleita comissão, constituída por um quarto da composição do Senado, obedecida a proporcionalidade das representações partidárias ou dos blocos parlamentares, e que ficará responsável pelo processo;'
					},
					"3":{
						"caput":'a comissão encerrará seu trabalho com o fornecimento do libelo acusatório, que será anexado ao processo e entregue ao Presidente do Senado Federal, para remessa, em original, ao Presidente do Supremo Tribunal Federal, com a comunicação do dia designado para o julgamento;'
					},
					"4":{
						"caput":'o Primeiro Secretário enviará ao acusado cópia autenticada de todas as peças do processo, inclusive do libelo, intimando-o do dia e hora em que deverá comparecer ao Senado para o julgamento;'
					},
					"5":{
						"caput":'estando o acusado ausente do Distrito Federal, a sua intimação será solicitada pelo Presidente do Senado ao Presidente do Tribunal de Justiça do Estado em que ele se encontre;'
					},
					"6":{
						"caput":'servirá de escrivão um funcionário da Secretaria do Senado designado pelo Presidente do Senado.'
					},
			}
		}]
	},
	"381":{
		"caput":'Instaurado o processo, o Presidente da República ficará suspenso de suas funções (Const., art. 86, § 1º, II).',
		"enumeravel": [{
			"tipo":"paragrafo",
			"lista":{
					"1":{
						"caput":'Se, decorrido o prazo de cento e oitenta dias, o julgamento não estiver concluído, cessará o afastamento do Presidente da República, sem prejuízo do regular prosseguimento do processo (Const., art. 86, § 2º).'
					},
			}
		}]
	},
	"382":{
		"caput":'No processo e julgamento a que se referem os arts. 377 a 381 aplicar-se á, no que couber, o disposto na Lei nº 1.079, de 10 de abril de 1950.'
	},
	"383":{
		"caput":'Na apreciação do Senado Federal sobre a escolha de autoridades, observar-se-ão as seguintes normas:',
		"enumeravel": [{
			"tipo":"inciso",
			"lista":{
					"1":{
						"caput":'a mensagem, que será lida em plenário e encaminhada à comissão competente, deverá estar acompanhada de amplos esclarecimentos sobre o candidato e de:',
						"enumeravel": [{
							"tipo":"letra",
							"lista":{
									"1":{
										"caput":'vitae, no qual constem:',
										"enumeravel": [{
											"tipo":"numero",
											"lista":{
													"1":{
														"caput":'atividades profissionais exercidas pelo indicado, com a discriminação dos referidos períodos;'
													},
													"2":{
														"caput":'relação das publicações de sua autoria, com as referências bibliográficas que permitam sua recuperação;'
													},
													"3":{
														"caput":'caso dos indicados na forma do inciso III do art. 52 da Constituição Federal, declaração do indicado:'
													},
													"4":{
														"caput":'à existência de parentes seus que exercem ou exerceram atividades, públicas ou privadas, vinculadas a sua atividade profissional, com a discriminação dos referidos períodos;'
													},
													"5":{
														"caput":'à sua participação, em qualquer tempo, como sócio, proprietário ou gerente, de empresas ou entidades não governamentais, com a discriminação dos referidos períodos;'
													},
													"6":{
														"caput":'regularidade fiscal, nos âmbitos federal, estadual e municipal;'
													},
													"7":{
														"caput":'à existência de ações judiciais nas quais figure como autor ou réu, com indicação atualizada da tramitação processual;'
													},
													"8":{
														"caput":'à sua atuação, nos últimos 5 (cinco) anos, contados retroativamente ao ano em que se deu sua indicação, em juízos e tribunais, em conselhos de administração de empresas estatais ou em cargos de direção de agências reguladoras;'
													},
													"9":{
														"caput":'escrita, apresentada de forma sucinta, em que o indicado demonstre ter experiência profissional, formação técnica adequada e afinidade intelectual e moral para o exercício da atividade;'
													},
													"10":{
														"caput":'caso dos indicados na forma do inciso IV do art. 52 da Constituição Federal, relatórios produzidos pelo Ministério das Relações Exteriores contendo:'
													},
													"11":{
														"caput":'sobre o Estado ou organização internacional para o qual o candidato foi indicado;'
													},
													"12":{
														"caput":'dos tratados e acordos assinados com o respectivo Estado ou organização internacional, bem como dos contratos de empréstimos e financiamentos oficiais concedidos pelo Brasil, incluindo os atos referentes a perdão ou renegociação de dívidas e a renúncia fiscal, diferenciando entre atos em vigor e atos ainda sujeitos à aprovação do Congresso Nacional, nos termos do art. 49, inciso I, da Constituição Federal;',
														"enumeravel": [{
															"tipo":"inciso",
															"lista":{
																	"1":{
																		"caput":'o exame das indicações feitas na forma do inciso III do art. 52 da Constituição Federal seguirá as seguintes etapas:',
																		"enumeravel": [{
																			"tipo":"letra",
																			"lista":{
																					"1":{
																						"caput":'relator apresentará o relatório à comissão, com recomendações, se for o caso, para que sejam apresentadas informações adicionais;'
																					},
																					"2":{
																						"caput":'concedida, automaticamente, vista coletiva aos membros da comissão e divulgado o relatório por meio do portal do Senado Federal;'
																					},
																					"3":{
																						"caput":'portal do Senado Federal possibilitará à sociedade encaminhar informações sobre o indicado ou perguntas a ele dirigidas, que serão submetidas ao exame do relator com vistas ao seu aproveitamento, inclusive quanto à necessidade de realização de audiência pública em face das informações e indagações recebidas;'
																					},
																					"4":{
																						"caput":'relator poderá discutir com os membros da comissão o conteúdo das questões que serão formuladas ao indicado;'
																					},
																					"5":{
																						"caput":'comissão convocará o candidato para, em prazo estipulado, não inferior a 5 (cinco) dias úteis, ouvi-lo, em arguição pública, sobre assuntos pertinentes ao desempenho do cargo a ser ocupado (Const., art. 52, III);'
																					},
																					"6":{
																						"caput":'inquirição de candidato, cada Senador interpelante disporá de 10 (dez) minutos, assegurado igual prazo para resposta, imediata, do interpelado, facultadas réplica e tréplica, ambas também imediatas, por 5 (cinco) minutos;'
																					},
																					"7":{
																						"caput":'relatório será votado;'
																					},
																			}
																		}]
																	},
																	"2":{
																		"caput":'a arguição de candidato a chefe de missão diplomática de caráter permanente será feita em reunião secreta (Const., art. 52, IV), aplicando-se o procedimento descrito no inciso II deste artigo, no que couber;'
																	},
																	"3":{
																		"caput":'além da arguição do candidato e do disposto no art. 93, a comissão poderá realizar investigações e requisitar, da autoridade competente, informações complementares;'
																	},
																	"4":{
																		"caput":'o relatório deverá conter dados sobre o candidato, passando a constituir parecer com o resultado da votação, aprovando ou rejeitando o nome indicado;'
																	},
																	"5":{
																		"caput":'a reunião será pública, sendo a votação procedida por escrutínio secreto, vedadas declaração ou justificação de voto, exceto com referência ao aspecto legal;'
																	},
																	"6":{
																		"caput":'o parecer será apreciado pelo Plenário em sessão pública, sendo a votação procedida por escrutínio secreto;'
																	},
																	"7":{
																		"caput":'a manifestação do Senado será comunicada ao Presidente da República, consignando-se o resultado da votação.'
																	},
															}
														}]
													},
													"13":{
														"caput":'A manifestação do Senado Federal e das comissões sobre a escolha de chefe de missão diplomática de caráter permanente será procedida em sessão e reunião secretas (Const., art. 52, IV).'
													},
													"14":{
														"caput":'A resposta negativa às hipóteses previstas nos itens 1, 2, 4 e 5 da alínea b do inciso I deste artigo deverá ser declarada por escrito.'
													},
													"15":{
														"caput":'A declaração de que trata o item 3 da alínea b do inciso I deste artigo deverá ser acompanhada de documentação comprobatória emitida pelos órgãos competentes.'
													},
											}
										}]
									},
							}
						}]
					},
			}
		}]
	},
	"384":{
		"caput":'A eleição dos membros do Conselho da República será feita mediante lista sêxtupla elaborada pela Mesa, ouvidas as lideranças com atuação no Senado.',
		"enumeravel": [{
			"tipo":"paragrafo",
			"lista":{
					"1":{
						"caput":'Proceder-se-á à eleição por meio de cédulas uninominais, considerando-se eleito o indicado que obtiver a maioria de votos, presente a maioria absoluta dos membros do Senado.'
					},
					"2":{
						"caput":'Eleito o primeiro representante do Senado, proceder-se-á à eleição do segundo, dentre os cinco indicados restantes, obedecido o mesmo critério previsto no § 1º.'
					},
					"3":{
						"caput":'Se, na primeira apuração, nenhum dos indicados alcançar maioria de votos, proceder-se-á a nova votação, e, se mesmo nesta, aquele quorum não for alcançado, a eleição ficará adiada para outra sessão, a ser convocada pela Presidência e, assim, sucessivamente.'
					},
					"4":{
						"caput":'No processamento da eleição, aplicar-se-ão, no que couber, as normas regimentais que dispuserem sobre escolha de autoridades.'
					},
					"5":{
						"caput":'À eleição dos suplentes, previstos na Lei nº 8.041, de 5 de junho de 1990, aplica-se o disposto neste artigo.'
					},
			}
		}]
	},
	"385":{
		"caput":'A mensagem do Presidente da República solicitando autorização para destituir o Procurador-Geral da República, uma vez lida em plenário, será distribuída, para apresentação de parecer, à Comissão de Constituição, Justiça e Cidadania.',
		"enumeravel": [{
			"tipo":"paragrafo",
			"lista":{
					"1":{
						"caput":'Aplicar-se-á na tramitação da mensagem, no que couber, o disposto para escolha de autoridades, sendo que a destituição somente se efetivará se aprovada pela maioria absoluta de votos.'
					},
			}
		}]
	},
	"386":{
		"caput":'O Senado conhecerá da declaração, proferida em decisão definitiva pelo Supremo Tribunal Federal, de inconstitucionalidade total ou parcial de lei mediante:',
		"enumeravel": [{
			"tipo":"inciso",
			"lista":{
					"1":{
						"caput":'comunicação do Presidente do Tribunal;'
					},
					"2":{
						"caput":'representação do Procurador-Geral da República;'
					},
					"3":{
						"caput":'projeto de resolução de iniciativa da Comissão de Constituição, Justiça e Cidadania.'
					},
			}
		}]
	},
	"387":{
		"caput":'A comunicação, a representação e o projeto a que se refere o art. 386 deverão ser instruídos com o texto da lei cuja execução se deva suspender, do acórdão do Supremo Tribunal Federal, do parecer do Procurador-Geral da República e da versão do registro taquigráfico do julgamento.'
	},
	"388":{
		"caput":'Lida em plenário, a comunicação ou representação será encaminhada à Comissão de Constituição, Justiça e Cidadania, que formulará projeto de resolução suspendendo a execução da lei, no todo ou em parte.'
	},
	"389":{
		"caput":'O Senado apreciará pedido de autorização para operações externas, de natureza financeira, de interesse da União, dos Estados, do Distrito Federal, dos Territórios e dos Municípios (Const., art. 52, V), instruído com:',
		"enumeravel": [{
			"tipo":"inciso",
			"lista":{
					"1":{
						"caput":'documentos que o habilitem a conhecer, perfeitamente, a operação, os recursos para satisfazer os compromissos e a sua finalidade;'
					},
					"2":{
						"caput":'publicação oficial com o texto da autorização do Legislativo competente;'
					},
					"3":{
						"caput":'parecer do órgão competente do Poder Executivo.'
					},
			}
		}]
	},
	"390":{
		"caput":'Na tramitação da matéria de que trata o art. 389, obedecer-se-ão as seguintes normas:',
		"enumeravel": [{
			"tipo":"inciso",
			"lista":{
					"1":{
						"caput":'lida no Período do Expediente, a matéria será encaminhada à Comissão de Assuntos Econômicos, a fim de ser formulado o respectivo projeto de resolução, concedendo ou negando a medida pleiteada;'
					},
					"2":{
						"caput":'a resolução, uma vez promulgada, será enviada, em todo o seu teor, à entidade interessada e ao órgão a que se refere o art. 389, III, devendo constar do instrumento da operação.'
					},
			}
		}]
	},
	"391":{
		"caput":'Qualquer modificação nos compromissos originariamente assumidos dependerá de nova autorização do Senado.'
	},
	"392":{
		"caput":'O disposto nos arts. 389 a 391 aplicar-se-á, também, aos casos de aval da União, Estado, Distrito Federal ou Município, para a contratação de empréstimo externo por entidade autárquica subordinada ao Governo Federal, Estadual ou Municipal.'
	},
	"393":{
		"caput":'Compete ao Senado:',
		"enumeravel": [{
			"tipo":"inciso",
			"lista":{
					"1":{
						"caput":'fixar limites globais para o montante da dívida consolidada da União, dos Estados, do Distrito Federal e dos Municípios (Const., art. 52, VI);'
					},
					"2":{
						"caput":'dispor sobre limites globais e condições para as operações de crédito externo e interno da União, dos Estados, do Distrito Federal e dos Municípios, de suas autarquias e demais entidades controladas pelo poder público federal (Const., art. 52, VII);'
					},
					"3":{
						"caput":'dispor sobre limites e condições para a concessão de garantia da União em operações de crédito externo e interno (Const., art. 52, VIII);'
					},
					"4":{
						"caput":'estabelecer limites globais e condições para o montante da dívida mobiliária dos Estados, do Distrito Federal e dos Municípios (Const., art. 52, IX).'
					},
			}
		}]
	},
	"393-A":{
		"caput":'A avaliação de que trata o art. 99-A será realizada anualmente por grupo de Senadores da Comissão de Assuntos Econômicos designados pelo Presidente da Comissão.'
	},
	"393-B":{
		"caput":'Para atender aos objetivos da avaliação prevista no art. 52, XV, da Constituição Federal, o Senado poderá solicitar informações e documentos à União, aos Estados, ao Distrito Federal e aos Municípios, compreendidos os três Poderes e os órgãos e entidades da administração direta e indireta, além do Conselho Nacional de Política Fazendária (Confaz) e de outras instituições da sociedade organizada.'
	},
	"393-C":{
		"caput":'Serão observados os seguintes prazos nos trabalhos de avaliação periódica do Sistema Tributário Nacional:',
		"enumeravel": [{
			"tipo":"inciso",
			"lista":{
					"1":{
						"caput":'para recebimento de documentos e informações, até 15 de março;'
					},
					"2":{
						"caput":'para realização de audiências públicas, até 30 de abril;'
					},
					"3":{
						"caput":'para apresentação do relatório final, até 30 de junho.'
					},
			}
		}]
	},
	"393-D":{
		"caput":'A funcionalidade do Sistema Tributário Nacional será avaliada considerando-se, entre outros, os seguintes aspectos:',
		"enumeravel": [{
			"tipo":"inciso",
			"lista":{
					"1":{
						"caput":'complexidade e qualidade da legislação;'
					},
					"2":{
						"caput":'custos de conformidade à normatização tributária;'
					},
					"3":{
						"caput":'qualidade dos tributos, especialmente quanto:',
						"enumeravel": [{
							"tipo":"letra",
							"lista":{
									"1":{
										"caput":'justiça fiscal;'
									},
									"2":{
										"caput":'atendimento aos princípios constitucionais tributários;'
									},
									"3":{
										"caput":'atendimento às necessidades orçamentárias;'
									},
									"4":{
										"caput":'custo das obrigações acessórias;'
									},
							}
						}]
					},
					"4":{
						"caput":'carga tributária;'
					},
					"5":{
						"caput":'equilíbrio federativo, especialmente quanto:',
						"enumeravel": [{
							"tipo":"letra",
							"lista":{
									"1":{
										"caput":'participação da União, dos Estados, do Distrito Federal e dos Municípios no total da receita tributária, antes e depois das transferências constitucionais e legais;'
									},
									"2":{
										"caput":'participação das transferências constitucionais e legais na receita tributária dos entes federados;'
									},
							}
						}]
					},
					"6":{
						"caput":'renúncias fiscais;'
					},
					"7":{
						"caput":'harmonização normativa;'
					},
					"8":{
						"caput":'redução das desigualdades regionais;'
					},
					"9":{
						"caput":'compatibilidade com a legislação de outros países ou blocos econômicos.'
					},
			}
		}]
	},
	"393-E":{
		"caput":'O desempenho das administrações tributárias da União, dos Estados, do Distrito Federal e dos Municípios será avaliado considerando-se, entre outros, os seguintes aspectos:',
		"enumeravel": [{
			"tipo":"inciso",
			"lista":{
					"1":{
						"caput":'relação entre o custo da administração e o montante arrecadado;'
					},
					"2":{
						"caput":'exercício efetivo das competências tributárias pelos entes federados;'
					},
					"3":{
						"caput":'desempenho da fiscalização;'
					},
					"4":{
						"caput":'relação entre pagamento espontâneo e coercitivo dos tributos;'
					},
					"5":{
						"caput":'desempenho da cobrança judicial e extrajudicial da dívida ativa tributária;'
					},
					"6":{
						"caput":'efetividade dos programas de recuperação fiscal, especialmente quanto a parcelamento, anistia e remissão;'
					},
					"7":{
						"caput":'grau de integração das administrações tributárias;'
					},
					"8":{
						"caput":'gastos e resultados com educação fiscal;'
					},
					"9":{
						"caput":'qualidade do atendimento ao contribuinte;'
					},
					"10":{
						"caput":'grau de informalidade da economia.'
					},
			}
		}]
	},
	"393-F":{
		"caput":'O grupo de Senadores de que trata o art. 393-A elaborará relatório conclusivo, que será submetido à deliberação do Plenário da Comissão de Assuntos Econômicos, em caráter terminativo.',
		"enumeravel": [{
			"tipo":"paragrafo",
			"lista":{
					"1":{
						"caput":'Cópia integral do relatório aprovado será enviada ao Presidente da República, à Câmara dos Deputados, aos Governadores dos Estados e do Distrito Federal, às Assembleias Legislativas Estaduais, à Câmara Legislativa do Distrito Federal e aos Tribunais de Contas.'
					},
					"2":{
						"caput":'Resumo executivo com as principais conclusões será enviado aos Municípios.'
					},
			}
		}]
	},
	"394":{
		"caput":'Ao Senado Federal, no que se refere à competência tributária dos Estados e do Distrito Federal, compete:',
		"enumeravel": [{
			"tipo":"inciso",
			"lista":{
					"1":{
						"caput":'fixar alíquotas máximas do imposto sobre transmissão causa mortis e doação de quaisquer bens ou direitos (Const., art. 155, § 1º, IV);'
					},
					"2":{
						"caput":'estabelecer as alíquotas aplicáveis às operações e prestações interestaduais e de exportação (Const., art. 155, § 2º, IV);'
					},
					"3":{
						"caput":'estabelecer alíquotas mínimas nas operações internas (Const., art. 155, § 2º, V, a);'
					},
					"4":{
						"caput":'fixar alíquotas máximas nas operações internas para resolver conflito específico que envolva interesse de Estados e do Distrito Federal (Const., art. 155, § 2º, V, b).'
					},
			}
		}]
	},
	"395":{
		"caput":'As matérias a que se referem os arts. 393 e 394 terão a tramitação regimental prevista para os demais projetos de resolução.'
	},
	"396":{
		"caput":'O Senado Federal remeterá o texto da resolução a que se referem os arts. 389 a 394 ao Presidente da República, aos Governadores, às Assembleias Legislativas, à Câmara Legislativa do Distrito Federal e aos Prefeitos e Câmaras de Vereadores dos Municípios interessados, com a indicação da sua publicação no Diário do Senado Federal e no Diário Oficial da União.'
	},
	"397":{
		"caput":'O Ministro de Estado comparecerá perante o Senado:',
		"enumeravel": [{
			"tipo":"inciso",
			"lista":{
					"1":{
						"caput":'quando convocado, por deliberação do Plenário, mediante requerimento de qualquer Senador ou comissão, para prestar, pessoalmente, informações sobre assunto previamente determinado (Const., art. 50, caput);'
					},
					"2":{
						"caput":'quando o solicitar, mediante entendimento com a Mesa, para expor assunto de relevância de seu Ministério (Const., art. 50, § 1º).'
					},
			}
		}]
	},
	"398":{
		"caput":'Quando houver comparecimento de Ministro de Estado perante o Senado, adotar-se-ão as seguintes normas:',
		"enumeravel": [{
			"tipo":"inciso",
			"lista":{
					"1":{
						"caput":'nos casos do inciso I do art. 397, a Presidência oficiará ao Ministro de Estado, dando-lhe conhecimento da convocação e da lista das informações desejadas, a fim de que declare quando comparecerá ao Senado, no prazo que lhe estipular, não superior a trinta dias;'
					},
					"2":{
						"caput":'nos casos do inciso II do art. 397, a Presidência comunicará ao Plenário o dia e a hora que marcar para o comparecimento;'
					},
					"3":{
						"caput":'no plenário, o Ministro de Estado ocupará o lugar que a Presidência lhe indicar;'
					},
					"4":{
						"caput":'será assegurado o uso da palavra ao Ministro de Estado na oportunidade combinada, sem embargo das inscrições existentes;'
					},
					"5":{
						"caput":'a sessão em que comparecer o Ministro de Estado será destinada exclusivamente ao cumprimento dessa finalidade;'
					},
					"6":{
						"caput":'se, entretanto, o Ministro desejar falar ao Senado no mesmo dia em que o solicitar, ser-lhe-á assegurada a oportunidade após as deliberações da Ordem do Dia;'
					},
					"7":{
						"caput":'se o tempo normal da sessão não permitir que se conclua a exposição do Ministro de Estado, com a correspondente fase de interpelações, será ela prorrogada ou se designará outra sessão para esse fim;'
					},
					"8":{
						"caput":'o Ministro de Estado ficará subordinado às normas estabelecidas para o uso da palavra pelos Senadores;'
					},
					"9":{
						"caput":'o Ministro de Estado só poderá ser aparteado na fase das interpelações desde que o permita;'
					},
					"10":{
						"caput":'terminada a exposição do Ministro de Estado, que terá a duração de meia hora, abrir-se-á a fase de interpelação, pelos Senadores inscritos, dentro do assunto tratado, dispondo o interpelante de cinco minutos, assegurado igual prazo para a resposta do interpelado, após o que poderá este ser contraditado pelo prazo máximo de dois minutos, concedendo-se ao Ministro de Estado o mesmo tempo para a tréplica;'
					},
					"11":{
						"caput":'a palavra aos Senadores será concedida na ordem de inscrição, intercalando-se oradores de cada partido;'
					},
					"12":{
						"caput":'ao Ministro de Estado é lícito fazer-se acompanhar de assessores, aos quais a Presidência designará lugares próximos ao que ele deva ocupar, não lhes sendo permitido interferir nos debates.'
					},
			}
		}]
	},
	"399":{
		"caput":'Na hipótese de não ser atendida convocação feita de acordo com o disposto no art. 397, I, o Presidente do Senado promoverá a instauração do procedimento legal cabível ao caso.'
	},
	"400":{
		"caput":'O disposto nos arts. 397 a 399 aplica-se, quando possível, aos casos de comparecimento de Ministro a reunião de comissão.'
	},
	"400-A":{
		"caput":'Aplica-se o disposto neste Título, no que couber, ao comparecimento ao Senado de titulares de órgãos diretamente subordinados à Presidência da República (Const., art. 50).'
	},
	"401":{
		"caput":'O Regimento Interno poderá ser modificado ou reformado por projeto de resolução de iniciativa de qualquer Senador, da Comissão Diretora ou de comissão temporária para esse fim criada, em virtude de deliberação do Senado, e da qual deverá fazer parte um membro da Comissão Diretora.',
		"enumeravel": [{
			"tipo":"paragrafo",
			"lista":{
					"1":{
						"caput":'Em qualquer caso, o projeto, após publicado em avulso eletrônico, ficará sobre a mesa durante cinco dias úteis a fim de receber emendas.'
					},
					"2":{
						"caput":'Decorrido o prazo previsto no § 1º, o projeto será enviado:',
						"enumeravel": [{
							"tipo":"inciso",
							"lista":{
									"1":{
										"caput":'à Comissão de Constituição, Justiça e Cidadania, em qualquer caso;'
									},
									"2":{
										"caput":'à comissão que o houver elaborado, para exame das emendas, se as houver recebido;'
									},
									"3":{
										"caput":'à Comissão Diretora, se de autoria individual de Senador.'
									},
							}
						}]
					},
					"3":{
						"caput":'Os pareceres das comissões serão emitidos no prazo de dez dias úteis, quando o projeto for de simples modificação, e no de vinte dias úteis, quando se tratar de reforma.'
					},
					"4":{
						"caput":'Aplicam-se à tramitação do projeto de alteração ou reforma do Regimento as normas estabelecidas para os demais projetos de resolução.'
					},
					"5":{
						"caput":'A redação final do projeto de reforma do Regimento Interno compete à comissão que o houver elaborado e o de autoria individual de Senador, à Comissão Diretora.'
					},
			}
		}]
	},
	"402":{
		"caput":'A Mesa fará, ao fim de cada legislatura, consolidação das modificações feitas no Regimento.',
		"enumeravel": [{
			"tipo":"paragrafo",
			"lista":{
					"1":{
						"caput":'Na consolidação, a Mesa poderá, sem modificação de mérito, alterar a ordenação das matérias e fazer as correções de redação que se tornarem necessárias.'
					},
			}
		}]
	},
	"403":{
		"caput":'Constituirá questão de ordem, suscitável em qualquer fase da sessão, pelo prazo de cinco minutos, qualquer dúvida sobre interpretação ou aplicação deste Regimento.',
		"enumeravel": [{
			"tipo":"paragrafo",
			"lista":{
					"1":{
						"caput":'Para contraditar questão de ordem é permitido o uso da palavra a um só Senador, por prazo não excedente ao fixado neste artigo.'
					},
			}
		}]
	},
	"404":{
		"caput":'A questão de ordem deve ser objetiva, indicar o dispositivo regimental em que se baseia, referir-se a caso concreto relacionado com a matéria tratada na ocasião, não podendo versar sobre tese de natureza doutrinária ou especulativa.'
	},
	"405":{
		"caput":'A questão de ordem será decidida pelo Presidente, com recurso para o Plenário, de ofício ou mediante requerimento, que só será aceito se formulado ou apoiado por líder.'
	},
	"406":{
		"caput":'Considera-se simples precedente a decisão sobre questão de ordem, só adquirindo força obrigatória quando incorporada ao Regimento.'
	},
	"407":{
		"caput":'Nenhum Senador poderá falar, na mesma sessão, sobre questão de ordem já resolvida pela Presidência.'
	},
	"408":{
		"caput":'Havendo recurso para o Plenário, sobre decisão da Presidência em questão de ordem, é lícito a esta solicitar a audiência da Comissão de Constituição, Justiça e Cidadania sobre a matéria, quando se tratar de interpretação de texto constitucional.',
		"enumeravel": [{
			"tipo":"paragrafo",
			"lista":{
					"1":{
						"caput":'Solicitada a audiência, fica sobrestada a decisão.'
					},
					"2":{
						"caput":'O parecer da Comissão deverá ser proferido no prazo de dois dias úteis, após o que, com ou sem parecer, será o recurso incluído em Ordem do Dia para deliberação do Plenário.'
					},
					"3":{
						"caput":'Quando se tratar de questão de ordem sobre matéria em regime de urgência nos termos do art. 336, I, ou com prazo de tramitação, o parecer deverá ser proferido imediatamente, podendo o Presidente da comissão ou o relator solicitar prazo não excedente a duas horas.'
					},
			}
		}]
	},
	"409":{
		"caput":'As petições, memoriais, representações ou outros documentos enviados ao Senado serão recebidos pelo Serviço de Protocolo e, segundo a sua natureza, despachados às comissões competentes ou arquivados, depois de lidos em plenário, quando o merecerem, a juízo da Presidência.'
	},
	"410":{
		"caput":'Não serão recebidas petições e representações sem data e assinaturas ou em termos desrespeitosos, podendo as assinaturas, a juízo da Presidência, ser reconhecidas.'
	},
	"411":{
		"caput":'O Senado não encaminhará à Câmara ou a outro órgão do poder público documento compreendido no art. 409.'
	},
	"412":{
		"caput":'A legitimidade na elaboração de norma legal é assegurada pela observância rigorosa das disposições regimentais, mediante os seguintes princípios básicos:',
		"enumeravel": [{
			"tipo":"inciso",
			"lista":{
					"1":{
						"caput":'a participação plena e igualitária dos Senadores em todas as atividades legislativas, respeitados os limites regimentais;'
					},
					"2":{
						"caput":'modificação da norma regimental apenas por norma legislativa competente, cumpridos rigorosamente os procedimentos regimentais;'
					},
					"3":{
						"caput":'impossibilidade de prevalência sobre norma regimental de acordo de lideranças ou decisão de Plenário, exceto quando tomada por unanimidade mediante voto nominal, resguardado o quorum mínimo de três quintos dos votos dos membros da Casa;'
					},
					"4":{
						"caput":'nulidade de qualquer decisão que contrarie norma regimental;'
					},
					"5":{
						"caput":'prevalência de norma especial sobre a geral;'
					},
					"6":{
						"caput":'decisão dos casos omissos de acordo com a analogia e os princípios gerais de Direito;'
					},
					"7":{
						"caput":'preservação dos direitos das minorias;'
					},
					"8":{
						"caput":'definição normativa, a ser observada pela Mesa em questão de ordem decidida pela Presidência;'
					},
					"9":{
						"caput":'decisão colegiada, ressalvadas as competências específicas estabelecidas neste Regimento;'
					},
					"10":{
						"caput":'impossibilidade de tomada de decisões sem a observância do quorum regimental estabelecido;'
					},
					"11":{
						"caput":'pauta de decisões feita com antecedência tal que possibilite a todos os Senadores seu devido conhecimento;'
					},
					"12":{
						"caput":'publicidade das decisões tomadas, exceção feita aos casos específicos previstos neste Regimento;'
					},
					"13":{
						"caput":'possibilidade de ampla negociação política somente por meio de procedimentos regimentais previstos.'
					},
			}
		}]
	},
	"413":{
		"caput":'A transgressão a qualquer desses princípios poderá ser denunciada, mediante questão de ordem, nos termos do disposto no art. 404.',
		"enumeravel": [{
			"tipo":"paragrafo",
			"lista":{
					"1":{
						"caput":'Levantada a questão de ordem referida neste artigo, a Presidência determinará a apuração imediata da denúncia, verificando os fatos pertinentes, mediante consulta aos registros da Casa, notas taquigráficas, fitas magnéticas ou outros meios cabíveis'
					},
			}
		}]
	},
}
function getArtigos(){
  /*
  if(arguments.length === 2) console.log('arguments:', arguments[0], arguments[1])
  else console.log('arguments:', arguments[0])
  */
  return arguments.length === 2 ?
      Object.keys(artigos)
      .slice(arguments[0]-1, arguments[1])
      .reduce( (obj, numArtigo) => {return {...obj, [numArtigo] : artigos[numArtigo]} }, {})
      : {[arguments[0]]: artigos[arguments[0]]}
}

export { artigos, getArtigos }
